import { AbstractMesh, EngineStore, MirrorTexture, Plane, Scene, Vector3 } from '@babylonjs/core';

import { RenderManager } from '@/manager';
import { clearArchetype, World } from '@/World';

import type { Reflector } from '@/components';

export class ReflectorSystem {
  private static instance: ReflectorSystem | undefined;
  public static getReflectorEntities() {
    return this.instance?.getReflectors() ?? [];
  }
  public static getReflectorsInFloorplan(floorplanId: string) {
    return this.instance?.getReflectorsInFloorplan(floorplanId) ?? [];
  }
  public static changeReflectorSettings(reflector: Reflector, level: number, blurKernel: number) {
    this.instance?.changeReflectorSettings(reflector, level, blurKernel);
  }
  public static getReflectionTexture(floorplanId: string) {
    return this.instance?.getReflectionTexture(floorplanId) ?? null;
  }

  private targetArchetype = World.with('Identifier', 'Reflector');
  private floorplanArchetype = World.with('Identifier', 'Floorplan');
  private reflectionTargetArchetype = World.with('Identifier', 'TransformNode', 'ReflectionTarget');

  private reflectionTextureList = new Map<string, MirrorTexture>();
  constructor() {
    ReflectorSystem.instance = this;
    this.reflectionTargetArchetype.onEntityAdded.add((e) => this.initReflectionTarget(e));
    this.floorplanArchetype.onEntityAdded.add((e) => this.initFloorplan(e));
  }
  public dispose() {
    ReflectorSystem.instance = undefined;
    this.reflectionTextureList.forEach((r) => r.dispose());
    this.reflectionTextureList.clear();
    clearArchetype(this.targetArchetype);
    clearArchetype(this.floorplanArchetype);
    clearArchetype(this.reflectionTargetArchetype);
  }

  beforeRenderExecute(scene: Scene) {
    for (const entity of this.targetArchetype.entities) {
      const reflector = entity.Reflector;
      if (reflector && reflector.reflectionTexture.isReady()) {
        const reflectionTexture = reflector.reflectionTexture;
        if (reflectionTexture.level !== reflector.level) {
          reflectionTexture.level = reflector.level;
        }
        if (reflectionTexture.adaptiveBlurKernel !== reflector.blurKernel) {
          reflectionTexture.adaptiveBlurKernel = reflector.blurKernel;
          RenderManager.RenderUpdate();
        }
      }
    }
  }

  private initReflectionTarget(entity: (typeof this.reflectionTargetArchetype.entities)[number]) {
    World.removeComponent(entity, 'ReflectionTarget');
    const floorplanId = entity.Identifier.floorplanId;
    const meshes = entity.TransformNode.getChildMeshes();
    const reflectionTexture = this.getReflectionTexture(floorplanId);
    if (reflectionTexture) {
      reflectionTexture.renderList?.push(...meshes);
    }
  }
  private initFloorplan(entity: (typeof this.floorplanArchetype.entities)[number]) {
    const floorplanId = entity.Identifier.floorplanId;
    const reflectionTexture = this.getReflectionTexture(floorplanId);
    if (reflectionTexture) {
      reflectionTexture.mirrorPlane = Plane.FromPositionAndNormal(
        entity.Floorplan.pivotPosition,
        Vector3.Up().scale(-1)
      );
      World.addComponent(entity, 'Reflector', {
        reflectionTexture: reflectionTexture,
        level: reflectionTexture.level,
        blurKernel: reflectionTexture.blurKernel,
      });
    }
  }
  private getReflectionTexture(floorplanId: string) {
    if (!this.reflectionTextureList.has(floorplanId)) {
      const reflectionTexture = new MirrorTexture('mirror', 1024, undefined, true);
      reflectionTexture.level = 0.1;
      reflectionTexture.adaptiveBlurKernel = 40;
      reflectionTexture.renderList = [];
      this.reflectionTextureList.set(floorplanId, reflectionTexture);
    }

    return this.reflectionTextureList.get(floorplanId)!;
  }

  public getReflectors() {
    return this.targetArchetype.entities ?? [];
  }
  public getReflectorsInFloorplan(floorplanId: string) {
    return (
      this.targetArchetype.entities.filter((e) => e.Identifier.floorplanId === floorplanId) ?? []
    );
  }

  public changeReflectorSettings(reflector: Reflector, level: number, blurKernel: number) {
    reflector.level = level;
    if (blurKernel > 0) reflector.blurKernel = blurKernel;
    RenderManager.RenderUpdate();
  }
}
