import {
  ArcRotateCamera,
  BaseCameraPointersInput,
  CameraInputTypes,
  type IPointerEvent,
  type Nullable,
  type PointerTouch,
  Vector3,
} from '@babylonjs/core';

import { RenderManager } from '@/manager';


/**
 * Manage the pointers inputs to control an arc rotate camera.
 * @see https://doc.babylonjs.com/features/featuresDeepDive/cameras/customizingCameraInputs
 */

export class ArcRotateCameraPointersInput extends BaseCameraPointersInput {
  // Issue #58 - Pan with one finger, Rotate with two fingers
  public swapPanAndRotate = true;

  /**
   * Defines the camera the input is attached to.
   */
  public camera: ArcRotateCamera = new ArcRotateCamera('', 0, 0, 0, new Vector3());

  /**
   * The minimum radius used for pinch, to avoid radius lock at 0
   */
  public static MinimumRadiusForPinch: number = 0.001;

  /**
   * Gets the class name of the current input.
   * @returns the class name
   */
  public getClassName(): string {
    return 'ArcRotateCameraPointersInput';
  }

  /**
   * Defines the buttons associated with the input to handle camera move.
   */
  // @serialize()
  public buttons = [0, 1, 2];

  /**
   * Defines the pointer angular sensibility  along the X axis or how fast is
   * the camera rotating.
   */
  // @serialize()
  public angularSensibilityX = 1000.0;

  /**
   * Defines the pointer angular sensibility along the Y axis or how fast is
   * the camera rotating.
   */
  // @serialize()
  public angularSensibilityY = 1000.0;

  /**
   * Defines the pointer pinch precision or how fast is the camera zooming.
   */
  // @serialize()
  public pinchPrecision = 12.0;

  /**
   * pinchDeltaPercentage will be used instead of pinchPrecision if different
   * from 0.
   * It defines the percentage of current camera.radius to use as delta when
   * pinch zoom is used.
   */
  // @serialize()
  public pinchDeltaPercentage = 0;

  /**
   * When useNaturalPinchZoom is true, multi touch zoom will zoom in such
   * that any object in the plane at the camera's target point will scale
   * perfectly with finger motion.
   * Overrides pinchDeltaPercentage and pinchPrecision.
   */
  // @serialize()
  public useNaturalPinchZoom: boolean = false;

  /**
   * Defines whether zoom (2 fingers pinch) is enabled through multitouch
   */
  // @serialize()
  public pinchZoom: boolean = true;

  /**
   * Defines the pointer panning sensibility or how fast is the camera moving.
   */
  // @serialize()
  public panningSensibility: number = 1000.0;

  /**
   * Defines whether panning (2 fingers swipe) is enabled through multitouch.
   */
  // @serialize()
  public multiTouchPanning: boolean = true;

  /**
   * Defines whether panning is enabled for both pan (2 fingers swipe) and
   * zoom (pinch) through multitouch.
   */
  // @serialize()
  public multiTouchPanAndZoom: boolean = true;

  /**
   * Revers pinch action direction.
   */
  public pinchInwards = true;

  private _isPanClick: boolean = false;
  private _twoFingerActivityCount: number = 0;
  private _isPinching: boolean = false;

  constructor() {
    super();
  }

  /**
   * Move camera from multi touch panning positions.
   * @param previousMultiTouchPanPosition
   * @param multiTouchPanPosition
   */
  private _computeMultiTouchPanning(
    previousMultiTouchPanPosition: Nullable<PointerTouch>,
    multiTouchPanPosition: Nullable<PointerTouch>
  ): void {
    if (this.panningSensibility !== 0 && previousMultiTouchPanPosition && multiTouchPanPosition) {
      const moveDeltaX = multiTouchPanPosition.x - previousMultiTouchPanPosition.x;
      const moveDeltaY = multiTouchPanPosition.y - previousMultiTouchPanPosition.y;
      if (this.swapPanAndRotate) {
        this.camera.inertialAlphaOffset -= moveDeltaX / this.angularSensibilityX;
        this.camera.inertialBetaOffset -= moveDeltaY / this.angularSensibilityX;
      } else {
        const [deltaX, deltaY] = this.checkIsPanPossible(moveDeltaX, moveDeltaY);
        this.camera.inertialPanningX += -deltaX / this.panningSensibility;
        this.camera.inertialPanningY += deltaY / this.panningSensibility;
      }
    }
  }

  /**
   * Move camera from pinch zoom distances.
   * @param previousPinchSquaredDistance
   * @param pinchSquaredDistance
   */
  private _computePinchZoom(
    previousPinchSquaredDistance: number,
    pinchSquaredDistance: number
  ): void {
    const radius = this.camera.radius || ArcRotateCameraPointersInput.MinimumRadiusForPinch;
    if (this.useNaturalPinchZoom) {
      this.camera.radius =
        (radius * Math.sqrt(previousPinchSquaredDistance)) / Math.sqrt(pinchSquaredDistance);
    } else if (this.pinchDeltaPercentage) {
      this.camera.inertialRadiusOffset +=
        (pinchSquaredDistance - previousPinchSquaredDistance) *
        0.001 *
        radius *
        this.pinchDeltaPercentage;
    } else {
      this.camera.inertialRadiusOffset +=
        (pinchSquaredDistance - previousPinchSquaredDistance) /
        ((this.pinchPrecision *
          (this.pinchInwards ? 1 : -1) *
          (this.angularSensibilityX + this.angularSensibilityY)) /
          2);
    }
  }

  /**
   * Called on pointer POINTERMOVE event if only a single touch is active.
   * @param point
   * @param offsetX
   * @param offsetY
   */
  public onTouch(point: Nullable<PointerTouch>, offsetX: number, offsetY: number): void {
    if (
      this.panningSensibility !== 0 &&
      ((this._ctrlKey && this.camera._useCtrlForPanning) || this._isPanClick)
    ) {
       {
        const [deltaX, deltaY] = this.checkIsPanPossible(offsetX, offsetY);
        this.camera.inertialPanningX += -deltaX / this.panningSensibility;
        this.camera.inertialPanningY += deltaY / this.panningSensibility;
        //  
        RenderManager.RenderUpdate();
      }
    } else {
      if (this.swapPanAndRotate) {
        const [deltaX, deltaY] = this.checkIsPanPossible(offsetX, offsetY);
        this.camera.inertialPanningX += -deltaX / this.panningSensibility;
        this.camera.inertialPanningY += deltaY / this.panningSensibility;
        //  
        RenderManager.RenderUpdate();
      } else {
        this.camera.inertialAlphaOffset -= offsetX / this.angularSensibilityX;
        this.camera.inertialBetaOffset -= offsetY / this.angularSensibilityY;
        //  
        RenderManager.RenderUpdate();
      }
    }
  }

  public checkIsPanPossible(offsetX: number, offsetY: number): number[] {
    return [offsetX, offsetY];
  }

  /**
   * Called on pointer POINTERDOUBLETAP event.
   */
  public onDoubleTap() {
    if (this.camera.useInputToRestoreState) {
      this.camera.restoreState();
      //   
      RenderManager.RenderUpdate();
    }
  }

  private startA: { x: number; y: number } = { x: 0, y: 0 };
  private startB: { x: number; y: number } = { x: 0, y: 0 };

  /**
   * Called on pointer POINTERMOVE event if multiple touches are active.
   * @param pointA
   * @param pointB
   * @param previousPinchSquaredDistance
   * @param pinchSquaredDistance
   * @param previousMultiTouchPanPosition
   * @param multiTouchPanPosition
   */
  public onMultiTouch(
    pointA: Nullable<PointerTouch>,
    pointB: Nullable<PointerTouch>,
    previousPinchSquaredDistance: number,
    pinchSquaredDistance: number,
    previousMultiTouchPanPosition: Nullable<PointerTouch>,
    multiTouchPanPosition: Nullable<PointerTouch>
  ): void {
    if (previousPinchSquaredDistance === 0 && previousMultiTouchPanPosition === null) {
      // First time this method is called for new pinch.
      // Next time this is called there will be a
      // previousPinchSquaredDistance and pinchSquaredDistance to compare.
      return;
    }
    if (pinchSquaredDistance === 0 && multiTouchPanPosition === null) {
      // Last time this method is called at the end of a pinch.
      return;
    }

    // Zoom and panning enabled together
    if (this.multiTouchPanAndZoom) {
      this._computePinchZoom(previousPinchSquaredDistance, pinchSquaredDistance);
      this._computeMultiTouchPanning(previousMultiTouchPanPosition, multiTouchPanPosition);
      RenderManager.RenderUpdate();

      // Zoom and panning enabled but only one at a time
    } else if (this.multiTouchPanning && this.pinchZoom) {
      this._twoFingerActivityCount++;

      if (pointA && pointB) {
        if (this._twoFingerActivityCount <= 1) {
          this.startA.x = pointA.x;
          this.startA.y = pointA.y;
          this.startB.x = pointB.x;
          this.startB.y = pointB.y;
          this._isPinching = true;
        } else if (2 <= this._twoFingerActivityCount && this._twoFingerActivityCount <= 10) {
          const vecA = [pointA.x - this.startA.x, pointA.y - this.startA.y];
          const vecB = [pointB.x - this.startB.x, pointB.y - this.startB.y];
          const innerProduct = vecA[0] * vecB[0] + vecA[1] * vecB[1];
          this._isPinching = innerProduct <= 0;
        }
      }

      if (this._isPinching) {
        // Since pinch has not been active long, assume we intend to zoom.
        this._computePinchZoom(previousPinchSquaredDistance, pinchSquaredDistance);
        RenderManager.RenderUpdate();

        // Since we are pinching, remain pinching on next iteration.
        // this._isPinching = true;
      } else {
        // Pause between pinch starting and moving implies not a zoom event. Pan instead.
        this._computeMultiTouchPanning(previousMultiTouchPanPosition, multiTouchPanPosition);
        RenderManager.RenderUpdate();
      }

      // Panning enabled, zoom disabled
    } else if (this.multiTouchPanning) {
      this._computeMultiTouchPanning(previousMultiTouchPanPosition, multiTouchPanPosition);
      RenderManager.RenderUpdate();

      // Zoom enabled, panning disabled
    } else if (this.pinchZoom) {
      this._computePinchZoom(previousPinchSquaredDistance, pinchSquaredDistance);
      RenderManager.RenderUpdate();
    }
  }

  /**
   * Called each time a new POINTERDOWN event occurs. Ie, for each button
   * press.
   * @param evt
   */
  public onButtonDown(evt: IPointerEvent): void {
    this._isPanClick = evt.button === this.camera._panningMouseButton;
  }

  /**
   * Called each time a new POINTERUP event occurs. Ie, for each button
   * release.
   */
  public onButtonUp(): void {
    this._twoFingerActivityCount = 0;
    this._isPinching = false;
  }

  /**
   * Called when window becomes inactive.
   */
  public onLostFocus(): void {
    this._isPanClick = false;
    this._twoFingerActivityCount = 0;
    this._isPinching = false;
  }
}
(<any>CameraInputTypes)['ArcRotateCameraPointersInput'] = ArcRotateCameraPointersInput;
