import {
  Color3,
  EasingFunction,
  type Material,
  type Nullable,
  RegisterMaterialPlugin,
  UnregisterMaterialPlugin,
} from '@babylonjs/core';

import { RoomLightConfiguration, RoomLightMaterial } from '@/material/RoomLightMaterial';
import { playAnimation } from '@/util/AnimationHelper';
import {
  createEasingFunction,
  EasingFunctionType,
  EasingModeType,
} from '@/util/EasingFunctionType';

export class MaterialPluginManager {
  private static singletonInstance: MaterialPluginManager | undefined;
  public static get instance(): MaterialPluginManager {
    if (this.singletonInstance === undefined) {
      this.singletonInstance = new MaterialPluginManager();
    }
    return this.singletonInstance;
  }

  constructor() {
    MaterialPluginManager.singletonInstance = this;

    //-------------------------------------------------
    // * RoomLight Material 
    RegisterMaterialPlugin('RoomLightReceiver', (material) => {
      // @ts-ignore
      material.roomLight = new RoomLightMaterial(material);
      // @ts-ignore
      return material.roomLight;
    });
  }

  public reset() {
    UnregisterMaterialPlugin('RoomLightReceiver');

    this.roomLightConfiguration = {};
  }

  //-------------------------------------------------
  // * RoomLightMaterial
  public roomLightConfiguration: { [key: string]: RoomLightConfiguration } = {};
  private roomLightAnimationDuration: number = 0.6;
  private roomLightAnimationEasingFunction: EasingFunction = createEasingFunction(
    EasingFunctionType.QuarticEase,
    EasingModeType.Out
  );
  public registRoomLightMaterial(roomId: string, material: Nullable<Material>) {
    if (material && material.pluginManager) {
      const plugin = material.pluginManager.getPlugin('RoomLight');
      if (plugin) {
        if (!this.roomLightConfiguration[roomId]) {
          this.roomLightConfiguration[roomId] = new RoomLightConfiguration();
        }

        // @ts-ignore
        plugin.config = this.roomLightConfiguration[roomId];
        // @ts-ignore
        plugin.isEnabled = true;
      }
    }
  }
  public setRoomLightAnimationOption(
    duration: number,
    type?: EasingFunctionType,
    mode?: EasingModeType
  ) {
    this.roomLightAnimationDuration = duration;
    if (!type || !mode) return;

    this.roomLightAnimationEasingFunction = createEasingFunction(type, mode);
  }
  public setRoomLightColor(roomId: string, isOn: boolean, intensity?: number) {
    if (!this.roomLightConfiguration[roomId]) return;
    intensity = intensity ?? 1;
    const value = 1 - 0.85 * (1 - intensity);
    const color = isOn ? new Color3(value, value, value) : new Color3(0.15, 0.15, 0.15);

    playAnimation(
      {
        target: this.roomLightConfiguration[roomId],
        property: {
          targetProperty: 'roomColor',
          keyFrame: {
            duration: this.roomLightAnimationDuration,
            from: this.roomLightConfiguration[roomId].roomColor,
            to: color,
          },
        },
        easingFunction: this.roomLightAnimationEasingFunction,
      },
      undefined,
      true
    );
  }

  //-------------------------------------------------
}
