import { clearArchetype, World } from '@/World';

export class ActiveHomeSystem {
  private static activeHomeId = '';
  private static instance: ActiveHomeSystem | undefined;
  public static setActiveHome(homeId: string) {
    this.activeHomeId = homeId;
    this.instance?.allUpdateActiveHome();
  }

  private targetArchetype = World.with('Identifier');

  constructor() {
    ActiveHomeSystem.instance = this;
    this.targetArchetype.onEntityAdded.add((e) => this.updateActiveHome(e));
  }

  dispose() {
    ActiveHomeSystem.instance = undefined;
    clearArchetype(this.targetArchetype);
  }

  private updateActiveHome = (entity: (typeof this.targetArchetype.entities)[number]) => {
    if (entity.Identifier.homeId === ActiveHomeSystem.activeHomeId) {
      if (!entity.ActiveHome) World.addComponent(entity, 'ActiveHome', true);
    } else {
      if (entity.ActiveHome) World.removeComponent(entity, 'ActiveHome');
    }
  };
  private allUpdateActiveHome = () => {
    this.targetArchetype.entities.forEach(this.updateActiveHome);
  };
}
