import { PBRMaterial, StandardMaterial } from '@babylonjs/core';

import { StructureType } from '@/components';
import { RenderManager } from '@/manager';
import { clearArchetype, World } from '@/World';

import type { MaterialData } from '@/data';

export class MaterialChangeSystem {
  private static instance: MaterialChangeSystem | undefined;
  public static getMaterialChangeableEntities() {
    return this.instance?.getMaterialChangeable() ?? [];
  }
  public static getMaterialChangeableEntitiesInFloorplan(floorplanId: string) {
    return this.instance?.getMaterialChangeableInFloorplan(floorplanId) ?? [];
  }

  private targetArchetype = World.with(
    'Identifier',
    'TransformNode',
    'Structure',
    'MaterialChangeable'
  );
  // private targetArchetypeInActiveHome = this.targetArchetype.with('ActiveHome');

  constructor() {
    MaterialChangeSystem.instance = this;
  }

  dispose() {
    MaterialChangeSystem.instance = undefined;
    clearArchetype(this.targetArchetype);
  }

  private getMaterialChangeable() {
    return this.targetArchetype.entities;
  }
  private getMaterialChangeableInFloorplan(floorplanId: string) {
    return this.targetArchetype.entities.filter((e) => e.Identifier.floorplanId == floorplanId);
  }
}
