import {
  DeviceOrientationCamera,
  PointerEventTypes,
  PointerInfo,
  Quaternion,
  Vector2,
  Vector3,
} from '@babylonjs/core';

export class GyroVerticalDragHandler {
  public static pointMapDrag = new Map<number, Vector2>();

  public static processVerticalDrag = (
    e: PointerInfo,
    camera: DeviceOrientationCamera,
    withHorizontal = false
  ): boolean => {
    if (e.type === PointerEventTypes.POINTERDOWN) {
      this.pointMapDrag.set(
        (e.event as PointerEvent).pointerId,
        new Vector2(e.event.clientX, e.event.clientY)
      );
      camera.enableHorizontalDragging(-2 / 300);
    }

    if (e.type === PointerEventTypes.POINTERUP) {
      const id = (e.event as PointerEvent).pointerId;
      this.pointMapDrag.clear();
      camera.enableHorizontalDragging(-2 / 300);
    }

    if (e.type === PointerEventTypes.POINTERMOVE) {
      if (this.pointMapDrag.size === 1) {
        const values = Array.from(this.pointMapDrag.values());
        const p1 = new Vector2(values[0].x, values[0].y);
        const p2 = new Vector2(e.event.clientX, e.event.clientY);

        this.pointMapDrag.set(
          (e.event as PointerEvent).pointerId,
          new Vector2(e.event.clientX, e.event.clientY)
        );

        const isVertical = Math.abs(p2.y - p1.y) > Math.abs(p2.x - p1.x);

        if (isVertical) {
          //camera.enableHorizontalDragging(0);
          const delta = p2.y - p1.y;
          const dir = camera.target.subtract(camera.position);
          const addVec = new Vector3(dir.x * (delta * 0.01), 0, dir.z * (delta * 0.01));
          camera.position = camera.position.add(addVec);
          camera.target = camera.target.add(addVec);

          return true;
        }

        //     (withHorizontal  true  )
        if (withHorizontal) {
          if (!isVertical) {
            const delta = p2.x - p1.x;
            const dir = camera.target.subtract(camera.position);
            dir.applyRotationQuaternionInPlace(
              Quaternion.RotationAxis(Vector3.Up(), -delta * 0.003)
            );
            const pos = camera.position.clone();
            camera.target = pos.add(dir);
          }
        }
      }
    }
    return false;
  };
}
