import { ArcRotateCamera, Scene } from '@babylonjs/core';

import { clearArchetype, World } from '@/World';

import type { Entity } from '@/Entity';

export class CeilingSystem {
  private targetArchetype = World.with('ActiveHome', 'Identifier', 'Structure', 'Ceiling');

  private hideHeights: Map<string, number[]> = new Map();

  constructor() {
    this.targetArchetype.onEntityAdded.add((e) => this.initCeiling(e));
  }

  dispose() {
    clearArchetype(this.targetArchetype);
    this.hideHeights.clear();
  }

  private initCeiling = (entity: (typeof this.targetArchetype.entities)[number]) => {
    let hideHeights = this.hideHeights.get(entity.entityId);
    if (!hideHeights) {
      hideHeights = entity.Structure.meshes
        .flatMap((mesh) => mesh.getBoundingInfo().boundingBox)
        .flatMap((bb) => {
          return bb.centerWorld.y - bb.extendSizeWorld.y;
        });
      this.hideHeights.set(entity.entityId, hideHeights);
    }
  };

  public afterRenderExecute = (scene: Scene) => {
    if (!scene._activeCamera) return;

    for (const entity of this.targetArchetype.entities) {
      const structure = entity.Structure;

      let hideHeights = this.hideHeights.get(entity.entityId);
      if (!hideHeights) return;

      for (let i = 0; i < structure.meshes.length; i++) {
        const visible = scene._activeCamera.position.y <= hideHeights[i];
        structure.meshes[i].visibility = visible ? 1 : 0;
      }
    }
  };
}
