import {
  ArcRotateCamera,
  type DeepImmutableObject,
  Matrix,
  Scene,
  Vector3,
  Viewport,
} from '@babylonjs/core';

import { type StateByThingType, ThingType } from '@/components';
import { ObservableManager } from '@/manager';
import { clearArchetype, World } from '@/World';

export type GuiState = {
  key: string;
  /** thing  */
  name: string;
  /**  x  */
  x: number;
  /**  y  */
  y: number;
  // /** thing  onCLick()    */
  // thing: IThings;
  /**  camera zoom   0~1    min + (max - min) * iconSize   */
  iconSize: number;
  /** thing type */
  thingType?: ThingType;
  /** thing state */
  thingState?: StateByThingType;
};

export class GUIDisplaySystem {
  private targetArchetype = World.with(
    'ActiveHome',
    'Identifier',
    'TransformNode',
    'GUIDisplayable'
  );
  private viewPort?: Viewport;
  private transformMatrix?: Matrix;

  dispose() {
    clearArchetype(this.targetArchetype);
  }

  public afterRenderExecute = (scene: Scene) => {
    const guiData = this.guiUpdate(scene);
    ObservableManager.instance.onUpdateGUIPosition.notifyObservers(guiData);
  };

  private guiUpdate = (scene: Scene): GuiState[] | undefined => {
    const camera = scene.activeCamera;

    if (camera === null || !(camera instanceof ArcRotateCamera)) return;

    const engine = scene.getEngine();
    const scalingLevel = engine.getHardwareScalingLevel();
    const viewport = camera.viewport.clone();
    viewport.y = 1 - viewport.y; // bjs origin is SW

    this.viewPort = viewport.toGlobal(
      engine.getRenderWidth() * scalingLevel,
      engine.getRenderHeight() * scalingLevel
    );

    if (!this.transformMatrix) {
      this.transformMatrix = scene.getTransformMatrix();
    }

    if (!!this.viewPort && !!this.transformMatrix) {
      const iconSize = this.getIconSize(camera);
      const transformMatrix = this.transformMatrix as DeepImmutableObject<Matrix>;
      const viewPort = this.viewPort as DeepImmutableObject<Viewport>;

      return (
        this.targetArchetype.entities.map((entity) => {
          const transform = entity.TransformNode;
          const coordinates = Vector3.Project(
            transform.getAbsolutePosition(),
            Matrix.Identity(),
            transformMatrix,
            viewPort
          );

          return {
            key: entity.entityId,
            name: transform.name,
            x: coordinates.x,
            y: coordinates.y - viewPort.height,
            iconSize: iconSize,
            thingType: entity.ThingState ? entity.ThingState.type : undefined,
            thingState: entity.ThingState ? entity.ThingState.state : undefined,
          };
        }) ?? []
      );
    }
    return [];
  };

  // defaultIconSize = 36;
  // minIconSize = 24;
  // maxIconSize = 50;
  private getIconSize = (camera: ArcRotateCamera) => {
    const radius = camera.radius;
    const minRadius = camera.lowerRadiusLimit;
    const maxRadius = camera.upperRadiusLimit;
    if (minRadius === null || maxRadius === null) {
      return 1;
    } else {
      const radiusGap = maxRadius - minRadius;
      return radiusGap === 0 ? 1 : 1 - Math.min(1, (radius - minRadius) / radiusGap);
    }
  };
}
