import { type DoorState, ThingType, type WindowState } from '@/components';
import { playAnimation } from '@/util/AnimationHelper';
import {
  createEasingFunction,
  EasingFunctionType,
  EasingModeType,
} from '@/util/EasingFunctionType';
import { clearArchetype, World } from '@/World';

export class OpenableSystem {
  //--------------------------------------------
  private targetArchetype = World.with('ActiveHome', 'Identifier', 'ThingState', 'Openable');
  private targetArchetypeDirty = this.targetArchetype.with('ThingStateDirty');

  private openEasingFunction = createEasingFunction(
    EasingFunctionType.QuadraticEase,
    EasingModeType.Out
  );
  private closeEasingFunction = createEasingFunction(
    EasingFunctionType.QuarticEase,
    EasingModeType.Out
  );

  constructor() {
    this.targetArchetype.onEntityAdded.add((e) => this.initEntity(e));
    this.targetArchetypeDirty.onEntityAdded.add((e) => this.updateState(e));
  }

  dispose() {
    clearArchetype(this.targetArchetype);
    clearArchetype(this.targetArchetypeDirty);
  }

  //--------------------------------------------
  private initEntity = (entity: (typeof this.targetArchetype.entities)[number]) => {
    const openable = entity.Openable;
    openable.target.unfreezeWorldMatrix();
    openable.target.getChildMeshes().forEach((mesh) => mesh.unfreezeWorldMatrix());
  };
  private updateState = (entity: (typeof this.targetArchetypeDirty.entities)[number]) => {
    const openable = entity.Openable;
    const thingState = entity.ThingState;
    const currentState = thingState.state;
    if (!currentState) return;

    const duration = 0.8;

    if (thingState.type === ThingType.Door) {
      const isOpen = (currentState as DoorState).isOpen;

      if (isOpen) {
        playAnimation({
          target: openable.target,
          property: {
            targetProperty: 'rotation',
            keyFrame: {
              duration: duration,
              from: openable.closeRotation,
              to: openable.openRotation,
            },
          },
          easingFunction: this.openEasingFunction,
        });
      } else {
        playAnimation({
          target: openable.target,
          property: {
            targetProperty: 'rotation',
            keyFrame: {
              duration: duration,
              from: openable.openRotation,
              to: openable.closeRotation,
            },
          },
          easingFunction: this.closeEasingFunction,
        });
      }
    } else {
      const isOpen = (currentState as WindowState).isOpen;
      if (isOpen) {
        playAnimation({
          target: openable.target,
          property: {
            targetProperty: 'position',
            keyFrame: {
              duration: duration,
              from: openable.closePosition,
              to: openable.openPosition,
            },
          },
          easingFunction: this.openEasingFunction,
        });
      } else {
        playAnimation({
          target: openable.target,
          property: {
            targetProperty: 'position',
            keyFrame: {
              duration: duration,
              from: openable.openPosition,
              to: openable.closePosition,
            },
          },
          easingFunction: this.closeEasingFunction,
        });
      }
    }
  };
}
