import {
  Color3,
  MaterialDefines,
  MaterialPluginBase,
  PBRBaseMaterial,
  PBRMaterial,
  Scene,
  StandardMaterial,
  UniformBuffer,
} from '@babylonjs/core';

export class RoomLightDefines extends MaterialDefines {
  ROOMLIGHT = false;
}

export class RoomLightConfiguration {
  roomColor: Color3 = Color3.White();
}

export class RoomLightMaterial extends MaterialPluginBase {
  //---------------------------------------
  private colorName: string;
  constructor(material: PBRMaterial | StandardMaterial, addToPluginList?: boolean) {
    super(material, 'RoomLight', 200, new RoomLightDefines(), addToPluginList);
    this.colorName = material instanceof PBRBaseMaterial ? 'finalColor' : 'color';
  }

  //---------------------------------------
  private _config: RoomLightConfiguration | undefined;
  get config() {
    return this._config;
  }
  set config(value) {
    if (this._config === value) {
      return;
    }
    this._config = value;
  }

  //---------------------------------------
  private _isEnabled = false;
  get isEnabled() {
    return this._isEnabled;
  }
  set isEnabled(enabled) {
    if (this._isEnabled === enabled) {
      return;
    }
    this._isEnabled = enabled;
    this.markAllDefinesAsDirty();
    this._enable(this._isEnabled);
  }

  //---------------------------------------
  getClassName() {
    return 'RoomLightConfiguration';
  }

  //---------------------------------------
  prepareDefines(defines: RoomLightDefines, scene: Scene) {
    defines['ROOMLIGHT'] = this._isEnabled;
  }

  //---------------------------------------
  getUniforms() {
    return {
      ubo: [{ name: 'roomColor', size: 3, type: 'vec3' }],
      fragment: `#ifdef ROOMLIGHT
                uniform vec3 roomColor;
            #endif`,
    };
  }

  bindForSubMesh(uniformBuffer: UniformBuffer, scene: Scene) {
    if (this._isEnabled) {
      uniformBuffer.updateColor3('roomColor', this.config!.roomColor);
    }
  }

  //---------------------------------------
  getCustomCode(shaderType: string) {
    return shaderType === 'vertex' ? this.getVertexCode() : this.getFragmentCode();
  }

  getVertexCode() {
    return null;
  }

  getFragmentCode() {
    return {
      CUSTOM_FRAGMENT_BEFORE_FRAGCOLOR: `
      #ifdef ROOMLIGHT
          ${this.colorName}.rgb *= roomColor;
      #endif
    `,
    };
  }
}
