import { Observable } from '@babylonjs/core';

import { type GuiState } from '../../system/GUIDisplaySystem';

export class ObservableManager {
  private static singletonInstance: ObservableManager | undefined;
  public static get instance(): ObservableManager {
    if (this.singletonInstance === undefined) {
      this.singletonInstance = new ObservableManager();
    }
    return this.singletonInstance;
  }

  public onUpdateGUIPosition: Observable<GuiState[] | undefined>;
  public onFirstModelLoaded: Observable<undefined>;
  public onAllModelLoaded: Observable<undefined>;
  public onModelLoadProgress: Observable<number>;
  public onEntityStateChanged: Observable<string>;

  constructor() {
    ObservableManager.singletonInstance = this;

    this.onUpdateGUIPosition = new Observable<GuiState[] | undefined>();
    this.onFirstModelLoaded = new Observable<undefined>();
    this.onAllModelLoaded = new Observable<undefined>();
    this.onModelLoadProgress = new Observable<number>();
    this.onEntityStateChanged = new Observable<string>();
  }

  reset = () => {
    this.onUpdateGUIPosition.clear();
    this.onFirstModelLoaded.clear();
    this.onAllModelLoaded.clear();
    this.onModelLoadProgress.clear();
    this.onEntityStateChanged.clear();
  };
}
