import { EngineStore, Ray, Vector3 } from '@babylonjs/core';

import { type LightState } from '@/components';
import { MaterialPluginManager } from '@/manager';
import { clearArchetype, World } from '@/World';

export class LightControllerSystem {
  private static instance: LightControllerSystem | undefined;

  //--------------------------------------------
  private targetArchetype = World.with(
    'Identifier',
    'TransformNode',
    'Structure',
    'ThingState',
    'LightController'
  );
  private targetArchetypeInActiveHome = this.targetArchetype.with('ActiveHome');
  private targetArchetypeDirty = this.targetArchetypeInActiveHome.with('ThingStateDirty');

  constructor() {
    LightControllerSystem.instance = this;
    this.targetArchetypeDirty.onEntityAdded.add((e) => this.updateState(e));
  }

  dispose() {
    LightControllerSystem.instance = undefined;
    clearArchetype(this.targetArchetype);
    clearArchetype(this.targetArchetypeInActiveHome);
    clearArchetype(this.targetArchetypeDirty);
  }

  private updateState = (entity: (typeof this.targetArchetypeDirty.entities)[number]) => {
    const state = entity.ThingState.state as LightState;
    MaterialPluginManager.instance.setRoomLightColor(entity.entityId, state.isOn, state.intensity);
  };
}
