import {
  AbstractMesh,
  Animation,
  Camera,
  Color3,
  EasingFunction,
  EngineStore,
  Material,
  Scene,
  TransformNode,
  Vector3,
  Viewport,
} from '@babylonjs/core';

import type { RoomLightConfiguration } from '@/material/RoomLightMaterial';

//-------------------------------------------------
// *  
const ANIMATION_TARGET_FRAME = 30;
// * Target   Type.
type AnimationTarget =
  | AbstractMesh
  | Material
  | Camera
  | Viewport
  | RoomLightConfiguration
  | TransformNode;
// * KeyFrame From/To Value     Type.
type AnimationValue = number | Vector3 | Color3;

//-------------------------------------------------
/**
 * #### Animation  Type
 * * target : AnimationGroup  1  . (AbstractMesh | Material | Camera ...)
 * * property : AnimationGroup  1  property.  property  Animation  .
 * * easingFunction :   ,  Animation  easingFunction .
 * * onAnimationEnd :   ,  Animation     .
 * 
 * @example
 * ####  Property
 * ```ts
 *  {
 *    target: mesh.material,
 *    property: {
 *      targetProperty: 'albedoColor',
 *      keyFrame: {
 *        duration: 0.6,
 *        from: mesh.material.albedoColor,
 *        to: this.disableColor,
 *      },
 *    },
 *    easingFunction: createEasingFunction(
 *                      EasingFunctionType.QuarticEase, 
 *                      EasingModeType.In)
 *  }
 * ```
 * ####  Property
 * ```ts
 *  {
 *    target: mesh.material,
 *    property: [
        {
          targetProperty: 'target',
          keyFrame: {
            duration: 0.6,
            from: camera.target,
            to: target,
          },
        },
        {
          targetProperty: 'beta',
          keyFrame: {
            duration: 0.6,
            from: camera.beta,
            to: beta,
          },
        },
      ],
      easingFunction: this.getEasingFunction()
 *  }
 * ```
 */
export interface AnimationInput<T> {
  target: T | T[];
  property: AnimationProperty<T> | AnimationProperty<T>[];
  easingFunction?: EasingFunction;
  onAnimationEnd?: () => void;
}

//-------------------------------------------------
/**
 * #### Property  Type
 * * targetProperty : Animation  property .
 * * keyFrame : Animation  1  KeyFrame.
 */
export interface AnimationProperty<T> {
  targetProperty: keyof T;
  keyFrame: AnimationKeyFrame | AnimationKeyFrame[];
}

//-------------------------------------------------
/**
 * #### KeyFrame  Type
 * * duration :      frame  . (seconds )
 * * frame :     frame . (, duration  )
 * * from :  .
 * * to :  . (from to type  .)
 */
export interface AnimationKeyFrame {
  duration?: number;
  frame?: number;
  from: AnimationValue;
  to: AnimationValue;
}

//-------------------------------------------------
/**
 * #### AnimationGroup   .
 *
 * @example
 * ```ts
 *  playAnimation(
 *    this.prop.scene,
 *    {
 *      target: mesh.material,
 *      property: {
 *        targetProperty: 'albedoColor',
 *        keyFrame: {
 *          duration: 0.6,
 *          from: mesh.material.albedoColor,
 *          to: this.disableColor,
 *        },
 *      easingFunction: createEasingFunction(
 *                        EasingFunctionType.QuarticEase,
 *                        EasingModeType.In),
 *      onAnimationEnd: () => {
 *          console.log('onAnimationEnd');
 *        }
 *      }
 *    });
 * ```
 */
export const playAnimation = async <T extends AnimationTarget>(
  input: AnimationInput<T>,
  scene?: Scene,
  stopCurrunt: boolean = false
) => {
  let currentScene = !!scene ? scene : EngineStore.LastCreatedScene;
  if (!currentScene) return;

  let animations: Animation[] = [];

  // *  Property  Animation .
  if (Array.isArray(input.property)) {
    animations.push(
      ...input.property.map((property) =>
        generateAnimationByProperty(property, input.easingFunction)
      )
    );
  } else {
    animations.push(generateAnimationByProperty(input.property, input.easingFunction));
  }

  // *      Frame .
  let maxFrame = 0;
  for (let i = 0; i < animations.length; i++) {
    animations[i].getKeys().forEach((keyFrame) => {
      maxFrame = Math.max(keyFrame.frame, maxFrame);
    });
  }

  // *    Target .
  if (Array.isArray(input.target)) {
    const animatables = input.target.flatMap((target) =>
      !!currentScene
        ? playAnimationTarget(currentScene, target, animations, maxFrame, stopCurrunt)
        : []
    );

    // *   , await  .
    if (!!input.onAnimationEnd) {
      await Promise.all(animatables.map((animatable) => animatable.waitAsync()));
      input.onAnimationEnd();
    }
  } else {
    const animatable = playAnimationTarget(
      currentScene,
      input.target,
      animations,
      maxFrame,
      stopCurrunt
    );
    if (!!input.onAnimationEnd) {
      await animatable.waitAsync();
      input.onAnimationEnd();
    }
  }
};

//-------------------------------------------------
export const generateAnimation = (targetProperty: string, animationType: number) => {
  return new Animation(
    targetProperty,
    targetProperty,
    ANIMATION_TARGET_FRAME,
    animationType,
    Animation.ANIMATIONLOOPMODE_CONSTANT
  );
};
const generateAnimationByProperty = (param: AnimationProperty, easingFunction?: EasingFunction) => {
  let type = 0;
  if (Array.isArray(param.keyFrame)) {
    type = getAnimationType(param.keyFrame[0].from);
  } else {
    type = getAnimationType(param.keyFrame.from);
  }
  const animation = generateAnimation(param.targetProperty, type);
  if (easingFunction) animation.setEasingFunction(easingFunction);

  if (Array.isArray(param.keyFrame)) {
    let start = 0;
    param.keyFrame.forEach((keyFrame) => {
      let frame = 0;
      if (keyFrame.duration !== undefined) {
        frame = timeToFrame(keyFrame.duration);
      } else if (keyFrame.frame !== undefined) {
        frame = keyFrame.frame;
      }
      setAnimationKeyFrame(animation, keyFrame, start, start + frame);
      start += frame;
    });
  } else {
    let frame = 0;
    if (param.keyFrame.duration !== undefined) {
      frame = timeToFrame(param.keyFrame.duration);
    } else if (param.keyFrame.frame !== undefined) {
      frame = param.keyFrame.frame;
    }
    setAnimationKeyFrame(animation, param.keyFrame, 0, frame);
  }
  return animation;
};
const setAnimationKeyFrame = (
  animation: Animation,
  keyFrame: AnimationKeyFrame,
  start: number,
  end: number
) => {
  animation.setKeys([
    { frame: start, value: keyFrame.from },
    { frame: end, value: keyFrame.to },
  ]);
};
const playAnimationTarget = (
  scene: Scene,
  target: AnimationTarget,
  animations: Animation[],
  frame: number,
  stopCurrunt: boolean
) => {
  target.animations = animations;

  const animatable = scene.beginAnimation(
    target,
    0,
    frame,
    false,
    1,
    undefined,
    undefined,
    stopCurrunt
  );
  animatable.disposeOnEnd = true;

  return animatable;
};
const TYPEOF_NUMBER = 'number';
const getAnimationType = (value: AnimationValue) => {
  if (typeof value === TYPEOF_NUMBER) {
    return Animation.ANIMATIONTYPE_FLOAT;
  } else if (value instanceof Vector3) {
    return Animation.ANIMATIONTYPE_VECTOR3;
  } else if (value instanceof Color3) {
    return Animation.ANIMATIONTYPE_COLOR3;
  }

  return Animation.ANIMATIONTYPE_FLOAT;
};
//-------------------------------------------------
export const timeToFrame = (seconds: number) => {
  return Math.round(seconds * ANIMATION_TARGET_FRAME);
};
