import {
  AbstractMesh,
  Angle,
  Color4,
  Database,
  DeviceOrientationCamera,
  Engine,
  FreeCamera,
  GizmoManager,
  type Nullable,
  PointerEventTypes,
  PointerInfo,
  Quaternion,
  Scene,
  Vector3,
} from '@babylonjs/core';

import {
  cacheJsonData,
  deleteCache,
  disposeLoader,
  floorplanData,
  loadHome,
  type QualitySettings,
  Resolution,
} from '@/data';
import {
  CameraManager,
  FurnitureManager,
  GraphicManager,
  MaterialPluginManager,
  ObservableManager,
  RenderManager,
  SystemManager,
} from '@/manager';
import { World } from '@/World';
import { CameraType, ZEDCamera, ZEDCameraBase, ZEDFreeCamera, ZEDGyroCamera } from '@/ZEDCamera';

import { PerformanceProfiler } from './manager/PerformanceProfiler';
import {
  ActiveHomeSystem,
  FloorplanSystem,
  MaterialChangeSystem,
  RoomSystem,
  TransformSystem,
} from './system';
import { CameraAnimationHelper } from './util/camera/CameraAnimationHelper';
import {
  getDirectionToCenter,
  getWallMeshesAll,
  timeout,
  toAngle,
  toRadian,
} from './util/camera/CameraUtils';
import { GyroPinchHandler } from './util/camera/GyroPinchHandler';
import { GyroVerticalDragHandler } from './util/camera/GyroVerticalDragHandler';
import { Profile } from './util/Profile';
import { convertUnityRotationYToBabylon, convertUnityScaleToBabylon } from './util/Utile';

class ZED {
  //-------------------------------------------------
  // * Core Member
  private engine!: Engine;
  private scene!: Scene;

  //-------------------------------------------------
  // * Base Member
  private zedcamera!: ZEDCameraBase;
  private renderManager!: RenderManager;
  private systemManager!: SystemManager;
  private gizmoManager!: GizmoManager;
  private materialPluginManager!: MaterialPluginManager;

  //-------------------------------------------------
  // * API Class
  public camera!: CameraManager;
  public furniture!: FurnitureManager;
  public graphic!: GraphicManager;
  public observer!: ObservableManager;
  public performance!: PerformanceProfiler;

  //-------------------------------------------------
  constructor(
    private canvasOrContext: Nullable<
      HTMLCanvasElement | OffscreenCanvas | WebGLRenderingContext | WebGL2RenderingContext
    >
  ) {
    this.initialize();
  }

  //-------------------------------------------------
  // * initialize & dispose
  private initialize = () => {
    Profile.start('Initialize');
    // * Core Member 
    this.engine = new Engine(this.canvasOrContext, true, {
      adaptToDeviceRatio: true,
    });
    //     true  ,     
    // https://forum.babylonjs.com/t/engine-disableuniformbuffers-is-set-to-false-in-mobile-mode-and-cause-a-bad-performance/41228
    this.engine.disableUniformBuffers = true;
    this.scene = new Scene(this.engine);

    // * Scene 
    this.scene.clearColor = new Color4(0, 0, 0, 0);
    this.scene.autoClear = false; // Color buffer
    this.scene.autoClearDepthAndStencil = false; // Depth and stencil, obviously
    this.scene.skipFrustumClipping = true;
    this.scene.skipPointerMovePicking = true;
    this.scene.blockMaterialDirtyMechanism = true;
    this.scene.getEngine().stopRenderLoop();

    // * GizmoManager 
    this.gizmoManager = new GizmoManager(this.scene);
    this.gizmoManager.enableAutoPicking = false;
    this.gizmoManager.onAttachedToNodeObservable.add((ed) => {
      const enable = !!ed;
      this.gizmoManager.positionGizmoEnabled = enable;
      this.gizmoManager.rotationGizmoEnabled = enable;
      if (!!this.gizmoManager.gizmos.positionGizmo) {
        this.gizmoManager.gizmos.positionGizmo.yGizmo.isEnabled = false;
        this.gizmoManager.gizmos.positionGizmo.scaleRatio = 0.8;
      }
      if (!!this.gizmoManager.gizmos.rotationGizmo) {
        this.gizmoManager.gizmos.rotationGizmo.xGizmo.isEnabled = false;
        this.gizmoManager.gizmos.rotationGizmo.zGizmo.isEnabled = false;
        this.gizmoManager.gizmos.rotationGizmo.scaleRatio = 0.5;
      }
    });

    // * Singleton Member 
    this.observer = new ObservableManager();
    this.materialPluginManager = new MaterialPluginManager();

    // * Base Member 
    this.zedcamera = new ZEDCamera(this.scene);
    this.renderManager = new RenderManager(
      this.scene,
      this.engine,
      this.gizmoManager,
      this.canvasOrContext instanceof HTMLCanvasElement ? this.canvasOrContext : undefined
    );
    this.systemManager = new SystemManager(this.scene);

    // * API Class 
    this.camera = new CameraManager(this.engine, this.scene, this.zedcamera);
    this.furniture = new FurnitureManager(this.gizmoManager);
    this.graphic = new GraphicManager();
    this.performance = PerformanceProfiler.getInstance(this.scene);

    //-------------------------------------------------
    // * Model            
    this.observer.onAllModelLoaded.add(() => {
      this.setActiveHome(this.activeHomeId);
    });
    Profile.end('Initialize');
  };
  public dispose = () => {
    Profile.reset();

    // * ECS World Clear
    World.clear();

    disposeLoader();

    // * Singleton Member Reset
    this.observer.reset();
    this.materialPluginManager.reset();

    // * Base Member Dispose
    this.graphic.dispose();
    this.renderManager.reset();
    this.zedcamera.reset();
    this.gizmoManager.dispose();
    this.systemManager.reset();

    // * Babylon.js Dispose
    this.scene.clearCachedVertexData();
    this.scene.cleanCachedTextureBuffer();
    this.scene.dispose();
    this.engine.dispose();
  };

  //-------------------------------------------------
  // * DBStorageEnabled (   )
  public EnableDBStorage(enable: boolean) {
    Database.IDBStorageEnabled = enable;
  }

  //-------------------------------------------------
  // * Change Camera

  public changeCamera = (newType: CameraType) => {
    const currentType = this.getCameraType();

    if (currentType === newType) return;

    let newCamera: ZEDCameraBase | undefined = undefined;

    switch (newType) {
      case CameraType.Free:
        newCamera = new ZEDFreeCamera(this.scene);
        break;
      case CameraType.ArcRotate:
        newCamera = new ZEDCamera(this.scene);
        (newCamera as ZEDCamera).recoverMinMaxDistance();
        break;
      case CameraType.Gyro:
        const oldCamera = this.zedcamera.babylonCamera();
        const dir = oldCamera.target.subtract(oldCamera.position);
        newCamera = new ZEDGyroCamera(this.scene, oldCamera.position, dir);
        break;
      default:
        throw new Error('impossible camera type.');
    }

    //    
    newCamera.copyFromCamera(this.zedcamera);
    this.changeZedCamera(newCamera);

    //   (   )
    this.clearGyroCallbacks();
    if (newType === CameraType.Gyro) {
      this.scene.onPointerObservable.add(this.onTouchInGyro);
    }

    this.renderManager.runRenderLoop(true);
  };

  public getCameraType = () => {
    if (this.zedcamera instanceof ZEDCamera) return CameraType.ArcRotate;
    else if (this.zedcamera instanceof ZEDFreeCamera) return CameraType.Free;
    else if (this.zedcamera instanceof ZEDGyroCamera) return CameraType.Gyro;

    throw new Error('impossible camera type.');
  };

  //-------------------------------------------------
  // * Gyro Mode
  private gyroTransitionAnimationSeconds = 0.7;

  private isGyroSupported = true;

  //     Top View  
  // ( top view   ,       )
  public startTopViewForGyro = (animationSeconds?: number) => {
    if (animationSeconds !== undefined) {
      this.gyroTransitionAnimationSeconds = animationSeconds;
    }

    if (this.getCameraType() !== CameraType.ArcRotate) {
      this.changeCamera(CameraType.ArcRotate);
    }
    this.scene.onPointerObservable.add(this.onTouchInTopView);

    this.camera.topViewFit();
  };

  public endGyro = () => {
    this.changeCamera(CameraType.ArcRotate);
  };

  //      
  // - Free   
  // -        
  // - dir  undefined    
  public moveToGyroPosition = async (
    startPosition?: Vector3,
    startDir?: Vector3,
    animationSeconds?: number
  ) => {
    if (this.getCameraType() === CameraType.Gyro) {
      return;
    }

    //   
    if (animationSeconds !== undefined) {
      this.gyroTransitionAnimationSeconds = animationSeconds;
    }
    const seconds = this.gyroTransitionAnimationSeconds;

    //  ,  
    const pos = startPosition ? startPosition : new Vector3(0, 1.6, 0);
    const dir = startDir ? startDir : getDirectionToCenter(pos, getWallMeshesAll());

    //   
    const gyroCamera = new ZEDGyroCamera(this.scene, pos, dir);
    const internalGyro = gyroCamera.babylonCamera();

    //    
    // (isGyroSupported   false    )
    if (this.isGyroSupported) {
      const p1 = (gyroCamera as ZEDGyroCamera).getGyroAngles();
      const p2 = new Promise((resolve) => setTimeout(resolve, 2000, undefined));
      const value = await Promise.any([p1, p2]);

      if (value === undefined) this.isGyroSupported = false;
    }

    // *********************************************
    // 1.  Free  
    this.changeCamera(CameraType.Free);
    const internalFree = this.zedcamera.babylonCamera();
    // Free Camera  target    
    // ( arcRotate   radius      )
    const unitDir = internalFree.target.subtract(internalFree.position).normalize();
    internalFree.target = internalFree.position.add(unitDir);
    if (internalFree.rotation.y < 0) {
      internalFree.rotation.y += 360 * toRadian;
    }

    // *********************************************
    // 2.   

    //  (   )
    if (!this.isGyroSupported) {
      internalGyro.position = pos!;
      internalGyro.position.y = 1.6;
      internalGyro.setTarget(internalGyro.position.add(dir!));
      internalGyro.inputs.removeByType('FreeCameraMouseInput');
    }

    //  
    const promise = CameraAnimationHelper.toOtherCameraState(
      internalFree,
      internalGyro,
      this.scene,
      seconds
    );

    // return promise;

    //    resolve
    return new Promise((resolve) => setTimeout(resolve, seconds * 1000 * 1.1, undefined));
  };

  private onTouchInTopView = (e: PointerInfo) => {
    const position = e.pickInfo && e.pickInfo.pickedPoint ? e.pickInfo.pickedPoint : undefined;
    if (e.type === PointerEventTypes.POINTERTAP) {
      //    ->  
      this.moveToGyroPosition(position!).then(() => {
        this.changeCamera(CameraType.Gyro);
      });
    }
  };

  private onTouchInGyro = (e: PointerInfo) => {
    const deviceCamera = this.zedcamera.babylonCamera() as DeviceOrientationCamera;

    if (GyroPinchHandler.processPinch(e, deviceCamera)) return;

    //       
    const withHorizontal = this.isGyroSupported === false ? true : false;

    GyroVerticalDragHandler.processVerticalDrag(e, deviceCamera, withHorizontal);

    if (e.type === PointerEventTypes.POINTERTAP) {
      // this.zedcamera.moveToPosition(e.pickInfo?.pickedPoint); // 
    } else if (e.type === PointerEventTypes.POINTERDOUBLETAP) {
      this.startTopViewForGyro(); //  
    }
  };

  //   -       
  private clearGyroCallbacks = () => {
    this.scene.onPointerObservable.removeCallback(this.onTouchInTopView);
    this.scene.onPointerObservable.removeCallback(this.onTouchInGyro);
  };

  //   -        
  private changeZedCamera = (newCamera: ZEDCameraBase) => {
    const oldCamera = this.zedcamera;
    this.camera = new CameraManager(this.engine, this.scene, newCamera);
    this.graphic.replaceCamera(oldCamera.babylonCamera(), newCamera.babylonCamera());
    this.zedcamera = newCamera;
    this.zedcamera.setActive();
  };

  //-------------------------------------------------
  // * Load & Save
  private defalutQualitySettings: QualitySettings = {
    simpleMode: false,
    modelLOD: 'LOD0',
    textureMaxSize: '2048',
    resolution: Resolution.p900,
    reflection: false,
    useNormalTexture: false,
  };

  public loadHome = async (homeId: string, qualitySettings?: QualitySettings) => {
    this.activeHomeId = homeId;

    if (qualitySettings === undefined) {
      qualitySettings = this.defalutQualitySettings;
    }
    if (qualitySettings.reflection === undefined) {
      qualitySettings.reflection = this.defalutQualitySettings.reflection;
    }
    if (qualitySettings.resolution === undefined) {
      qualitySettings.resolution = this.defalutQualitySettings.resolution;
    }
    if (qualitySettings.useNormalTexture === undefined) {
      qualitySettings.useNormalTexture = this.defalutQualitySettings.useNormalTexture;
    }
    RenderManager.SetResolution(qualitySettings.resolution!);

    Profile.start('Load Home');
    await loadHome(this.scene, homeId, this.graphic, qualitySettings);
    Profile.end('Load Home');
    this.performance.loadHomeTotal = Profile.get('Load Home') ?? 0;

    Profile.print();
    Profile.reset();
  };

  public async saveCache() {
    const homeId = this.activeHomeId;
    const materialChangedEntities = MaterialChangeSystem.getMaterialChangeableEntities();

    const floorplan = floorplanData[homeId];
    if (!floorplan) return;

    floorplan.forEach((f) => {
      const entities = TransformSystem.getMovableTransformEntitiesInFloorplan(f.floorplanId);
      const floorplanEntity = FloorplanSystem.getFloorplanEntity(f.floorplanId)?.Floorplan;

      f.furniturePlaceData.forEach((d) => {
        const entity = entities.find((e) => e.TransformNode.name === d.name);
        if (entity) {
          const TransformNode = entity.TransformNode;
          const position = floorplanEntity
            ? TransformNode.absolutePosition.subtract(floorplanEntity.pivotPosition)
            : TransformNode.absolutePosition;

          if (TransformNode.rotationQuaternion) {
            TransformNode.rotationQuaternion.toEulerAnglesToRef(TransformNode.rotation);
            TransformNode.rotationQuaternion = null;
          }
          const rotationY = Angle.FromRadians(
            convertUnityRotationYToBabylon(TransformNode.rotation.y)
          ).degrees();
          const scale = convertUnityScaleToBabylon(TransformNode.scaling);
          // @ts-ignore
          d.position = { x: position.x, y: position.y, z: position.z };
          d.rotationY = rotationY;
          // @ts-ignore
          d.scale = { x: scale.x, y: scale.y, z: scale.z };
        }
      });

      const overrideData = materialChangedEntities
        .filter((e) => e.Identifier.floorplanId === f.floorplanId)
        .flatMap((e) => e.MaterialChangeable.materialOverrideData);

      f.materialOverrideData = overrideData;

      console.log(f);
    });

    await cacheJsonData('User', `Zed_${homeId}.json`, floorplan);
  }
  public async deleteCache() {
    await deleteCache('User');
  }

  //-------------------------------------------------
  // * Home
  public activeHomeId: string = '';
  public setActiveHome = (homeId: string, moveCamera = true): void => {
    this.activeHomeId = homeId;
    ActiveHomeSystem.setActiveHome(homeId);

    if (moveCamera) {
      if (this.activeFloorplanEntities.length > 0) {
        const toTopView = false;
        this.camera.setTargetFloor(
          this.activeFloorplanEntities[0].Identifier.floorplanId,
          toTopView
        );
      }
    } else {
      RenderManager.RenderUpdate();
    }
  };

  //-------------------------------------------------
  // * Floorplan
  public get activeFloorplanEntities() {
    return FloorplanSystem.getFloorplanEntitiesInActiveHome();
  }

  //-------------------------------------------------
  // * Room
  /**
   * Room Component   Entity RoomInfo  .
   * @param floorplanId   floorplan  RoomInfo .
   * @returns
   */
  public getRooms(floorplanId?: string) {
    return RoomSystem.getRooms(floorplanId);
  }
  /**
   * RoomObject Component   Entity RoomObjectInfo  .
   * @param roomId   room  Entity .
   * @returns
   */
  public getRoomObjects(roomId?: string) {
    return RoomSystem.getRoomObjects(roomId);
  }
  /**
   * RoomObject Component   Entity  Thing(IoT)State  Entity RoomObjectInfo  .
   * @param roomId   room  Entity .
   * @returns
   */
  public getRoomObjectsOnlyThing(roomId?: string) {
    return RoomSystem.getRoomObjectsOnlyThing(roomId);
  }
}
export default ZED;
