import { ArcRotateCamera, PBRMaterial, Scene } from '@babylonjs/core';

import { toRadian } from '@/util/Utile';
import { clearArchetype, World } from '@/World';

export class TransparentSystem {
  private targetArchetype = World.with('ActiveHome', 'Identifier', 'Structure', 'Transparentable');

  constructor() {
    this.targetArchetype.onEntityAdded.add((e) => this.initTransparentable(e));
  }
  dispose() {
    clearArchetype(this.targetArchetype);
  }

  private initTransparentable = (entity: (typeof this.targetArchetype.entities)[number]) => {
    entity.Structure.meshes.forEach((mesh) => {
      if (mesh.material && mesh.material instanceof PBRMaterial) {
        mesh.material.transparencyMode = 2;
      }
    });
  };

  public afterRenderExecute = (scene: Scene) => {
    const camera = scene.activeCamera;

    if (camera === null || !(camera instanceof ArcRotateCamera)) return;

    const beta = camera.beta;
    const alpha = 0.55;
    const radianStart = toRadian(20);
    const radianEnd = radianStart + toRadian(60);

    let ratio = (beta - radianStart) / (radianEnd - radianStart);
    if (ratio > 1) ratio = 1;
    if (ratio < 0) ratio = 0;

    // change material
    const targetAlpha = 1 - ratio * (1 - alpha);
    if (!Number.isNaN(targetAlpha)) {
      for (const entity of this.targetArchetype.entities) {
        const structure = entity.Structure;
        structure.meshes.forEach((mesh) => {
          if (mesh.material && mesh.material instanceof PBRMaterial) {
            mesh.material.alpha = targetAlpha;
          }
        });
      }
    }
  };
}
