import { clearArchetype, World } from '@/World';

export class FloorplanSystem {
  private static instance: FloorplanSystem | undefined;
  public static getFloorplanEntities() {
    return this.instance?.getFloorplanEntities() ?? [];
  }
  public static getFloorplanEntitiesInActiveHome() {
    return this.instance?.getFloorplanEntitiesInActiveHome() ?? [];
  }
  public static getFloorplanEntity(floorplanId: string) {
    return this.instance?.getFloorplanEntity(floorplanId);
  }

  private targetArchetype = World.with('Identifier', 'Floorplan');
  private targetArchetypeInActiveHome = this.targetArchetype.with('ActiveHome');

  constructor() {
    FloorplanSystem.instance = this;
  }

  dispose() {
    FloorplanSystem.instance = undefined;
    clearArchetype(this.targetArchetype);
    clearArchetype(this.targetArchetypeInActiveHome);
  }

  public getFloorplanEntities() {
    return this.targetArchetype.entities ?? [];
  }
  public getFloorplanEntitiesInActiveHome() {
    return this.targetArchetypeInActiveHome.entities ?? [];
  }
  public getFloorplanEntity(floorplanId: string) {
    return this.targetArchetype.entities.find((e) => e.Identifier.floorplanId == floorplanId);
  }
}
