import {
  Animation,
  ArcRotateCamera,
  Scene,
  TargetCamera,
  Vector3,
  Viewport,
} from '@babylonjs/core';
import { CircleEase, EasingFunction } from '@babylonjs/core/Animations/easing';

import { generateAnimation, playAnimation, timeToFrame } from '../AnimationHelper';
import { createEasingFunction, EasingFunctionType, EasingModeType } from '../EasingFunctionType';

import { arcToFree, toRadian } from './CameraUtils';

export class CameraAnimationHelper {
  private scene: Scene;

  constructor(scene: Scene) {
    this.scene = scene;

    this.easingFunction = new CircleEase();
    this.easingFunction.setEasingMode(EasingFunction.EASINGMODE_EASEOUT);
  }

  public moveAnimationTargetCameraWithArcRotate = async (
    camera: TargetCamera,
    target: Vector3,
    alpha: number,
    beta: number,
    radius: number,
    seconds: number
  ) => {
    const { pos, rot } = arcToFree(this.scene, target, alpha, beta, radius);

    if (seconds <= 0) {
      camera.target = target;
      camera.position = pos;
      camera.upVector = new Vector3(0, 1, 0);
    } else {
      await playAnimation(
        {
          target: camera,
          property: [
            {
              targetProperty: 'position',
              keyFrame: {
                duration: seconds,
                from: camera.position,
                to: pos,
              },
            },
            {
              targetProperty: 'rotation',
              keyFrame: {
                duration: seconds,
                from: camera.rotation,
                to: rot,
              },
            },
            {
              targetProperty: 'upVector',
              keyFrame: {
                duration: seconds,
                from: camera.upVector,
                to: new Vector3(0, 1, 0),
              },
            },
          ],
          easingFunction: this.getEasingFunction(),
          onAnimationEnd: () => {},
        },
        this.scene
      );
    }
  };

  public moveAnimationTargetCamera = async (
    camera: TargetCamera,
    pos: Vector3,
    rot: Vector3,
    seconds: number
  ) => {
    if (seconds <= 0) {
      camera.position = pos;
      camera.rotation = rot;
    } else {
      await playAnimation(
        {
          target: camera,
          property: [
            {
              targetProperty: 'position',
              keyFrame: {
                duration: seconds,
                from: camera.position,
                to: pos,
              },
            },
            {
              targetProperty: 'rotation',
              keyFrame: {
                duration: seconds,
                from: camera.rotation,
                to: rot,
              },
            },
          ],
          easingFunction: this.getEasingFunction(),
          onAnimationEnd: () => {},
        },
        this.scene
      );
    }
  };

  public moveAnimation = async (
    camera: ArcRotateCamera,
    target: Vector3,
    alpha: number,
    beta: number,
    radius: number,
    seconds: number
  ) => {
    this.resetInertia(camera);

    const angleLimit = (rad: number): number => {
      if (rad > Math.PI) rad -= 2 * Math.PI;
      if (rad < -Math.PI) rad += 2 * Math.PI;
      return rad;
    };

    if (seconds <= 0) {
      camera.target = target;
      camera.alpha = angleLimit(alpha);
      camera.beta = beta;
      camera.radius = radius;
    } else {
      await playAnimation(
        {
          target: camera,
          property: [
            {
              targetProperty: 'target',
              keyFrame: {
                duration: seconds,
                from: camera.target,
                to: target,
              },
            },
            {
              targetProperty: 'alpha',
              keyFrame: {
                duration: seconds,
                from: angleLimit(camera.alpha),
                to: angleLimit(alpha),
              },
            },
            {
              targetProperty: 'beta',
              keyFrame: {
                duration: seconds,
                from: camera.beta,
                to: beta,
              },
            },
            {
              targetProperty: 'radius',
              keyFrame: {
                duration: seconds,
                from: camera.radius,
                to: radius,
              },
            },
          ],
          easingFunction: this.getEasingFunction(),
          onAnimationEnd: () => {},
        },
        this.scene
      );
    }
  };

  public changeAlphaWithAnimation = (
    alphaFrom: number,
    alphaTo: number,
    camera: ArcRotateCamera
  ) => {
    this.resetInertia(camera);

    playAnimation(
      {
        target: camera,
        property: {
          targetProperty: 'alpha',
          keyFrame: {
            duration: 0.2,
            from: alphaFrom,
            to: alphaTo,
          },
        },
        easingFunction: this.getEasingFunction(),
      },
      this.scene
    );
  };

  public positionAnimation = (
    camera: TargetCamera,
    position: Vector3,
    seconds: number,
    stopCurrent?: boolean
  ) => {
    playAnimation(
      {
        target: camera,
        property: [
          {
            targetProperty: 'position',
            keyFrame: {
              duration: seconds,
              from: camera.position,
              to: position,
            },
          },
          {
            targetProperty: 'target',
            keyFrame: {
              duration: seconds,
              from: camera.target,
              to: camera.target,
            },
          },
        ],
        easingFunction: this.getEasingFunction(),
      },
      this.scene,
      stopCurrent
    );
  };

  public zoomAnimation = (
    camera: ArcRotateCamera,
    radius: number,
    target: Vector3,
    seconds: number,
    stopCurrent?: boolean
  ) => {
    this.resetInertia(camera);

    playAnimation(
      {
        target: camera,
        property: [
          {
            targetProperty: 'radius',
            keyFrame: {
              duration: seconds,
              from: camera.radius,
              to: radius,
            },
          },
          {
            targetProperty: 'target',
            keyFrame: {
              duration: seconds,
              from: camera.target,
              to: target,
            },
          },
        ],
        easingFunction: this.getEasingFunction(),
      },
      this.scene,
      stopCurrent
    );
  };

  public viewportAnimation = (
    camera: TargetCamera,
    startViewport: Viewport,
    endViewport: Viewport,
    second: number,
    onEnd?: () => void
  ) => {
    playAnimation(
      {
        target: camera.viewport,
        property: [
          {
            targetProperty: 'x',
            keyFrame: {
              duration: second,
              from: startViewport.x,
              to: endViewport.x,
            },
          },
          {
            targetProperty: 'y',
            keyFrame: {
              duration: second,
              from: startViewport.y,
              to: endViewport.y,
            },
          },
          {
            targetProperty: 'width',
            keyFrame: {
              duration: second,
              from: startViewport.width,
              to: endViewport.width,
            },
          },
          {
            targetProperty: 'height',
            keyFrame: {
              duration: second,
              from: startViewport.height,
              to: endViewport.height,
            },
          },
        ],
        onAnimationEnd: onEnd,
      },
      this.scene
    );
  };

  public static async toOtherCameraState(
    camera: TargetCamera,
    destination: TargetCamera,
    scene: Scene,
    duration: number
  ) {
    const endFrame = timeToFrame(duration);

    const animPos = generateAnimation('position', Animation.ANIMATIONTYPE_VECTOR3);
    animPos.setKeys([
      { frame: 0, value: camera.position },
      { frame: endFrame, value: destination.position },
    ]);
    camera.animations.push(animPos);

    const animFov = generateAnimation('fov', Animation.ANIMATIONTYPE_FLOAT);
    animFov.setKeys([
      { frame: 0, value: camera.fov },
      { frame: endFrame, value: destination.fov },
    ]);
    camera.animations.push(animFov);

    const animRot = generateAnimation('rotation', Animation.ANIMATIONTYPE_VECTOR3);
    const rot1 = camera.rotation;
    const rot2 = destination.rotationQuaternion.toEulerAngles();
    if (rot2.y < 0) rot2.y += 360 * toRadian;
    animRot.setKeys([
      { frame: 0, value: rot1 },
      { frame: endFrame, value: rot2 },
    ]);
    camera.animations.push(animRot);

    const animatable = scene.beginAnimation(camera, 0, timeToFrame(duration), false);

    await animatable.waitAsync();
  }

  private resetInertia = (camera: ArcRotateCamera) => {
    camera.inertialAlphaOffset = 0;
    camera.inertialBetaOffset = 0;
    camera.inertialPanningX = 0;
    camera.inertialPanningY = 0;
  };

  private easingFunction: EasingFunction;

  private getEasingFunction = (): EasingFunction => {
    return this.easingFunction;
  };

  public setEasingFunction = (functionType: EasingFunctionType, mode: EasingModeType): void => {
    this.easingFunction = createEasingFunction(functionType, mode);
  };
}
