import {
  DeviceType,
  GizmoManager,
  PointerDragBehavior,
  PointerInput,
  TransformNode,
  Vector3,
} from '@babylonjs/core';

import { type StateByThingType, type VideoPlayerIO } from '@/components';
import { addModel } from '@/data';
import { RenderManager } from '@/manager';
import { HallwaySystem, ThingStateSystem, TransformSystem, VideoPlayerSystem } from '@/system';
import { PointerDragSystem } from '@/system/PointerDragSystem';
import { convertUnityCoordsToBabylon } from '@/util/Utile';
import { World } from '@/World';

import type { WebDeviceInputSystem } from '@babylonjs/core/DeviceInput/webDeviceInputSystem';

export class FurnitureManager {
  private gizmoManager: GizmoManager;
  constructor(gizmoManager: GizmoManager) {
    this.gizmoManager = gizmoManager;
  }

  //-------------------------------------------------
  // * Control ThingState Entities
  public getThingStateEntities() {
    return ThingStateSystem.getThingStateEntities();
  }
  public getThingStateEntitiesInFloorplan(floorId: string) {
    return ThingStateSystem.getThingStateEntitiesInFloorplan(floorId);
  }
  public updateThingState(entityId: string, state: StateByThingType) {
    ThingStateSystem.updateThingState(entityId, state);
  }

  //-------------------------------------------------
  // * Control Hidable Entities
  public setHallwayVisibility(visibility: number): void {
    HallwaySystem.setHallwayVisibility(visibility);
  }

  //-------------------------------------------------
  // * Control MovableTransform Entities
  public getMovableTransformEntitiesInFloorplan(floorplanId: string) {
    return TransformSystem.getMovableTransformEntitiesInFloorplan(floorplanId);
  }
  public updateTransform(entityId: string, position?: Vector3, rotationY?: number) {
    TransformSystem.updateTransform(entityId, position, rotationY);
  }
  public setTransfromGizmo(transformNode?: TransformNode) {
    if (!!transformNode) {
      this.gizmoManager.attachableNodes = [transformNode];
      this.gizmoManager.attachToNode(transformNode);
    } else {
      this.gizmoManager.attachableNodes = null;
      this.gizmoManager.attachToNode(null);
    }
    RenderManager.RenderUpdate();
  }
  public setEnableDraggingMovable(enable: boolean) {
    PointerDragSystem.setEnableDraggingMovable(enable);
  }

  public pickup(
    rootNode: TransformNode,
    /** touch  pointerId */
    pointerId?: number
  ) {
    // Impose pointer down
    const engine = rootNode.getEngine();
    const scene = rootNode.getScene();
    const webDeviceInputSystem = engine._deviceSourceManager
      ?._deviceInputSystem as WebDeviceInputSystem;
    const deviceType = pointerId === undefined ? DeviceType.Mouse : DeviceType.Touch;
    const deviceSlot = deviceType === DeviceType.Mouse ? 0 : 0;
    if (deviceType === DeviceType.Touch) {
      webDeviceInputSystem['_activeTouchIds'][deviceSlot] = pointerId;
    }
    if (!webDeviceInputSystem['_inputs'][deviceType]) {
      webDeviceInputSystem['_inputs'][deviceType] = {};
    }
    if (!webDeviceInputSystem['_inputs'][deviceType][deviceSlot]) {
      webDeviceInputSystem['_addPointerDevice'](deviceType, deviceSlot, 0, 0);
    }
    const pointer = webDeviceInputSystem['_inputs'][deviceType][deviceSlot];
    pointer[PointerInput.LeftClick] = 1;
    scene._inputManager['_pointerCaptures'][1] = true;
    scene._inputManager['_totalPointersPressed']++;

    // TODO:    
    const pointerDragBehavior = rootNode.getBehaviorByName('PointerDrag') as PointerDragBehavior;
    const dragDeltaRatio = pointerDragBehavior.dragDeltaRatio;
    pointerDragBehavior.onDragStartObservable.addOnce(() => {
      pointerDragBehavior.dragDeltaRatio = 1;
      rootNode.position = convertUnityCoordsToBabylon(pointerDragBehavior.lastDragPosition);
    });
    pointerDragBehavior.onDragEndObservable.addOnce(() => {
      pointerDragBehavior.dragDeltaRatio = dragDeltaRatio;
    });
    pointerDragBehavior.startDrag(1);
  }

  /**
   *   
   * @remark    
   *
   * @todo   
   *
   * @version unreleased
   * @param param0
   * @returns  entity
   */
  public async addFurniture(
    homeId: string,
    floorplanId: string,
    furnitureName: string,
    position?: Vector3,
    rotationY?: number,
    scale?: Vector3
  ) {
    const model = await addModel(homeId, floorplanId, furnitureName, position, rotationY, scale);

    return model ? model.transformNodes[0] : undefined;
  }

  //-------------------------------------------------
  // * Control VideoPlayer Entities
  public getVideoPlayers(): VideoPlayerIO[] {
    return VideoPlayerSystem.getVideoPlayers();
  }
}
