import {
  AbstractMesh,
  Color4,
  EngineStore,
  Epsilon,
  Matrix,
  Mesh,
  MeshBuilder,
  Octree,
  Quaternion,
  Scene,
  TmpColors,
  TmpVectors,
  TransformNode,
  UtilityLayerRenderer,
  Vector3,
  VertexBuffer,
} from '@babylonjs/core';

import { getClosestPointOnTriangle } from '@/util/Utile';
import { clearArchetype, World } from '@/World';

import type { MagnetismFixture } from '@/components/Magnestism';

const defaultOptions = {
  defaultMaxDistance: 0.01,
  isDebug: false,
};

export type MagenetismSystemOptions = typeof defaultOptions;

/**
 *  Fixture  
 *   FixtureValid 
 */
export class MagnetismSystem {
  private withMagnetism = World.with('ActiveHome', 'TransformNode', 'MagnetismFixtures');

  private magnetismEnabled = this.withMagnetism.with('TransformDirty');

  private magnetismDone = this.withMagnetism.with('MagnetismValid').without('PointerDragging');

  private magnetismTarget = World.with('ActiveHome', 'Structure', 'MagnetismTarget');

  private readonly options: MagenetismSystemOptions;

  constructor(options?: Partial<MagenetismSystemOptions>) {
    this.options = {
      ...defaultOptions,
      ...options,
    };
  }

  dispose() {
    clearArchetype(this.withMagnetism);
    clearArchetype(this.magnetismEnabled);
    clearArchetype(this.magnetismDone);
    clearArchetype(this.magnetismTarget);
  }

  public beforeRenderExecute = (scene: Scene) => {
    this.calculateMagnetism();
    this.finishMagnetism();
  };

  /**
   *    FixtureValid 
   *
   * @remarks
   *
   *  Transform   
   */
  private calculateMagnetism = () => {
    if (this.magnetismEnabled.size < 1) return;

    const scene = EngineStore.LastCreatedScene!;
    const octree = scene.createOrUpdateSelectionOctree();

    for (const entity of this.magnetismEnabled) {
      const { MagnetismFixtures: Fixtures, TransformNode } = entity;
      const transform = TransformNode;
      const transformMatrix = transform.getWorldMatrix();

      const currentTransform = transform.getWorldMatrix().clone();
      const candidateFixtures = [...Fixtures.fixtures];
      const validFixtures = [];

      while (candidateFixtures.length > 0) {
        const closestFixture = this.findClosestFixture(currentTransform, candidateFixtures);
        if (!closestFixture) break;
        candidateFixtures.splice(closestFixture.index, 1);
        validFixtures.push(closestFixture.fixture);

        const { fixture, facePosition, faceNormal } = closestFixture;

        const targetScale = TmpVectors.Vector3[0];
        const targetQuaternion = TmpVectors.Quaternion[0];
        const targetPosition = TmpVectors.Vector3[1];

        transformMatrix.decompose(targetScale, targetQuaternion, targetPosition);

        const targetRotation = targetQuaternion.toEulerAngles();

        Matrix.ComposeToRef(
          targetScale,
          Quaternion.RotationYawPitchRoll(
            fixture.normal.y != 0
              ? Math.PI + targetRotation.y
              : -Math.atan2(faceNormal.x, -faceNormal.z) -
                  Math.atan2(fixture.normal.x, -fixture.normal.z),
            Math.PI +
              Math.atan2(
                faceNormal.y,
                Math.sqrt(faceNormal.z * faceNormal.z + faceNormal.x * faceNormal.x)
              ) +
              Math.atan2(
                fixture.normal.y,
                Math.sqrt(fixture.normal.z * fixture.normal.z + fixture.normal.x * fixture.normal.x)
              ),
            0
          ),
          Vector3.ZeroReadOnly,
          currentTransform
        );

        currentTransform.setTranslation(
          facePosition.subtract(Vector3.TransformCoordinates(fixture.position, currentTransform))
        );

        if (this.options.isDebug) {
          this.drawDebug(fixture, transformMatrix, transform, facePosition, faceNormal);
        }
      }

      if (Fixtures.fixtures.length === candidateFixtures.length) {
        World.removeComponent(entity, 'MagnetismValid');
      } else {
        World.update(entity, 'MagnetismValid', {
          fixtures: validFixtures,
          matrix: currentTransform,
        });
      }
    }
  };

  private findClosestFixture = (transformMatrix: Matrix, fixtures: MagnetismFixture[]) => {
    const closestFixtures = fixtures
      .flatMap((fixture, i) => {
        return this.magnetismTarget.entities.flatMap((e) => {
          const meshes = e.Structure.meshes;
          return meshes.flatMap((c) => {
            // if (c.isDescendantOf(transform)) return [];
            if (!(c instanceof Mesh)) return [];

            const cMatrix = c.getWorldMatrix();
            const cInvertedMatrix = Matrix.Invert(cMatrix);
            const targetToCandidateMatrix = transformMatrix.multiply(cInvertedMatrix);
            const localFixturePosition = Vector3.TransformCoordinates(
              fixture.position,
              targetToCandidateMatrix
            );
            const normals = c.getVerticesData(VertexBuffer.NormalKind)!;
            const positions = c.getVerticesData(VertexBuffer.PositionKind)!;
            const indices = c.getIndices()!;

            // TODO: only check facets of neighboring blocks @see AbstractMesh.getFacetsAtLocalCoordinates
            const closestFaces = [...Array((indices.length / 3) | 0)]
              .flatMap((_, faceIndex) => {
                var i0 = indices[3 * faceIndex] * 3;
                var i1 = indices[3 * faceIndex + 1] * 3;
                var i2 = indices[3 * faceIndex + 2] * 3;

                const verticeNormals = [
                  TmpVectors.Vector3[0].fromArray(normals, i0),
                  TmpVectors.Vector3[1].fromArray(normals, i1),
                  TmpVectors.Vector3[2].fromArray(normals, i2),
                ] as const;

                // `getFacetLocalNormals` ComputeNormals      
                // TODO: cache faceNormals
                const faceNormal = verticeNormals
                  .reduce((a, n) => {
                    return a.addInPlace(n);
                  }, TmpVectors.Vector3[3].setAll(0))
                  .scaleInPlace(1 / verticeNormals.length)
                  .normalize();

                const worldFaceNormal = Vector3.TransformNormal(faceNormal, cMatrix);

                const isConstraintValid =
                  fixture.constraints?.some((c) => {
                    switch (c.type) {
                      case 'padding':
                        return (
                          c.value.x + Epsilon >= Math.abs(worldFaceNormal.x) &&
                          c.value.y + Epsilon >= Math.abs(worldFaceNormal.y) &&
                          c.value.z + Epsilon >= Math.abs(worldFaceNormal.z)
                        );
                      case 'normal':
                        return worldFaceNormal.equalsWithEpsilon(c.value);
                    }
                  }) ?? true;

                if (!isConstraintValid) return [];

                const vertices = [
                  TmpVectors.Vector3[0].fromArray(positions, i0),
                  TmpVectors.Vector3[1].fromArray(positions, i1),
                  TmpVectors.Vector3[2].fromArray(positions, i2),
                ] as const;

                const localClosestPoint = getClosestPointOnTriangle(localFixturePosition, vertices);
                const distance = Vector3.DistanceSquared(localFixturePosition, localClosestPoint);

                if (distance > (fixture.maxDistance ?? this.options.defaultMaxDistance)) return [];

                return [
                  {
                    candidate: c,
                    faceIndex: faceIndex,
                    facePosition: Vector3.TransformCoordinates(localClosestPoint, cMatrix),
                    distance: distance,
                    faceNormal: worldFaceNormal,
                  },
                ];
              })
              .sort(({ distance: ad }, { distance: bd }) => ad - bd);

            const closestFace = closestFaces[0];

            if (!closestFace) return [];

            return [
              {
                index: i,
                fixture: fixture,
                ...closestFace,
              },
            ];
          });
        });
      })
      .sort(({ distance: ad }, { distance: bd }) => ad - bd);

    return closestFixtures[0];
  };

  private drawDebug = (
    fixture: MagnetismFixture,
    transformMatrix: Matrix,
    transform: TransformNode,
    facePosition: Vector3,
    faceNormal: Vector3
  ) => {
    TmpColors.Color4[0].set(0, 1, 1, 1);
    TmpColors.Color4[1].set(0, 1, 0, 1);
    TmpColors.Color4[2].set(1, 1, 0, 1);
    const normalColor = new Color4(1, 0, 1, 1);
    const fixturePosition = Vector3.TransformCoordinates(fixture.position, transformMatrix);
    const fixtureNormal = Vector3.TransformNormal(fixture.normal, transformMatrix);
    const debugLine = MeshBuilder.CreateLineSystem(
      `${transform.name}_debugLine`,
      {
        lines: [
          [fixturePosition, facePosition],
          [facePosition, facePosition.add(faceNormal.scale(0.1))],
          [fixturePosition, fixturePosition.add(fixtureNormal.scale(0.1))],
        ],
        colors: [
          [TmpColors.Color4[0], TmpColors.Color4[1]],
          [TmpColors.Color4[2], TmpColors.Color4[2]],
          [normalColor, normalColor],
        ],
      },
      UtilityLayerRenderer.DefaultKeepDepthUtilityLayer.utilityLayerScene
    );
    UtilityLayerRenderer.DefaultKeepDepthUtilityLayer.utilityLayerScene.onAfterRenderObservable.addOnce(
      () => {
        debugLine.dispose();
      }
    );
  };

  private finishMagnetism = () => {
    for (const entity of this.magnetismDone) {
      const { TransformNode: source, MagnetismValid } = entity;
      const matrix = source.parent
        ? MagnetismValid.matrix.multiply(Matrix.Invert(source.parent.getWorldMatrix()))
        : MagnetismValid.matrix;
      matrix.decomposeToTransformNode(source);
      World.removeComponent(entity, 'MagnetismValid');
    }
  };
}
