import {
  AbstractMesh,
  ArcRotateCamera,
  FreeCamera,
  Matrix,
  Scene,
  TargetCamera,
  Vector3,
} from '@babylonjs/core';

import { StructureType } from '@/components';
import { StructureSystem } from '@/system';
import { World } from '@/World';

export const isPanningPossible = (
  offsetX: number,
  offsetY: number,
  scene: Scene,
  camera: ArcRotateCamera
): number[] => {
  const ret = [offsetX, offsetY];

  const canvas = scene.getEngine().getRenderingCanvas();
  if (!canvas) return ret;

  const ext2d = getProjectedExtendsByCamera(scene, camera, canvas);

  const w = canvas.width;
  const h = canvas.height;
  const modelW = ext2d.max.x - ext2d.min.x;
  const modelH = ext2d.max.y - ext2d.min.y;
  const margin = 0.01;
  const marginW = margin * modelW;
  const marginH = margin * modelH;
  const modelLeft = ext2d.min.x;
  const modelRight = ext2d.max.x;
  const modelTop = ext2d.min.y;
  const modelBot = ext2d.max.y;

  //    :      (      )
  if (offsetX < 0) {
    if (modelRight + offsetX <= w && modelLeft + offsetX < -marginW) ret[0] = 0;
  }
  //   
  if (offsetX > 0) {
    if (modelLeft + offsetX >= 0 && modelRight + offsetX > w + marginW) ret[0] = 0;
  }
  //   
  if (offsetY < 0) {
    if (modelBot + offsetY <= h && modelTop + offsetY < -marginH) ret[1] = 0;
  }
  //   
  if (offsetY > 0) {
    if (modelTop + offsetY >= 0 && modelBot + offsetY > h + marginH) ret[1] = 0;
  }

  return ret;
};

const getProjectedExtendsByCamera = (
  scene: Scene,
  camera: ArcRotateCamera,
  canvas: HTMLCanvasElement
) => {
  const pts: Vector3[] = [];
  const ext = scene.getWorldExtends();
  for (let x of [ext.min.x, ext.max.x]) {
    for (let y of [ext.min.y, ext.max.y]) {
      for (let z of [ext.min.z, ext.max.z]) {
        let p = project(x, y, z, canvas.width, canvas.height);
        pts.push(p);
      }
    }
  }

  function project(x: number, y: number, z: number, w: number, h: number): Vector3 {
    return Vector3.Project(
      new Vector3(x, y, z),
      Matrix.Identity(),
      scene.getTransformMatrix(),
      camera.viewport.toGlobal(w, h)
    );
  }

  const ext2d = { min: pts[0].clone(), max: pts[0].clone() };
  for (let p of pts) {
    if (p.x < ext2d.min.x) ext2d.min.x = p.x;
    if (p.y < ext2d.min.y) ext2d.min.y = p.y;
    if (p.x > ext2d.max.x) ext2d.max.x = p.x;
    if (p.y > ext2d.max.y) ext2d.max.y = p.y;
  }

  return ext2d;
};

export const getFitRadius = (
  scene: Scene,
  cameraTargetExtends: { min: Vector3; max: Vector3 },
  camera: TargetCamera
): number | undefined => {
  const fitMargin = 1.1;

  // calc radius with box top -> add box height(ext.max.y) + margin(10%)
  const canvas = scene.getEngine().getRenderingCanvas();
  if (!canvas) return undefined;

  const ext = cameraTargetExtends;
  const halfW = (ext.max.x - ext.min.x) * 0.5;
  const halfH = (ext.max.z - ext.min.z) * 0.5;
  const fovH = camera.fov;
  const fovW = camera.fov * (canvas.width / canvas.height);
  const radiusByW = halfW * (1 / Math.tan(fovW * 0.5));
  const radiusByH = halfH * (1 / Math.tan(fovH * 0.5));

  const fitRadius = Math.max(radiusByW, radiusByH) + (ext.max.y - ext.min.y);
  return fitRadius * fitMargin;
};

export const toAngle = 180 / Math.PI;

export const toRadian = Math.PI / 180;

export const getWallMeshesAll = () => {
  return StructureSystem.getStructureMeshesByType(StructureType.Wall);
};

export const getDirectionToCenter = (pos: Vector3, meshes: AbstractMesh[]) => {
  for (const mesh of meshes) {
    const bounding = mesh.getBoundingInfo();
    const newPos = new Vector3(pos.x, 1, pos.z);
    const isIn = bounding.intersectsPoint(newPos);
    if (isIn) {
      const box = bounding.boundingBox;
      const cen = box.minimumWorld.add(box.maximumWorld).scale(0.5);
      cen.y = 1;
      return newPos.subtract(cen).normalize().scale(-1);
    }
  }
  return new Vector3(1, 0, 0);
};

export const timeout = (ms: number) => {
  return new Promise((resolve) => setTimeout(resolve, ms));
};

let freeCamera : FreeCamera | undefined = undefined;
let arcCamera : ArcRotateCamera | undefined = undefined;

export const arcToFree = (
  scene: Scene,
  target: Vector3,
  alpha: number,
  beta: number,
  radius: number
) => {
  if (!freeCamera) freeCamera = new FreeCamera('covFree', Vector3.Zero(), scene);
  if (!arcCamera) arcCamera = new ArcRotateCamera('convArc', 0, 0, 1, Vector3.Zero(), scene);

  arcCamera.target = target;
  arcCamera.alpha = alpha === 0 ? 0.1 * toRadian : alpha;
  arcCamera.beta = beta;
  arcCamera.radius = radius;
  arcCamera.getWorldMatrix();

  freeCamera.position = arcCamera.position;
  freeCamera.upVector = arcCamera.upVector;
  freeCamera.setTarget(target);
  freeCamera.getWorldMatrix();

  return { pos: freeCamera.position, rot: freeCamera.rotation };
};

export const freeToArc = (scene: Scene, position: Vector3, rotation: Vector3, dist: number) => {
  if (!freeCamera) freeCamera = new FreeCamera('covFree', Vector3.Zero(), scene);
  if (!arcCamera) arcCamera = new ArcRotateCamera('convArc', 0, 0, 1, Vector3.Zero(), scene);

  freeCamera.position = position;
  freeCamera.rotation = rotation;
  freeCamera.getWorldMatrix();
  const dir = freeCamera.target.subtract(freeCamera.position).normalize();
  const newTarget = freeCamera.position.add(dir.scale(dist));

  arcCamera.target = newTarget;
  arcCamera.setPosition(position);
  arcCamera.getWorldMatrix();
  return {
    target: arcCamera.target,
    alpha: arcCamera.alpha,
    beta: arcCamera.beta,
    radius: arcCamera.radius,
  };
};
