import {
  ArcRotateCamera,
  DefaultRenderingPipeline,
  Engine,
  GizmoManager,
  Scene,
} from '@babylonjs/core';

import { Resolution } from '@/data';

export class RenderManager {
  /**   */
  private static singletonInstance: RenderManager | undefined;
  public static get instance(): RenderManager | undefined {
    return this.singletonInstance;
  }
  public static RenderUpdate = (onceUpdate: boolean = true) => {
    if (onceUpdate) {
      RenderManager.instance?.runRenderLoop(true);
    } else {
      RenderManager.instance!.forceRender = true;
    }
  };
  public static RenderStop = () => {
    RenderManager.instance!.forceRender = false;
  };
  public static SetResolution = (resolution: Resolution) => {
    RenderManager.instance!.setResolution(resolution);
  };

  private scene: Scene;
  private engine: Engine;
  private gizmoManager: GizmoManager;
  private resizeDelay = -1;

  public playingVideoCount = 0;
  public forceRender: boolean = false;

  constructor(
    scene: Scene,
    engine: Engine,
    gizmoManager: GizmoManager,
    canvasOrContext?: HTMLCanvasElement
  ) {
    RenderManager.singletonInstance = this;

    this.engine = engine;
    this.scene = scene;
    this.gizmoManager = gizmoManager;
    if (canvasOrContext) {
      new ResizeObserver((entries) => {
        engine.resize();
        this.resizeDelay = 5;
      }).observe(canvasOrContext);
    }

    this.startManualRenderLoop();
  }

  private getCamera = (): ArcRotateCamera => {
    return this.scene.activeCamera as ArcRotateCamera;
  };

  reset = () => {};

  private highPerfomance = true;
  private maxFps = 60;
  private defaultLimitRenderFps = 30;
  private prevforceRender = false;
  private limitRenderFps = 0;
  startManualRenderLoop = () => {
    const scene = this.scene;
    var then = Date.now();
    if (!!scene) {
      scene.getEngine().runRenderLoop(() => {
        //--------------------
        // loop Logic
        // ...
        //--------------------
        // fps check

        //--------------------
        // rendering Logic
        //--------------------
        const isGizmo = !!this.gizmoManager.attachableNodes;
        const isAnimating = scene.animatables.length > 0;
        const isPlayingVideo = this.playingVideoCount > 0;
        //        
        const forceRender = isGizmo || isAnimating || isPlayingVideo || this.forceRender;
        //       
        const isRemainInertia = this.isRemainInertia();
        //         1      
        const finishedForceRender = this.prevforceRender && !forceRender;
        let fps =
          forceRender || isRemainInertia || finishedForceRender || this.resizeDelay-- === 0
            ? this.getTargetFPS()
            : this.limitRenderFps;

        if (this.initResolution && this.forceRenderResolution) {
          if (forceRender) {
            this.applyResolution(this.forceRenderResolution);
          } else {
            this.applyResolution(this.initResolution);
          }
        }
        this.prevforceRender = forceRender;

        if (fps <= 0) return;

        const now = Date.now();
        const delta = now - then;

        fps = fps || 60;
        const interval = 1000 / fps;

        if (delta > interval) {
          then = now - (delta % interval);
          scene.render();
        }
      });
    }
  };

  renderRequestCount = 0;
  runRenderLoop = (justOnceUpdate: boolean = false) => {
    ++this.renderRequestCount;
    if (justOnceUpdate) {
      this.limitRenderFps = this.getTargetFPS();
      this.scene.onAfterRenderObservable.addOnce(this.stopRenderLoop);
    } else {
      this.limitRenderFps = this.getTargetFPS();
    }
  };

  stopRenderLoop = () => {
    --this.renderRequestCount;
    if (this.renderRequestCount <= 0) {
      this.renderRequestCount = 0;
      this.limitRenderFps = 0;
    }
  };

  enableHighPerfomance = (enable: boolean) => {
    this.highPerfomance = enable;
  };
  getTargetFPS = () => {
    return this.highPerfomance ? this.maxFps : this.defaultLimitRenderFps;
  };

  public isRemainInertia = (): boolean => {
    const camera = this.getCamera();
    return (
      camera.inertialAlphaOffset !== 0 ||
      camera.inertialBetaOffset !== 0 ||
      camera.inertialPanningX !== 0 ||
      camera.inertialPanningY !== 0 ||
      camera.inertialRadiusOffset !== 0
    );
  };

  private initResolution?: Resolution;
  private currentResolution?: Resolution;
  private forceRenderResolution?: Resolution;
  private maxWidth?: number;
  private maxHeight?: number;
  private hardwareScalingLevel?: number;
  public setResolution(resolution: Resolution) {
    if (this.initResolution === resolution) return;
    this.initResolution = resolution;

    const keys = Object.keys(Resolution).flatMap((key) => {
      if (!isNaN(Number(key)) || key == 'None') return [];
      else return key;
    });

    const index = keys.findIndex(
      (key) => key == Resolution[resolution.toString() as keyof typeof Resolution].toString()
    );
    if (index === -1) {
      this.forceRenderResolution = undefined;
    } else {
      this.forceRenderResolution =
        Resolution[keys[Math.min(index + 1, keys.length - 1)] as keyof typeof Resolution];
    }
    this.applyResolution(resolution);

    RenderManager.RenderUpdate();
  }

  private applyResolution(resolution: Resolution) {
    if (!this.hardwareScalingLevel) {
      this.hardwareScalingLevel = this.engine.getHardwareScalingLevel();
    }
    if (!this.maxWidth) {
      this.maxWidth = this.engine.getRenderWidth();
    }
    if (!this.maxHeight) {
      this.maxHeight = this.engine.getRenderHeight();
    }

    if (this.currentResolution === resolution) return;
    this.currentResolution = resolution;

    if (resolution == Resolution.MAX) {
      this.engine.setHardwareScalingLevel(this.hardwareScalingLevel);
    } else if (resolution == Resolution.NONE) {
      this.engine.setHardwareScalingLevel(1);
    } else {
      const scale = (this.maxWidth > this.maxHeight ? this.maxHeight : this.maxWidth) / resolution;

      this.engine.setHardwareScalingLevel(
        Math.max(scale * this.hardwareScalingLevel, this.hardwareScalingLevel)
      );
    }
    // console.log(`Resolution : ${this.engine.getRenderWidth()}x${this.engine.getRenderHeight()}`);
  }
}
