export interface ThingState {
  type: ThingType;
  state: StateByThingType;
}
export interface ThingStateDirty {}

export type StateByThingType =
  | LightState
  | AirConditionerState
  | BoilerState
  | DoorState
  | WindowState;

export enum ThingType {
  Light,
  AirConditioner,
  Boiler,
  Door,
  Window,
}
export interface LightState {
  isOn: boolean;
  /** min:0, max:1 */
  intensity?: number;
}
export interface AirConditionerState {
  isOn: boolean;
  targetTemp?: number;
  curTemp?: number;
}
export interface BoilerState {
  isOn: boolean;
  targetTemp?: number;
  curTemp?: number;
}
export interface DoorState {
  isOpen: boolean;
}
export interface WindowState {
  isOpen: boolean;
}
export const getDefaultThingState = (thingType: ThingType) => {
  switch (thingType) {
    case ThingType.Light:
      return { isOn: true, intensity: 1 } as LightState;
    case ThingType.AirConditioner:
      return { isOn: false } as AirConditionerState;
    case ThingType.Boiler:
      return { isOn: false } as BoilerState;
    case ThingType.Door:
      return { isOpen: false } as DoorState;
    case ThingType.Window:
      return { isOpen: false } as WindowState;
  }
};
