import { GUID } from '@babylonjs/core';
import { ArchetypeBucket, World } from 'miniplex';

import { type Entity } from '@/components';

const world = new World<Entity>();
export { world as World };

export const createEntity = (components?: Omit<Entity, 'entityId'>) => {
  const entity = world.add({
    entityId: GUID.RandomId(),
    ...components,
  });

  const transformNode = entity.TransformNode;
  if (transformNode) {
    // TODO: bindBabylon   
    transformNode.metadata = {
      ...transformNode.metadata,
      entity: entity,
    };
  }
  return entity;
};

export const clearArchetype = <E>(archetype: ArchetypeBucket<E>) => {
  archetype.clear();
  archetype.onEntityAdded.clear();
  archetype.onEntityRemoved.clear();
};
