import { AbstractMesh, InstancedMesh, Matrix, Mesh, Scene, TransformNode } from '@babylonjs/core';

import { RenderManager } from '@/manager';
import { clearArchetype, World } from '@/World';

const defaultOptions = {
  lerpTime: 0,
};

export type MagnetismGhostSystemOptions = typeof defaultOptions;

export class MagnetismGhostSystem {
  private toAddGhost = World.with('ActiveHome', 'TransformNode', 'MagnetismValid').without(
    'MagnetismGhost'
  );
  private toRemoveGhost = World.with('ActiveHome', 'TransformNode', 'MagnetismGhost').without(
    'MagnetismValid'
  );
  private activeGhost = World.with(
    'ActiveHome',
    'TransformNode',
    'MagnetismValid',
    'MagnetismGhost'
  );

  private static readonly prefix = 'MagnetismGhost_';

  private readonly options: MagnetismGhostSystemOptions;

  constructor(options?: Partial<MagnetismGhostSystemOptions>) {
    this.options = {
      ...defaultOptions,
      ...options,
    };
  }

  dispose() {
    clearArchetype(this.toAddGhost);
    clearArchetype(this.toRemoveGhost);
    clearArchetype(this.activeGhost);
  }

  public beforeRenderExecute = (scene: Scene) => {
    this.toAddGhost.entities.forEach(this.addGhost);
    this.toRemoveGhost.entities.forEach(this.removeGhost);
    this.activeGhost.entities.forEach(this.moveGhost);
  };

  private addGhost = (entity: (typeof this.toAddGhost.entities)[number]) => {
    const { TransformNode: source } = entity;
    const ghostRoot = source.clone(`${MagnetismGhostSystem.prefix}${source.name}`, source.parent);
    if (!ghostRoot) return;

    ghostRoot.freezeWorldMatrix(source.getWorldMatrix());

    source.getChildMeshes().forEach((sm) => {
      sm.visibility = 0;
    });

    World.addComponent(entity, 'MagnetismGhost', {
      rootNode: ghostRoot,
    });

    RenderManager.instance!.playingVideoCount++; // HACK
  };

  private removeGhost = (entity: (typeof this.toRemoveGhost.entities)[number]) => {
    const { TransformNode: source, MagnetismGhost } = entity;

    const root = MagnetismGhost.rootNode;
    root.dispose();

    source.getChildMeshes().forEach((n) => {
      n.visibility = 1;
    });

    World.removeComponent(entity, 'MagnetismGhost');

    RenderManager.instance!.playingVideoCount--; // HACK
  };

  private moveGhost = (entity: (typeof this.activeGhost.entities)[number]) => {
    const { MagnetismValid: FixtureValid, MagnetismGhost: FixtureGhost } = entity;

    const ghostRoot = FixtureGhost.rootNode;
    const targetMatrix =
      this.options.lerpTime == 0
        ? FixtureValid.matrix
        : Matrix.DecomposeLerp(
            ghostRoot.getWorldMatrix(),
            FixtureValid.matrix,
            this.options.lerpTime
          );
    ghostRoot.freezeWorldMatrix(targetMatrix);
  };
}
