import { Observable, PointerEventTypes, PointerInfo, Scene } from '@babylonjs/core';

import {
  ActiveHomeSystem,
  CameraTargetSystem,
  CeilingSystem,
  FloorplanSystem,
  GUIDisplaySystem,
  HallwaySystem,
  LightControllerSystem,
  MagnetismGhostSystem,
  MagnetismSystem,
  MaterialChangeSystem,
  OpenableSystem,
  PointerDragSystem,
  ReflectorSystem,
  RoomSystem,
  StructureSystem,
  ThingStateSystem,
  TransformSystem,
  TransparentSystem,
  VideoPlayerSystem,
} from '@/system';

export class SystemManager {
  private scene: Scene;
  public onBeforeRenderObservable: Observable<Scene> = new Observable<Scene>();
  public onAfterRenderObservable: Observable<Scene> = new Observable<Scene>();
  public onPointerUpObservable: Observable<PointerInfo> = new Observable<PointerInfo>();

  private transformSystem: TransformSystem;
  private guiUpdateSystem: GUIDisplaySystem;
  private transparentSystem: TransparentSystem;
  private videoPlayerSystem: VideoPlayerSystem;
  private reflectorSystem: ReflectorSystem;
  private ceilingSystem: CeilingSystem;
  private pointerDragSystem: PointerDragSystem;
  private magnetismSystem: MagnetismSystem;
  private magenetismGhostSystem: MagnetismGhostSystem;
  private activeHomeSystem: ActiveHomeSystem;
  private openableSystem: OpenableSystem;
  private lightControllerSystem: LightControllerSystem;
  private thingStateSystem: ThingStateSystem;
  private floorplanSystem: FloorplanSystem;
  private hallwaySystem: HallwaySystem;
  private materialChangeSystem: MaterialChangeSystem;
  private cameraTargetSystem: CameraTargetSystem;
  private structureSystem: StructureSystem;
  private roomSystem: RoomSystem;

  constructor(scene: Scene) {
    this.scene = scene;

    // * create systems
    this.transformSystem = new TransformSystem();
    this.guiUpdateSystem = new GUIDisplaySystem();
    this.transparentSystem = new TransparentSystem();
    this.videoPlayerSystem = new VideoPlayerSystem();
    this.reflectorSystem = new ReflectorSystem();
    this.ceilingSystem = new CeilingSystem();
    this.pointerDragSystem = new PointerDragSystem();
    this.magnetismSystem = new MagnetismSystem();
    this.magenetismGhostSystem = new MagnetismGhostSystem();
    this.activeHomeSystem = new ActiveHomeSystem();
    this.openableSystem = new OpenableSystem();
    this.lightControllerSystem = new LightControllerSystem();
    this.thingStateSystem = new ThingStateSystem();
    this.floorplanSystem = new FloorplanSystem();
    this.hallwaySystem = new HallwaySystem();
    this.materialChangeSystem = new MaterialChangeSystem();
    this.cameraTargetSystem = new CameraTargetSystem();
    this.structureSystem = new StructureSystem();
    this.roomSystem = new RoomSystem();

    //-----------------------------------------------
    // * add babylons event cycle
    this.scene.onBeforeRenderObservable.add(() => {
      this.onBeforeRenderObservable.notifyObservers(scene);
    });
    this.scene.onAfterRenderObservable.add(() => {
      this.onAfterRenderObservable.notifyObservers(scene);
    });
    this.scene.onPointerObservable.add((pointerInfo) => {
      this.onPointerUpObservable.notifyObservers(pointerInfo);
    }, PointerEventTypes.POINTERUP);

    //-----------------------------------------------
    // * onBeforeRenderObservable
    this.onBeforeRenderObservable.add((s) => {
      this.transformSystem.beforeRenderExecute(s);
      this.thingStateSystem.beforeRenderExecute(s);
      this.reflectorSystem.beforeRenderExecute(s);
      this.magnetismSystem.beforeRenderExecute(s);
      this.magenetismGhostSystem.beforeRenderExecute(s);
    });

    // * onAfterRenderObservable
    this.onAfterRenderObservable.add((s) => {
      this.guiUpdateSystem.afterRenderExecute(s);
      this.transparentSystem.afterRenderExecute(s);
      this.ceilingSystem.afterRenderExecute(s);
      this.thingStateSystem.afterRenderExecute(s);
      this.transformSystem.afterRenderExecute(s);
    });

    // * onPointerUpObservable
    this.onPointerUpObservable.add((pointerInfo) =>
      this.videoPlayerSystem.onPointerUp(pointerInfo)
    );
  }

  public reset() {
    // * onBeforeRenderObservable
    this.scene.onBeforeRenderObservable.clear();
    this.onBeforeRenderObservable.clear();

    // * onAfterRenderObservable
    this.scene.onAfterRenderObservable.clear();
    this.onAfterRenderObservable.clear();

    // * onPointerObservable
    this.scene.onPointerObservable.clear();
    this.onPointerUpObservable.clear();

    this.transformSystem.dispose();
    this.guiUpdateSystem.dispose();
    this.transparentSystem.dispose();
    this.videoPlayerSystem.dispose();
    this.reflectorSystem.dispose();
    this.ceilingSystem.dispose();
    this.pointerDragSystem.dispose();
    this.magnetismSystem.dispose();
    this.magenetismGhostSystem.dispose();
    this.activeHomeSystem.dispose();
    this.openableSystem.dispose();
    this.lightControllerSystem.dispose();
    this.thingStateSystem.dispose();
    this.floorplanSystem.dispose();
    this.hallwaySystem.dispose();
    this.materialChangeSystem.dispose();
    this.cameraTargetSystem.dispose();
    this.structureSystem.dispose();
    this.roomSystem.dispose();
  }
}
