import { AbstractMesh, Observable, PointerDragBehavior, Vector3 } from '@babylonjs/core';

import { RenderManager } from '@/manager';
import { clearArchetype, World } from '@/World';

import type { Entity } from '@/components';

export class PointerDragSystem {
  private static instance: PointerDragSystem | undefined;
  public static setEnableDraggingMovable(enable: boolean) {
    this.instance?.setEnableDraggingMovable(enable);
  }

  private draggingable = false;
  private targetArchetype = World.with('PointerDraggable', 'TransformNode', 'TransformMovable');
  private targetMovableArchetype = World.with('ActiveHome', 'TransformNode', 'TransformMovable');
  private behaviorMap = new Map<Entity, PointerDragBehavior>();

  constructor() {
    PointerDragSystem.instance = this;

    this.targetArchetype.onEntityAdded.add((e) => this.attachBehavior(e));
    this.targetArchetype.onEntityRemoved.add((e) => this.detachBehavior(e));
    this.targetMovableArchetype.onEntityAdded.add((e) => this.updatePointerDraggable(e));
  }

  dispose() {
    PointerDragSystem.instance = undefined;
    this.behaviorMap.clear();
    clearArchetype(this.targetArchetype);
    clearArchetype(this.targetMovableArchetype);
  }

  private attachBehavior = (entity: (typeof this.targetArchetype.entities)[number]) => {
    const { TransformNode: rootNode } = entity;

    const pointerDragBehavior = new PointerDragBehavior({ dragPlaneNormal: Vector3.UpReadOnly });
    pointerDragBehavior.dragDeltaRatio = 1;
    pointerDragBehavior.onDragStartObservable.add(() => {
      World.addComponent(entity, 'PointerDragging', true);
      RenderManager.RenderUpdate();
    });
    pointerDragBehavior.onDragObservable.add(() => {
      RenderManager.RenderUpdate();
    });
    pointerDragBehavior.onDragEndObservable.add(() => {
      World.removeComponent(entity, 'PointerDragging');
      RenderManager.RenderUpdate();
    });

    rootNode.addBehavior(pointerDragBehavior);

    this.behaviorMap.set(entity, pointerDragBehavior);
  };
  private detachBehavior = (entity: (typeof this.targetArchetype.entities)[number]) => {
    const { TransformNode: rootNode } = entity;
    const pointerDragBehavior = this.behaviorMap.get(entity);
    if (!pointerDragBehavior) return;
    rootNode.removeBehavior(pointerDragBehavior);
    this.behaviorMap.delete(entity);
  };

  public setEnableDraggingMovable(enable: boolean) {
    this.draggingable = enable;
    this.allUpdatePointerDraggable();
  }
  private allUpdatePointerDraggable = () => {
    this.targetMovableArchetype.entities.forEach((e) => this.updatePointerDraggable(e));
  };
  private updatePointerDraggable = (
    entity: (typeof this.targetMovableArchetype.entities)[number]
  ) => {
    if (this.draggingable) {
      if (!entity.PointerDraggable) World.addComponent(entity, 'PointerDraggable', true);
    } else {
      if (entity.PointerDraggable) World.removeComponent(entity, 'PointerDraggable');
    }
    entity.TransformNode.getChildMeshes().forEach((m) => (m.isPickable = this.draggingable));
  };
}
