export async function cacheJsonData(
  cacheName: string,
  fileName: string,
  data: Record<string, any>
) {
  if (typeof caches === 'undefined') return;

  const url = `${window.location.origin}/${fileName}`;
  const cache = await caches.open(cacheName);
  const jsonStr = JSON.stringify(data);
  const response = new Response(jsonStr, { headers: { 'content-type': 'application/json' } });
  await cache.put(url, response);

  console.log(`${cacheName} successfully saved`);
}

export async function getJsonDataFromCache<T>(
  cacheName: string,
  fileName: string
): Promise<T | undefined> {
  if (typeof caches === 'undefined') return;

  if (!(await caches.has(cacheName))) return undefined;

  const url = `${window.location.origin}/${fileName}`;
  const cache = await caches.open(cacheName);

  const response = await cache.match(url);
  if (response && response.headers.get('content-type')?.includes('application/json')) {
    const jsonStr = await response.text();
    console.log(`${cacheName} successfully loaded`);
    return JSON.parse(jsonStr) as T;
  }
  return undefined;
}

export async function deleteCache(cacheName: string) {
  if (typeof caches === 'undefined') return;

  caches
    .delete(cacheName)
    .then(() => {
      console.log(`${cacheName} successfully deleted`);
    })
    .catch((error) => {
      console.error(`${cacheName} deletion failed:`, error);
    });
}
