import { DepthOfFieldEffectBlurLevel, TonemappingOperator } from '@babylonjs/core';

export interface PostprocessSettings {
  useMSAA?: boolean;
  useFXAA?: boolean;
  useToneMapping?: boolean;
  toneMappingType?: TonemappingOperator;
  contrast?: number;
  expoure?: number;

  useColorGrading?: boolean;
  lutTexture?: string;
  lutIsBlob?: boolean;
  useColorCurve?: boolean;
  colorCurveSettings?: ColorCurveSettings;

  useBloom?: boolean;
  bloomKernel?: number;
  bloomScale?: number;
  bloomThreshold?: number;
  bloomWeight?: number;

  useDepthOfField?: boolean;
  blurLevel?: DepthOfFieldEffectBlurLevel; // 0 by default
  fStop?: number; // 1.4 by default
  focalLength?: number; // 50 by default, mm
  focusDistance?: number; // 2000 by default, mm
  lensSize?: number; // 50 by default

  useChromaticAberration?: boolean;
  aberrationAmount?: number;

  useVignette?: boolean;
  vignetteWeight?: number;

  useSharpen?: boolean;
  edgeAmount?: number;
  colorAmount?: number;

  useMotionBlur?: boolean;
  motionStrength?: number;
}

export interface ColorCurveSettings {
  globalDensity: number;
  globalExposure: number;
  globalHue: number;
  globalSaturation: number;
  highlightsDensity: number;
  highlightsExposure: number;
  highlightsHue: number;
  highlightsSaturation: number;
  midtonesDensity: number;
  midtonesExposure: number;
  midtonesHue: number;
  midtonesSaturation: number;
  shadowsDensity: number;
  shadowsExposure: number;
  shadowsHue: number;
  shadowsSaturation: number;
}

export let defaultPostprocessSettings: PostprocessSettings = {
  useMSAA: true,
  useFXAA: true,
  useToneMapping: true,
  toneMappingType: TonemappingOperator.Hable,
  contrast: 1.75,
  expoure: 1.35,
  useColorGrading: false,
  lutTexture: '',
  useColorCurve: false,
  colorCurveSettings: {
    globalDensity: 0, // 0 by default
    globalExposure: 0, // 0 by default
    globalHue: 30, // 30 by default
    globalSaturation: 0, // 0 by default
    highlightsDensity: 0, // 0 by default
    highlightsExposure: 0, // 0 by default
    highlightsHue: 30, // 30 by default
    highlightsSaturation: 0, // 0 by default
    midtonesDensity: 0, // 0 by default
    midtonesExposure: 0, // 0 by default
    midtonesHue: 30, // 30 by default
    midtonesSaturation: 0, // 0 by default
    shadowsDensity: 0, // 0 by default
    shadowsExposure: 0, // 0 by default
    shadowsHue: 30, // 30 by default
    shadowsSaturation: 0, // 0 by default;
  },

  useBloom: false,
  bloomKernel: 64, // 64 by default
  bloomScale: 0.5, // 0.5 by default
  bloomThreshold: 0.9, // 0.9 by default
  bloomWeight: 0.15, // 0.15 by default

  useDepthOfField: false,
  blurLevel: DepthOfFieldEffectBlurLevel.Low, // 0 by default
  fStop: 1.4, // 1.4 by default
  focalLength: 50, // 50 by default, mm
  focusDistance: 2000, // 2000 by default, mm
  lensSize: 50, // 50 by default

  useVignette: true,
  vignetteWeight: 1.05,

  useChromaticAberration: false,
  aberrationAmount: 10,

  useSharpen: false,
  edgeAmount: 0.3,
  colorAmount: 1,

  useMotionBlur: false,
  motionStrength: 1,
};

export const setDefalutPostprocessSettings = (settings: PostprocessSettings) => {
  defaultPostprocessSettings = settings;
};
