import { Scalar, Vector3 } from '@babylonjs/core';

/**
 * Unity Babylon   Unity  Vector  Babylon   Vector   .
 * @param vector Unity Vector
 * @returns Babylon Vector
 */
export const convertUnityCoordsToBabylon = (vector: Vector3) => {
  return new Vector3(-vector.x, vector.y, vector.z);
};
export const convertUnityRotationYToBabylon = (value: number) => {
  return -value;
};
export const convertUnityScaleToBabylon = (vector: Vector3) => {
  return new Vector3(vector.x, vector.y, vector.z);
};

export const toRadian = (angle: number) => {
  return angle * (Math.PI / 180);
};

/**
 *  src  JSON   Type  .
 * @param src Load JSON  
 * @returns   Type
 */
export const loadJson = async <T>(src: string) => {
  const metadata = (await new Promise((resolve, reject) => {
    const xhr = new XMLHttpRequest();
    xhr.open('GET', src);
    xhr.onload = (evt) => {
      resolve(JSON.parse(xhr.responseText));
    };
    xhr.onerror = (evt) => {
      reject(evt);
    };
    xhr.send();
  })) as T;

  return metadata;
};

/**
 * @see https://www.gamedev.net/forums/topic/552906-closest-point-on-triangle/
 */
export function getClosestPointOnTriangle(
  coordinate: Vector3,
  triangle: readonly [Vector3, Vector3, Vector3]
) {
  const edge0 = triangle[1].subtract(triangle[0]);
  const edge1 = triangle[2].subtract(triangle[0]);
  const v0 = triangle[0].subtract(coordinate);

  const a = Vector3.Dot(edge0, edge0);
  const b = Vector3.Dot(edge0, edge1);
  const c = Vector3.Dot(edge1, edge1);
  const d = Vector3.Dot(edge0, v0);
  const e = Vector3.Dot(edge1, v0);

  const det = a * c - b * b;
  let s = b * e - c * d;
  let t = b * d - a * e;

  if (s + t < det) {
    if (s < 0.0) {
      if (t < 0.0) {
        if (d < 0.0) {
          s = Scalar.Clamp(-d / a, 0.0, 1.0);
          t = 0.0;
        } else {
          s = 0.0;
          t = Scalar.Clamp(-e / c, 0.0, 1.0);
        }
      } else {
        s = 0.0;
        t = Scalar.Clamp(-e / c, 0.0, 1.0);
      }
    } else if (t < 0.0) {
      s = Scalar.Clamp(-d / a, 0.0, 1.0);
      t = 0.0;
    } else {
      let invDet = 1.0 / det;
      s *= invDet;
      t *= invDet;
    }
  } else {
    if (s < 0.0) {
      let tmp0 = b + d;
      let tmp1 = c + e;
      if (tmp1 > tmp0) {
        const numerator = tmp1 - tmp0;
        const denominator = a - 2 * b + c;
        s = Scalar.Clamp(numerator / denominator, 0.0, 1.0);
        t = 1 - s;
      } else {
        t = Scalar.Clamp(-e / c, 0.0, 1.0);
        s = 0.0;
      }
    } else if (t < 0.0) {
      if (a + d > b + e) {
        const numerator = c + e - b - d;
        const denominator = a - 2 * b + c;
        s = Scalar.Clamp(numerator / denominator, 0.0, 1.0);
        t = 1 - s;
      } else {
        s = Scalar.Clamp(-e / c, 0.0, 1.0);
        t = 0.0;
      }
    } else {
      const numerator = c + e - b - d;
      const denominator = a - 2 * b + c;
      s = Scalar.Clamp(numerator / denominator, 0.0, 1.0);
      t = 1 - s;
    }
  }

  return triangle[0].add(edge0.scale(s)).add(edge1.scale(t));
}
