import { Engine, Scene, Vector3, Viewport } from '@babylonjs/core';

import {
  type AlignOption,
  AnimationOption,
  CameraAngle,
  CameraPosition,
  CameraViewport,
} from '@/data/CameraType';
import { CameraTargetSystem, FloorplanSystem } from '@/system';

import type { ZEDCameraBase } from '@/ZEDCamera';

export class CameraManager {
  private readonly DEFAULT_ANIMATION_OPTION = new AnimationOption();
  private readonly DEFAULT_CAMERA_ANGLE = new CameraAngle();

  constructor(private engine: Engine, private scene: Scene, private zedCamera: ZEDCameraBase) {
    this.engine = engine;
    this.scene = scene;
    this.zedCamera = zedCamera;
  }

  public async moveTo(param: {
    position: CameraPosition;
    cameraAngle?: CameraAngle;
    isRelativeAngle?: boolean;
    animationOption?: AnimationOption;
  }): Promise<void> {
    if (param.cameraAngle === undefined) {
      param.cameraAngle = this.DEFAULT_CAMERA_ANGLE;
    }
    if (param.isRelativeAngle === undefined) {
      param.isRelativeAngle = false;
    }
    if (param.animationOption === undefined) {
      param.animationOption = this.DEFAULT_ANIMATION_OPTION;
    }

    const option = param.animationOption;
    this.zedCamera.setEasingFunction(option.easingFunction, option.easingMode);
    await this.zedCamera.moveTo(
      param.position,
      param.cameraAngle,
      param.isRelativeAngle,
      option.seconds
    );
  }

  public getCameraTargetEntities() {
    return CameraTargetSystem.getCameraTargetEntities();
  }
  public getCameraTargetEntityWithSameName(targetName: string) {
    return CameraTargetSystem.getCameraTargetEntityWithSameName(targetName);
  }

  public async moveToCameraTarget(param: {
    targetName: string;
    cameraAngle?: CameraAngle;
    dist?: number;
    animationOption?: AnimationOption;
    fromForward?: boolean;
  }): Promise<void> {
    const entity = this.getCameraTargetEntityWithSameName(param.targetName);
    if (!entity) return;

    if (param.cameraAngle === undefined) {
      param.cameraAngle = this.DEFAULT_CAMERA_ANGLE;
    }
    if (param.dist === undefined) {
      param.dist = 2;
    }
    if (param.animationOption === undefined) {
      param.animationOption = this.DEFAULT_ANIMATION_OPTION;
    }
    if (param.fromForward === undefined) {
      param.fromForward = true;
    }

    const transform = entity.TransformNode;
    const option = param.animationOption;
    this.zedCamera.setEasingFunction(option.easingFunction, option.easingMode);
    await this.zedCamera.moveToCameraTarget(
      transform.absolutePosition,
      transform.forward,
      param.cameraAngle,
      param.dist,
      option.seconds,
      param.fromForward
    );
  }

  public async moveToCoordinate(param: {
    position: Vector3;
    rotation: Vector3;
    animationOption?: AnimationOption;
  }): Promise<void> {
    if (param.animationOption === undefined) {
      param.animationOption = this.DEFAULT_ANIMATION_OPTION;
    }

    const option = param.animationOption;
    this.zedCamera.setEasingFunction(option.easingFunction, option.easingMode);
    await this.zedCamera.moveToCoordinate(param.position, param.rotation, option.seconds);
  }

  public quaterView(alphaAngle = 45, betaAngle = 45, animationOption?: AnimationOption): void {
    const option = animationOption ? animationOption : this.DEFAULT_ANIMATION_OPTION;
    this.zedCamera.setEasingFunction(option.easingFunction, option.easingMode);
    this.zedCamera.quaterView(alphaAngle, betaAngle, option.seconds);
  }

  public quaterViewFit(alphaAngle = 45, betaAngle = 45, animationOption?: AnimationOption): void {
    const option = animationOption ? animationOption : this.DEFAULT_ANIMATION_OPTION;
    this.zedCamera.setEasingFunction(option.easingFunction, option.easingMode);
    const ext = this.getExtendsOrSceneExtends();

    const width = this.engine.getRenderWidth();
    const height = this.engine.getRenderHeight();
    const ratio = width / height;
    const zoomPercent = ratio * 100 > 100 ? 100 : ratio * 100;

    this.zedCamera.quaterViewFit(alphaAngle, betaAngle, option.seconds, zoomPercent, ext);
  }

  public topViewFit(animationOption?: AnimationOption): void {
    const option = animationOption ? animationOption : this.DEFAULT_ANIMATION_OPTION;
    this.zedCamera.setEasingFunction(option.easingFunction, option.easingMode);
    const ext = this.getExtendsOrSceneExtends();
    this.zedCamera.topViewFit(option.seconds, ext);
  }

  public topViewCustom(param: {
    x?: number;
    y?: number;
    zoomPercent?: number;
    rotation?: number;
    animationOption?: AnimationOption;
  }): void {
    if (param.x === undefined) {
      param.x = 0;
    }
    if (param.y === undefined) {
      param.y = 0;
    }
    if (param.zoomPercent === undefined) {
      param.zoomPercent = 100;
    }
    if (param.rotation === undefined) {
      param.rotation = 0;
    }
    if (param.animationOption === undefined) {
      param.animationOption = this.DEFAULT_ANIMATION_OPTION;
    }
    const option = param.animationOption;
    this.zedCamera.setEasingFunction(option.easingFunction, option.easingMode);
    this.zedCamera.topViewCustom(
      param.x,
      param.y,
      param.zoomPercent,
      param.rotation,
      option.seconds,
      this.getExtendsOrSceneExtends()
    );
  }

  public zoomIn(param?: { percent?: number; animationOption?: AnimationOption }): void {
    if (param === undefined) {
      param = {};
    }
    if (param.percent === undefined) {
      param.percent = 30;
    }
    if (param.animationOption === undefined) {
      param.animationOption = this.DEFAULT_ANIMATION_OPTION;
    }
    const option = param.animationOption;

    this.zedCamera.setEasingFunction(option.easingFunction, option.easingMode);
    this.zedCamera.zoomIn(param.percent, option.seconds);
  }

  public zoomOut(param?: { percent?: number; animationOption?: AnimationOption }): void {
    if (param === undefined) {
      param = {};
    }
    if (param.percent === undefined) {
      param.percent = 30;
    }
    if (param.animationOption === undefined) {
      param.animationOption = this.DEFAULT_ANIMATION_OPTION;
    }
    const option = param.animationOption;

    this.zedCamera.setEasingFunction(option.easingFunction, option.easingMode);
    this.zedCamera.zoomOut(param.percent, option.seconds);
  }

  public setDistance(param: { distance: number; animationOption?: AnimationOption }): void {
    if (param.distance === undefined) {
      param.distance = 5;
    }
    if (param.animationOption === undefined) {
      param.animationOption = this.DEFAULT_ANIMATION_OPTION;
    }
    const option = param.animationOption;

    this.zedCamera.setEasingFunction(option.easingFunction, option.easingMode);
    this.zedCamera.setDistance(param.distance, option.seconds);
  }

  public zoomFitOnInit() {
    const ext = this.getExtendsOrSceneExtends();
    this.zedCamera.zoomFitOnInit(ext);
  }
  private getExtendsOrSceneExtends(): { min: Vector3; max: Vector3 } {
    //  Home  floor    extends  
    let ext: { min: Vector3; max: Vector3 } | undefined = undefined;
    const floorplanEntities = FloorplanSystem.getFloorplanEntities();
    if (floorplanEntities.length > 0) {
      ext = floorplanEntities[0].Floorplan.extends;
    }
    return ext ? ext : this.scene.getWorldExtends();
  }

  public setTargetFloor(floorId: string, toTopView = false, animationOption?: AnimationOption) {
    if (animationOption === undefined) {
      animationOption = this.DEFAULT_ANIMATION_OPTION;
    }
    this.zedCamera.setEasingFunction(animationOption.easingFunction, animationOption.easingMode);

    const targetEntity = FloorplanSystem.getFloorplanEntity(floorId);

    if (targetEntity) {
      const resetAngles = toTopView;
      this.zedCamera.initWithExtends(
        targetEntity.Floorplan.extends,
        resetAngles,
        animationOption.seconds
      );
    }
  }
  public setViewport(to: CameraViewport) {
    this.zedCamera.setViewport(to as Viewport);
    this.scene.render();
  }

  public transformViewport(from: CameraViewport, to: CameraViewport, second: number) {
    this.zedCamera.transformViewport(from as Viewport, to as Viewport, second);
  }

  public transformFromCurrentViewport(to: CameraViewport, second: number) {
    const from = this.zedCamera.getViewport();
    this.zedCamera.transformViewport(from as Viewport, to as Viewport, second);
  }

  public alignViewport(
    second: number,
    isLandscape: boolean,
    align: AlignOption,
    onEnd?: () => void
  ) {
    const width = this.engine.getRenderWidth();
    const height = this.engine.getRenderHeight();
    const ratio = width / height;
    const from = this.zedCamera.getViewport();
    let to = new CameraViewport(0, 0, isLandscape ? 0.7 : 1, 1);
    if (ratio > 1.0) {
      if (align.column === 'left') {
        to.width = ratio;
      } else if (align.column === 'right') {
        to.x = 1 - ratio;
        to.width = ratio;
      }
    }
    if (ratio < 1.0) {
      if (align.row === 'top' && ratio < 1.0) {
        to.y = 0.5 * (0.9 - ratio);
      } else if (align.row === 'bottom' && ratio < 1.0) {
        to.y = -0.5 * (1 - ratio);
      }
    }
    this.zedCamera.transformViewport(from as Viewport, to as Viewport, second, onEnd);
  }

  public enableControl(enable: boolean) {
    this.zedCamera.enableControl(enable);
  }

  public setCameraLimits(applyLimits: boolean) {
    this.zedCamera.setCameraLimits(applyLimits);
  }

  // min, max :  radius    ( )
  // undefined :    
  // null :   null   (  )
  public setMinMaxDistance(value: { min?: number | null; max?: number | null }) {
    this.zedCamera.setMinMaxDistance(value);
  }
}
// END of ZEDCamera class
