import { clearArchetype, World } from '@/World';

import type { StructureType } from '@/components';

export class StructureSystem {
  private static instance: StructureSystem | undefined;
  public static getStructureMeshesByType(type: StructureType) {
    return this.instance?.getStructureMeshesByType(type) ?? [];
  }

  private targetArchetype = World.with('ActiveHome', 'Identifier', 'Structure');

  constructor() {
    StructureSystem.instance = this;
  }
  dispose() {
    StructureSystem.instance = undefined;
    clearArchetype(this.targetArchetype);
  }

  private getStructureEntities() {
    return this.targetArchetype.entities ?? [];
  }
  private getStructureEntitiesByType(type: StructureType) {
    return this.getStructureEntities().filter((e) => e.Structure.structureType == type) ?? [];
  }
  private getStructureMeshesByType(type: StructureType) {
    return this.getStructureEntitiesByType(type).flatMap((e) => e.Structure.meshes) ?? [];
  }
}
