import {
  ArcRotateCamera,
  FreeCamera,
  FreeCameraMouseInput,
  FreeCameraTouchInput,
  type Nullable,
  PointerEventTypes,
  PointerInput,
  Scene,
  TargetCamera,
  Vector3,
  Viewport,
} from '@babylonjs/core';

import { CameraAngle, CameraControlType, CameraPosition } from '@/data';
import { RenderManager } from '@/manager';

import { ArcRotateCameraPointersInput } from './util/camera/ArcRotateCameraPointersInput';
import { CameraAnimationHelper } from './util/camera/CameraAnimationHelper';
import {
  arcToFree,
  freeToArc,
  getFitRadius,
  isPanningPossible,
  toAngle,
  toRadian,
} from './util/camera/CameraUtils';
import { DeviceOrientationCamera } from './util/camera/deviceOrientationCamera';
import { FreeCameraDeviceOrientationInput } from './util/camera/freeCameraDeviceOrientationInput';
import { EasingFunctionType, EasingModeType } from './util/EasingFunctionType';

//  ZED    abstract 
//  ,  targetCamera   
export abstract class ZEDCameraBase {
  protected scene: Scene;
  protected animationHelper: CameraAnimationHelper;

  protected currentEasingFunctionType = EasingFunctionType.CircleEase;
  protected currentEasingMode = EasingModeType.Out;

  protected defaultTarget: Vector3 = Vector3.Zero();
  protected defaultRadius: number = 3;
  protected defaultAlpha: number = 90 * toRadian;
  protected defaultBeta: number = 0;
  protected defaultFov: number = 0.8;

  protected fitRadiusWithMargin = 12;

  protected ext: { min: Vector3; max: Vector3 } | undefined;

  public static minDistance: number | null = 1;
  public static maxDistance: number | null = null;

  constructor(scene: Scene) {
    this.scene = scene;
    this.animationHelper = new CameraAnimationHelper(this.scene);
  }

  public reset() {}

  public abstract babylonCamera(): TargetCamera;

  public copyFromCamera(zedcamera: ZEDCameraBase) {
    const refCam = zedcamera.babylonCamera();
    const thisCam = this.babylonCamera();

    refCam.getWorldMatrix();

    //   
    if (thisCam instanceof ArcRotateCamera) {
      // lower radius        
      // (   )
      const radius = refCam.getTarget().subtract(refCam.position).length();
      if (thisCam.lowerRadiusLimit && radius < thisCam.lowerRadiusLimit) {
        thisCam.lowerRadiusLimit = radius;
        ZEDCameraBase.minDistance = radius;
      }
    } else {
      // free camera  upVector  
      thisCam.upVector = refCam.upVector;
    }

    //   (fov, position, target)
    thisCam.fov = refCam.fov;
    thisCam.position = refCam.position;
    thisCam.setTarget(refCam.getTarget());

    thisCam.getWorldMatrix();
  }

  public setActive(): void {
    this.scene.activeCamera = this.babylonCamera();
  }

  public initWithExtends(ext: { min: Vector3; max: Vector3 }, resetAngles = true, seconds = 0.3) {
    this.ext = ext;

    this.defaultTarget.x = (ext.min.x + ext.max.x) * 0.5;
    this.defaultTarget.y = ext.min.y;
    this.defaultTarget.z = (ext.min.z + ext.max.z) * 0.5;

    const fitRadius = getFitRadius(this.scene, ext, this.babylonCamera());
    if (!fitRadius) return;

    const camera = this.babylonCamera();
    camera.getWorldMatrix();

    const current = freeToArc(
      this.scene,
      camera.position,
      camera.rotation,
      camera.target.subtract(camera.position).length()
    );

    const alpha = resetAngles ? this.defaultAlpha : current.alpha;
    const beta = resetAngles ? this.defaultBeta : current.beta;

    this.animationHelper.moveAnimationTargetCameraWithArcRotate(
      this.babylonCamera(),
      this.defaultTarget,
      alpha,
      beta,
      fitRadius,
      seconds
    );
  }

  public renderApply() {
    RenderManager.RenderUpdate();
  }

  public topFit(ext: { min: Vector3; max: Vector3 }) {
    const camera = this.babylonCamera();

    const currentDist = camera.position.subtract(this.defaultTarget).length();
    const { pos, rot } = arcToFree(
      this.scene,
      this.defaultTarget,
      this.defaultAlpha,
      this.defaultBeta,
      currentDist
    );
    camera.fov = this.defaultFov;
    camera.position = pos;
    camera.setTarget(this.defaultTarget);

    this.zoomFit(true, ext);
  }

  public setCameraLimits(applyLimits = true) {}

  public setMinMaxDistance(val: { min?: number | null; max?: number | null }) {}

  public isRemainInertia(): boolean {
    return false;
  }

  public quaterView(alphaAngle = 45, betaAngle = 45, seconds: number) {
    const alpha = alphaAngle * toRadian;
    const beta = betaAngle * toRadian;
    const r = this.fitRadiusWithMargin;
    this.animationHelper.moveAnimationTargetCameraWithArcRotate(
      this.babylonCamera(),
      this.defaultTarget,
      alpha,
      beta,
      r,
      seconds
    );
  }

  public quaterViewFit(
    alphaAngle = 45,
    betaAngle = 45,
    seconds: number,
    zoomPercent: number,
    ext: { min: Vector3; max: Vector3 }
  ) {
    const alpha = alphaAngle * toRadian;
    const beta = betaAngle * toRadian;
    const r = getFitRadius(this.scene, ext, this.babylonCamera());
    const ratio = 1 + (100 - zoomPercent) / 100;

    if (r === undefined) return;
    this.animationHelper.moveAnimationTargetCameraWithArcRotate(
      this.babylonCamera(),
      this.defaultTarget,
      alpha,
      beta,
      r * ratio,
      seconds
    );
  }

  public zoomIn(percent = 30, seconds = 0.3) {
    if (percent < 0) {
      return;
    }
    const camera = this.babylonCamera();
    camera.getWorldMatrix();
    const pos = camera.position.clone();
    const target = camera.target.clone();
    const dir = pos.subtract(target).normalize();

    const curDist = pos.subtract(target).length();
    const newDist = curDist * (1 - percent / 100);

    const newPos = target.add(dir.scale(newDist));

    this.animationHelper.positionAnimation(camera, newPos, seconds);
  }

  public zoomOut(percent = 30, seconds = 0.3) {
    if (percent < 0) {
      return;
    }

    const camera = this.babylonCamera();
    camera.getWorldMatrix();
    const pos = camera.position.clone();
    const target = camera.target.clone();
    const dir = pos.subtract(target).normalize();

    const curDist = pos.subtract(target).length();
    const newDist = curDist * (1 + percent / 100);

    const newPos = target.add(dir.scale(newDist));

    this.animationHelper.positionAnimation(camera, newPos, seconds);
  }

  public setDistance(distance = 3, seconds = 0.3) {
    const camera = this.babylonCamera();
    camera.getWorldMatrix();
    const pos = camera.position.clone();
    const target = camera.target.clone();
    const dir = pos.subtract(target).normalize();
    const newPos = target.add(dir.scale(distance));

    this.animationHelper.positionAnimation(camera, newPos, seconds);
  }

  public zoomFitOnInit(ext: { min: Vector3; max: Vector3 }) {}

  protected zoomFit(useAnimation = false, ext: { min: Vector3; max: Vector3 }) {
    const fitRadius = getFitRadius(this.scene, ext, this.babylonCamera());
    if (fitRadius === undefined) {
      return;
    }

    const camera = this.babylonCamera();
    camera.getWorldMatrix();
    camera.target = this.defaultTarget;

    const pos = camera.position.clone();
    const target = camera.target.clone();
    const dir = pos.subtract(target).normalize();
    const newPos = target.add(dir.scale(fitRadius));

    this.animationHelper.positionAnimation(camera, newPos, 0.4);

    if (useAnimation) {
      this.animationHelper.positionAnimation(camera, newPos, 0.4);
    } else {
      camera.position = newPos;
    }
  }

  public async moveTo(
    pos: CameraPosition,
    cameraAngle: CameraAngle,
    isRelativeAngle = false,
    seconds = 1
  ) {
    let startVec = new Vector3(0, 0, -1);
    if (isRelativeAngle) {
      startVec = this.babylonCamera().target.subtract(this.babylonCamera().position).normalize();
    }
    const [alpha, beta] = cameraAngle.getRotatedAlphaBetaAsRadianFrom(startVec);
    const vec = CameraAngle.convertToVectorFromAlphaBetaRadian(alpha, beta);
    const convertedPos = new Vector3(-pos.x, pos.h, -pos.y);
    this.babylonCamera().getWorldMatrix();
    let target = this.babylonCamera().target.clone();
    let newPos = this.babylonCamera().position.clone();
    if (pos.isRelative) {
      newPos = this.babylonCamera().position.add(convertedPos);
      target = newPos.subtract(vec.scale(-1));
    } else {
      target = convertedPos.add(vec);
      newPos = target.subtract(vec);
    }
    const newRadius = Vector3.Distance(newPos, target);

    await this.animationHelper.moveAnimationTargetCameraWithArcRotate(
      this.babylonCamera(),
      target,
      alpha,
      beta,
      1,
      seconds
    );
  }

  public async moveToCameraTarget(
    position: Vector3,
    forward: Vector3,
    cameraAngle: CameraAngle,
    dist: number,
    seconds = 1,
    fromForward = true
  ) {
    const startVec = fromForward ? forward.clone().normalize().scale(-1) : new Vector3(0, 0, -1);
    const [alpha, beta] = cameraAngle.getRotatedAlphaBetaAsRadianFrom(startVec);
    const target = position;

    await this.animationHelper.moveAnimationTargetCameraWithArcRotate(
      this.babylonCamera(),
      target,
      alpha,
      beta,
      dist,
      seconds
    );
  }

  public async moveToCoordinate(position: Vector3, rotation: Vector3, seconds = 1) {
    await this.animationHelper.moveAnimationTargetCamera(
      this.babylonCamera(),
      position,
      rotation,
      seconds
    );
  }

  public topViewFit(seconds = 0.3, ext: { min: Vector3; max: Vector3 }) {
    const target = this.defaultTarget;
    const radius = getFitRadius(this.scene, ext, this.babylonCamera());
    if (radius === undefined) {
      return;
    }
    this.animationHelper.moveAnimationTargetCameraWithArcRotate(
      this.babylonCamera(),
      target,
      this.defaultAlpha,
      this.defaultBeta,
      radius,
      seconds
    );
  }

  public topViewCustom(
    x: number,
    y: number,
    zoomPercent: number,
    rotation: number,
    seconds = 0.3,
    ext: { min: Vector3; max: Vector3 }
  ) {
    if (zoomPercent <= 0) {
      return;
    }
    const target = this.defaultTarget;
    const newTarget = new Vector3(target.x - x, target.y, target.z - y);
    const radius = getFitRadius(this.scene, ext, this.babylonCamera());
    if (radius === undefined) {
      return;
    }
    const ratio = 1 + (100 - zoomPercent) / 100;
    this.animationHelper.moveAnimationTargetCameraWithArcRotate(
      this.babylonCamera(),
      newTarget,
      this.defaultAlpha + rotation * toRadian,
      this.defaultBeta,
      radius * ratio,
      seconds
    );
  }

  public setEasingFunction(functionType: EasingFunctionType, mode: EasingModeType): void {
    if (functionType === this.currentEasingFunctionType && mode === this.currentEasingMode) {
      return;
    }
    this.currentEasingFunctionType = functionType;
    this.currentEasingMode = mode;
    this.animationHelper.setEasingFunction(functionType, mode);
  }

  public getCurrentEasingSetting(): [EasingFunctionType, EasingModeType] {
    return [this.currentEasingFunctionType, this.currentEasingMode];
  }

  public setViewport(to: Viewport): void {
    Object.assign(this.babylonCamera().viewport, to);
  }

  public getViewport(): Viewport {
    return this.babylonCamera().viewport;
  }

  public transformViewport(from: Viewport, to: Viewport, frame: number, onEnd?: () => void): void {
    this.animationHelper.viewportAnimation(this.babylonCamera(), from, to, frame, onEnd);
  }

  public enableControl(enable: boolean): void {
    this.babylonCamera().inputs.removeByType('FreeCameraMouseInput');

    if (enable) {
      this.babylonCamera().inputs.add(new FreeCameraMouseInput());
    }
  }

  public moveToPosition(pos: Nullable<Vector3> | undefined, dir?: Vector3): void {
    const camera = this.babylonCamera();
    if (pos) {
      camera.position = new Vector3(pos.x, 1.6, pos.z);
      if (dir) {
        camera.setTarget(camera.position.add(dir.normalize()));
      }
    }
  }
}

export class ZEDFreeCamera extends ZEDCameraBase {
  private camera: FreeCamera;

  constructor(scene: Scene) {
    super(scene);

    this.camera = new FreeCamera('free', Vector3.Zero(), this.scene);

    this.camera.fov = 1.2;
    this.camera.inertia = 0;
    this.camera.minZ = 0.1;
    this.camera.attachControl(undefined, true);
  }

  public override babylonCamera(): TargetCamera {
    return this.camera;
  }
}

export class ZEDGyroCamera extends ZEDCameraBase {
  private camera: DeviceOrientationCamera;

  private _customInput: FreeCameraDeviceOrientationInput;

  constructor(scene: Scene, pos?: Vector3, dir?: Vector3) {
    super(scene);

    const startPos = new Vector3(2.2, 1.6, -1);
    if (pos) {
      startPos.x = pos.x;
      startPos.z = pos.z;
    }
    this.camera = new DeviceOrientationCamera('gyro', startPos, this.scene);

    this.camera.inputs.removeByType('FreeCameraDeviceOrientationInput');
    this._customInput = new FreeCameraDeviceOrientationInput(dir);

    this.camera.addCustomInput(this._customInput);

    this.camera.fov = 1.2;
    this.camera.inertia = 0;
    this.camera.minZ = 0.1;
    this.camera.attachControl(undefined, true);
    this.camera.enableHorizontalDragging(-2 / 300);
  }

  public async getGyroAngles(): Promise<number[]> {
    return this._customInput.getGyroAngles();
  }

  public override babylonCamera(): TargetCamera {
    return this.camera;
  }

  public override moveToPosition(pos: Nullable<Vector3> | undefined, dir?: Vector3): void {
    if (pos) {
      this.camera.position = new Vector3(pos.x, 1.6, pos.z);
      if (dir) {
        this.camera.target = this.camera.position.add(dir.normalize());
      }
    }
  }
}

export class ZEDCamera extends ZEDCameraBase {
  private camera: ArcRotateCamera;

  constructor(scene: Scene) {
    super(scene);

    const createArcRotateCamera = true;
    const replace = true;
    const attachCameraControls = true;
    scene.createDefaultCamera(createArcRotateCamera, replace, attachCameraControls);
    this.camera = this.scene.activeCamera as ArcRotateCamera;

    this.initializeArcRotateCamera();
  }

  private initializeArcRotateCamera() {
    this.setDefault();

    this.setCameraLimits();

    // Camera Acceleration down (default : 0.9)
    this.camera.inertia = 0.3;
    this.camera.panningInertia = 0.3;

    const pointersInput = this.createPointersInput();

    // Pan - Rotate swap (Pan with one finger)
    this.camera.inputs.removeByType('ArcRotateCameraPointersInput');
    this.camera.inputs.add(pointersInput);

    this.applyCameraSpeed();

    // Z-fighting  near  (0.05  ,    )
    this.camera.minZ = 0.05;
    this.camera.onViewMatrixChangedObservable.add((camera, es) => {
      if (camera instanceof ArcRotateCamera) {
        camera.minZ = camera.radius * 0.005; // 10m  0.05 min  
        if (camera.radius > 20) camera.minZ += 0.1; //    
        camera.minZ = Math.max(camera.minZ, 0.05); //  0.05
      }
    });
  }

  public override babylonCamera(): TargetCamera {
    return this.camera;
  }

  private cameraTypeSetting = CameraControlType.AllEach;

  private defaultLowerBetaLimit = 2 * toRadian;
  private defaultUpperBetaLimit = 90 * toRadian;

  public override initWithExtends(
    ext: { min: Vector3; max: Vector3 },
    resetAngles = true,
    seconds = 0.3
  ) {
    this.ext = ext;

    this.defaultTarget.x = (ext.min.x + ext.max.x) * 0.5;
    this.defaultTarget.y = ext.min.y;
    this.defaultTarget.z = (ext.min.z + ext.max.z) * 0.5;

    const fitRadius = getFitRadius(this.scene, ext, this.camera);
    if (!fitRadius) return;

    const alpha = resetAngles ? this.defaultAlpha : this.camera.alpha;
    const beta = resetAngles ? this.defaultBeta : this.camera.beta;

    this.animationHelper.moveAnimation(
      this.camera,
      this.defaultTarget,
      alpha,
      beta,
      fitRadius,
      seconds
    );
  }

  private setSceneEvents() {
    // Add double-tap handler
    const scene = this.scene;
    const camera = this.camera;
    const fitRadiusWithMargin = this.fitRadiusWithMargin;
    const zoomInToPoint = this.zoomInToPoint;
    const restoreCameraWithRotationHold = this.restoreCameraWithRotationHold;
    const renderUpdate = () => RenderManager.RenderUpdate();
    scene.onPointerObservable.add((evt) => {
      switch (evt.type) {
        case PointerEventTypes.POINTERDOUBLETAP:
          if (evt.pickInfo?.pickedPoint) {
            const currentRadius = camera.radius;
            if (currentRadius >= fitRadiusWithMargin * 0.95) {
              zoomInToPoint(camera, evt.pickInfo?.pickedPoint);
            } else {
              restoreCameraWithRotationHold(camera);
            }
          }
          break;
        case PointerEventTypes.POINTERWHEEL:
          renderUpdate();
          break;
      }
    });
  }

  private setDefault() {
    this.camera.target = this.defaultTarget;
    this.camera.radius = this.defaultRadius;
    this.camera.alpha = this.defaultAlpha;
    this.camera.beta = this.defaultBeta;
    this.camera.fov = this.defaultFov;
  }

  public override topFit(ext: { min: Vector3; max: Vector3 }) {
    this.camera.target = this.defaultTarget;
    this.camera.alpha = this.defaultAlpha;
    this.camera.beta = this.defaultBeta;
    this.camera.fov = this.defaultFov;

    this.zoomFit(true, ext);
  }

  private setCameraType(val: number) {
    this.cameraTypeSetting = val;
  }

  private panSpeedAdjust = 2;
  private rotationSpeedAdjust = 2;
  private zoomSpeedAdjust = 0;

  private setCameraSpeedOption(panSpeed: number, rotationSpeed: number, zoomSpeed: number) {
    this.panSpeedAdjust = panSpeed;
    this.rotationSpeedAdjust = rotationSpeed;
    this.zoomSpeedAdjust = zoomSpeed;

    this.applyCameraSpeed();
  }

  private applyCameraSpeed() {
    // small is faster (default : 1000)
    this.camera.panningSensibility = 150 * Math.pow(0.7, this.panSpeedAdjust);
    // small is faster (default : 12)
    this.camera.pinchPrecision = 36 * Math.pow(0.7, this.zoomSpeedAdjust);
    // small is faster (default : 1000)
    this.camera.angularSensibilityX = 500 * Math.pow(0.7, this.rotationSpeedAdjust);
    this.camera.angularSensibilityY = 500 * Math.pow(0.7, this.rotationSpeedAdjust);
  }

  public override setCameraLimits(applyLimits = true) {
    this.camera.lowerBetaLimit = applyLimits ? this.defaultLowerBetaLimit : null;
    this.camera.upperBetaLimit = applyLimits ? this.defaultUpperBetaLimit : null;
    this.camera.lowerRadiusLimit = applyLimits ? ZEDCameraBase.minDistance : null;
  }

  public override setMinMaxDistance(value: { min?: number | null; max?: number | null }) {
    if (value.min !== undefined) {
      ZEDCameraBase.minDistance = value.min;
      this.camera.lowerRadiusLimit = value.min;
    }
    if (value.max !== undefined) {
      ZEDCameraBase.maxDistance = value.max;
      this.camera.upperRadiusLimit = value.max;
    }
  }

  public recoverMinMaxDistance() {
    this.setMinMaxDistance({ min: ZEDCameraBase.minDistance, max: ZEDCameraBase.maxDistance });
  }

  private setCameraValue(ext: { min: Vector3; max: Vector3 }) {
    this.initializeArcRotateCamera();

    // set max radius
    const fitRadius = getFitRadius(this.scene, ext, this.camera);
    if (fitRadius !== undefined) {
      this.camera.upperRadiusLimit = fitRadius * 1.5;
      this.fitRadiusWithMargin = fitRadius;
    } else {
      throw new Error('Fail to get fitRadius in setCameraValue');
    }
  }

  private createPointersInput() {
    const pointersInput = new ArcRotateCameraPointersInput();

    pointersInput.checkIsPanPossible = (offsetX: number, offsetY: number) => {
      return isPanningPossible(offsetX, offsetY, this.scene, this.camera);
    };

    const cameraTypeSetting = this.cameraTypeSetting;
    if (
      cameraTypeSetting === CameraControlType.PanZoom ||
      cameraTypeSetting === CameraControlType.PanRotate
    ) {
      pointersInput.swapPanAndRotate = true;
      pointersInput.multiTouchPanAndZoom = false;
      if (cameraTypeSetting === CameraControlType.PanZoom) {
        pointersInput.multiTouchPanning = false;
        pointersInput.pinchZoom = true;
      } else {
        pointersInput.multiTouchPanning = true;
        pointersInput.pinchZoom = false;
      }
    } else if (
      cameraTypeSetting === CameraControlType.RotateZoom ||
      cameraTypeSetting === CameraControlType.RotatePan
    ) {
      pointersInput.swapPanAndRotate = false;
      pointersInput.multiTouchPanAndZoom = false;
      if (cameraTypeSetting === CameraControlType.RotateZoom) {
        pointersInput.multiTouchPanning = false;
        pointersInput.pinchZoom = true;
      } else {
        pointersInput.multiTouchPanning = true;
        pointersInput.pinchZoom = false;
      }
    } else if (
      cameraTypeSetting === CameraControlType.AllAll ||
      cameraTypeSetting === CameraControlType.AllEach
    ) {
      pointersInput.swapPanAndRotate = false;
      pointersInput.multiTouchPanAndZoom = cameraTypeSetting === CameraControlType.AllAll;
      pointersInput.multiTouchPanning = true;
      pointersInput.pinchZoom = true;
    }
    return pointersInput;
  }

  public override isRemainInertia(): boolean {
    return (
      this.camera.inertialAlphaOffset !== 0 ||
      this.camera.inertialBetaOffset !== 0 ||
      this.camera.inertialPanningX !== 0 ||
      this.camera.inertialPanningY !== 0
    );
  }

  private rotateLeft(camera: ArcRotateCamera) {
    this.rotateCommon(camera, false);
  }

  private rotateRight(camera: ArcRotateCamera) {
    this.rotateCommon(camera, true);
  }

  public override quaterView(alphaAngle = 45, betaAngle = 45, seconds: number) {
    const alpha = alphaAngle * toRadian;
    const beta = betaAngle * toRadian;
    const r = this.fitRadiusWithMargin;
    this.animationHelper.moveAnimation(this.camera, this.defaultTarget, alpha, beta, r, seconds);
  }

  private alphaStart: number = 0;

  private rotateCommon(camera: ArcRotateCamera, toRight: boolean) {
    camera.target = this.defaultTarget;
    camera.radius = this.fitRadiusWithMargin;
    camera.beta = 45 * toRadian;
    if (this.alphaStart === 0) {
      this.alphaStart = camera.alpha;
    }
    let nextAlpha = this.getNextRotationAlpha(this.alphaStart, toRight);
    this.animationHelper.changeAlphaWithAnimation(this.alphaStart, nextAlpha, camera);
    this.alphaStart = nextAlpha;
  }

  private getNextRotationAlpha(currentAlpha: number, doPlus: boolean) {
    const angle = toAngle * currentAlpha;
    const angleRounded = Math.round(angle);
    const isQuaterviewStateNow = angleRounded % 90 === 45 || angleRounded % 90 === -45;
    let finalAngle = 45;
    if (isQuaterviewStateNow) {
      finalAngle = doPlus ? angle + 90 : angle - 90;
    }
    return toRadian * finalAngle;
  }

  private restoreCameraWithRotationHold(camera: ArcRotateCamera) {
    this.animationHelper.zoomAnimation(camera, this.fitRadiusWithMargin, this.defaultTarget, 0.1);
  }

  private zoomInToPoint(camera: ArcRotateCamera, pt: Vector3) {
    if (camera.radius <= 0) {
      return; // cannot zoom in more
    }
    const customFactor = 0.4;
    const zoomFactor = (this.fitRadiusWithMargin * customFactor) / camera.radius;
    const targetToPt = pt.subtract(camera.target);
    const moveVec = targetToPt.multiplyByFloats(1 - zoomFactor, 1 - zoomFactor, 1 - zoomFactor);
    const newTarget = camera.target.add(moveVec);
    const targetRadius = camera.radius * zoomFactor;

    this.animationHelper.zoomAnimation(camera, targetRadius, newTarget, 3);
  }

  public override zoomIn(percent = 30, seconds = 0.3) {
    if (percent < 0) {
      return;
    }
    const radius = this.camera.radius * (1 - percent / 100);
    this.animationHelper.zoomAnimation(this.camera, radius, this.camera.target, seconds);
  }

  public override zoomOut(percent = 30, seconds = 0.3) {
    if (percent < 0) {
      return;
    }
    const radius = this.camera.radius * (1 + percent / 100);
    this.animationHelper.zoomAnimation(this.camera, radius, this.camera.target, seconds);
  }

  public override setDistance(distance = 3, seconds = 0.3) {
    this.animationHelper.zoomAnimation(this.camera, distance, this.camera.target, seconds);
  }

  public override zoomFitOnInit(ext: { min: Vector3; max: Vector3 }) {
    const fitRadius = getFitRadius(this.scene, ext, this.camera);
    if (fitRadius === undefined) {
      return;
    }
    this.fitRadiusWithMargin = fitRadius;
    this.setCameraValue(ext);
    this.setSceneEvents();
    this.animationHelper.zoomAnimation(this.camera, fitRadius, this.defaultTarget, 1.5, true);
  }

  protected override zoomFit(useAnimation = false, ext: { min: Vector3; max: Vector3 }) {
    const fitRadius = getFitRadius(this.scene, ext, this.camera);
    if (fitRadius === undefined) {
      return;
    }
    this.fitRadiusWithMargin = fitRadius;

    if (useAnimation) {
      this.animationHelper.zoomAnimation(this.camera, fitRadius, this.defaultTarget, 0.4);
    } else {
      this.camera.radius = fitRadius;
    }
  }

  public override async moveTo(
    pos: CameraPosition,
    cameraAngle: CameraAngle,
    isRelativeAngle = false,
    seconds = 1
  ) {
    let startVec = new Vector3(0, 0, -1);
    if (isRelativeAngle) {
      startVec = CameraAngle.convertToVectorFromAlphaBetaRadian(
        this.camera.alpha,
        this.camera.beta
      );
    }
    const [alpha, beta] = cameraAngle.getRotatedAlphaBetaAsRadianFrom(startVec);
    const vec = CameraAngle.convertToVectorFromAlphaBetaRadian(alpha, beta);
    const convertedPos = new Vector3(-pos.x, pos.h, -pos.y);
    let target = this.camera.target.clone();
    let newPos = this.camera.position.clone();
    if (pos.isRelative) {
      target = target.add(convertedPos);
      newPos = target.subtract(vec);
    } else {
      target = convertedPos.add(vec);
      newPos = target.subtract(vec);
    }
    const newRadius = Vector3.Distance(newPos, target);

    this.changeCameraLimitRadiusAndUpperBeta(newRadius, beta);

    await this.animationHelper.moveAnimation(this.camera, target, alpha, beta, 1, seconds);
  }

  public override async moveToCameraTarget(
    position: Vector3,
    forward: Vector3,
    cameraAngle: CameraAngle,
    dist: number,
    seconds = 1,
    fromForward = true
  ) {
    const startVec = fromForward ? forward.clone().normalize().scale(-1) : new Vector3(0, 0, -1);
    const [alpha, beta] = cameraAngle.getRotatedAlphaBetaAsRadianFrom(startVec);
    const target = position;

    this.changeCameraLimitRadiusAndUpperBeta(dist, beta);

    await this.animationHelper.moveAnimation(this.camera, target, alpha, beta, dist, seconds);
  }

  public override async moveToCoordinate(position: Vector3, rotation: Vector3, seconds = 1) {
    const rot = rotation.clone();
    rot.z = 0;
    const { target, alpha, beta, radius } = freeToArc(this.scene, position, rot, 1);

    await this.animationHelper.moveAnimation(this.camera, target, alpha, beta, radius, seconds);
  }

  private changeCameraLimitRadiusAndUpperBeta(radius: number, beta: number) {
    if (this.camera.lowerRadiusLimit && radius < this.camera.lowerRadiusLimit) {
      this.camera.lowerRadiusLimit = radius;
      ZEDCameraBase.minDistance = radius;
    }
    if (this.camera.upperRadiusLimit && radius > this.camera.upperRadiusLimit) {
      this.camera.upperRadiusLimit = radius;
      ZEDCameraBase.maxDistance = radius;
    }
  }

  public override topViewFit(seconds = 0.3, ext: { min: Vector3; max: Vector3 }) {
    const target = this.defaultTarget;
    const radius = getFitRadius(this.scene, ext, this.camera);
    if (radius === undefined) {
      return;
    }
    this.animationHelper.moveAnimation(
      this.camera,
      target,
      this.defaultAlpha,
      this.defaultBeta,
      radius,
      seconds
    );
  }

  public override topViewCustom(
    x: number,
    y: number,
    zoomPercent: number,
    rotation: number,
    seconds = 0.3,
    ext: { min: Vector3; max: Vector3 }
  ) {
    if (zoomPercent <= 0) {
      return;
    }
    const target = this.defaultTarget;
    const newTarget = new Vector3(target.x - x, target.y, target.z - y);
    const radius = getFitRadius(this.scene, ext, this.camera);
    if (radius === undefined) {
      return;
    }
    const ratio = 1 + (100 - zoomPercent) / 100;
    this.animationHelper.moveAnimation(
      this.camera,
      newTarget,
      this.defaultAlpha + rotation * toRadian,
      this.defaultBeta,
      radius * ratio,
      seconds
    );
  }

  public override setViewport(to: Viewport): void {
    Object.assign(this.camera.viewport, to);
  }

  public override getViewport(): Viewport {
    return this.camera.viewport;
  }

  public override transformViewport(
    from: Viewport,
    to: Viewport,
    frame: number,
    onEnd?: () => void
  ): void {
    this.animationHelper.viewportAnimation(this.camera, from, to, frame, onEnd);
  }

  public override enableControl(enable: boolean) {
    this.camera.inputs.removeByType('ArcRotateCameraPointersInput');

    if (enable) {
      const pointersInput = this.createPointersInput();
      this.camera.inputs.add(pointersInput);
      this.applyCameraSpeed();
    }
  }
}

export enum CameraType {
  ArcRotate,
  Free,
  Gyro,
}
