import {
  type Engine,
  EngineInstrumentation,
  type Scene,
  SceneInstrumentation,
} from '@babylonjs/core';

export type PerformanceInfo = {
  fps: number;
  meshSelection: number;
  renderTarget: number;
  render: number;
  frameTotal: number;
  interFrame: number;
  activeMeshes: number;
  drawCalls: number;
  vertices: number;
  resolution: string;
  loadHomeTotal: number;
  customInfo?: Map<string, Map<string, any>>;
};

export class PerformanceProfiler {
  private engine: Engine;

  private sceneInstrumentation: SceneInstrumentation | undefined = undefined;

  private engineInstrumentation: EngineInstrumentation | undefined = undefined;

  private intervalId: NodeJS.Timer | undefined = undefined;

  private customDataMap = new Map<string, Map<string, any>>();

  private constructor(private scene: Scene) {
    this.engine = scene.getEngine();
    this.scene = scene;
  }

  //  
  private static instance?: PerformanceProfiler = undefined;

  public static getInstance = (scene: Scene) => {
    if (this.instance === undefined) {
      this.instance = new PerformanceProfiler(scene);
    }
    return this.instance;
  };
  // END

  public loadHomeTotal = 0;

  public itemChanged = false;

  public addCustom(group: string, item: string, value: any) {
    if (!this.customDataMap.has(group)) {
      this.customDataMap.set(group, new Map<string, any>());
    }
    this.customDataMap.get(group)!.set(item, value);
  }

  public registerCallback(
    callback: (param: PerformanceInfo, itemChanged?: boolean) => void,
    intervalMs: number
  ) {
    //    unregister
    if (this.intervalId !== undefined) {
      this.unregisterCallback();
    }

    //    
    if (this.sceneInstrumentation === undefined) {
      this.createInstrumentations();
    }

    this.intervalId = setInterval(() => {
      // interval    
      const performanceInfo = {
        fps: this.scene.getEngine().getFps(),
        meshSelection: this.sceneInstrumentation!.activeMeshesEvaluationTimeCounter.lastSecAverage,
        renderTarget: this.sceneInstrumentation!.renderTargetsRenderTimeCounter.lastSecAverage,
        render: this.sceneInstrumentation!.renderTimeCounter.lastSecAverage,
        frameTotal: this.sceneInstrumentation!.frameTimeCounter.lastSecAverage,
        interFrame: this.sceneInstrumentation!.interFrameTimeCounter.lastSecAverage,
        activeMeshes: this.scene.getActiveMeshes().length,
        drawCalls: this.sceneInstrumentation?.drawCallsCounter.current!,
        vertices: this.scene.getTotalVertices(),
        resolution: this.engine.getRenderWidth() + ' x ' + this.engine.getRenderHeight(),
        loadHomeTotal: this.loadHomeTotal,
        customInfo: this.customDataMap,
      };
      callback(performanceInfo, this.itemChanged);
      if (this.itemChanged) {
        this.itemChanged = false;
      }
      // END
    }, intervalMs);
  }

  public unregisterCallback() {
    if (this.intervalId !== undefined) {
      clearInterval(this.intervalId);
      this.intervalId = undefined;

      this.sceneInstrumentation?.dispose();
      this.sceneInstrumentation = undefined;

      this.engineInstrumentation?.dispose();
      this.engineInstrumentation = undefined;
    }
  }

  public showInspector() {
    this.scene.debugLayer.show({ showExplorer: false, initialTab: 2 });
  }

  public hideInspector() {
    this.scene.debugLayer.hide();
  }

  private createInstrumentations = () => {
    this.sceneInstrumentation = new SceneInstrumentation(this.scene);
    this.sceneInstrumentation.captureActiveMeshesEvaluationTime = true;
    this.sceneInstrumentation.captureRenderTargetsRenderTime = true;
    this.sceneInstrumentation.captureFrameTime = true;
    this.sceneInstrumentation.captureRenderTime = true;
    this.sceneInstrumentation.captureInterFrameTime = true;
    this.sceneInstrumentation.captureParticlesRenderTime = true;
    this.sceneInstrumentation.captureSpritesRenderTime = true;
    this.sceneInstrumentation.capturePhysicsTime = true;
    this.sceneInstrumentation.captureAnimationsTime = true;

    this.engineInstrumentation = new EngineInstrumentation(this.scene.getEngine());
    this.engineInstrumentation.captureGPUFrameTime = true;
  };
}
