import { PointerInfo } from '@babylonjs/core';

import { pause, play, setVolume, type VideoPlayerIO } from '@/components';
import { clearArchetype, World } from '@/World';

export class VideoPlayerSystem {
  private static instance: VideoPlayerSystem | undefined;
  public static getVideoPlayers(): VideoPlayerIO[] {
    return this.instance?.getVideoPlayers() ?? [];
  }

  private targetArchetype = World.with('ActiveHome', 'Identifier', 'VideoPlayer');

  constructor() {
    VideoPlayerSystem.instance = this;
    this.targetArchetype.onEntityAdded.add((e) => this.initVideoPlayer(e));
  }
  private initVideoPlayer = (entity: (typeof this.targetArchetype.entities)[number]) => {
    entity.VideoPlayer.mesh.isPickable = true;
  };

  public dispose() {
    VideoPlayerSystem.instance = undefined;
    for (const entity of this.targetArchetype.entities) {
      const videoPlayer = entity.VideoPlayer;
      if (!!videoPlayer.videoTexture) {
        const videoEl = videoPlayer.videoTexture.video;
        videoPlayer.videoTexture.dispose();

        while (videoEl.firstChild) {
          if (!!videoEl.lastChild) videoEl.removeChild(videoEl.lastChild);
        }
        videoEl.src = '';
        videoEl.removeAttribute('src');
        videoEl.remove();
      }
    }
    clearArchetype(this.targetArchetype);
  }

  public onPointerUp(pointerInfo: PointerInfo) {
    const info = pointerInfo.pickInfo;
    if (!info) return;

    for (const entity of this.targetArchetype.entities) {
      const videoPlayer = entity.VideoPlayer;
      if (info.pickedMesh === videoPlayer.mesh) {
        if (!!videoPlayer.videoTexture) {
          if (videoPlayer.videoTexture.video.paused) {
            play(videoPlayer);
          } else {
            pause(videoPlayer);
          }
        } else {
          play(videoPlayer);
        }
      }
    }
  }

  private getVideoPlayers(): VideoPlayerIO[] {
    return (
      this.targetArchetype.entities.map((entity) => {
        const videoPlayer = entity.VideoPlayer;
        return {
          play: () => play(videoPlayer),
          pause: () => pause(videoPlayer),
          setVolume: (volume: number) => setVolume(videoPlayer, volume),
        };
      }) ?? []
    );
  }
}
