import {
  type AbstractMesh,
  Color3,
  type PBRMaterial,
  VideoTexture,
  type VideoTextureSettings,
} from '@babylonjs/core';

import { RenderManager } from '@/manager';

/**
 *      Component
 */
export interface VideoPlayer {
  initialized: boolean;
  src: string;
  mesh: AbstractMesh;
  material: PBRMaterial;
  defaultPlay: boolean;
  videoTexture?: VideoTexture;
}

const initializeVideoPlayer = (videoPlayer: VideoPlayer) => {
  if (!videoPlayer.videoTexture && videoPlayer.src !== '') {
    if (videoPlayer.defaultPlay) {
      RenderManager.instance!.playingVideoCount++;
    }

    const settings: VideoTextureSettings = {
      autoPlay: videoPlayer.defaultPlay,
      muted: false,
      autoUpdateTexture: true,
    };
    const videoTexture = new VideoTexture(
      videoPlayer.mesh.name,
      videoPlayer.src,
      null,
      false,
      true,
      0,
      settings
    );
    videoTexture.video.volume = 0.3;

    // FIXME: TEMP aspect
    videoTexture.uScale = 1;
    videoTexture.vScale = 16 / 9;
    videoTexture.vOffset = -0.4;

    videoPlayer.videoTexture = videoTexture;
  }
};
export const play = (target: VideoPlayer) => {
  const play = () => {
    if (!!target && !!target.videoTexture) {
      if (target.material.albedoTexture !== target.videoTexture) {
        target.material.unfreeze();
        target.material.albedoTexture = target.videoTexture;
        target.material.albedoColor = Color3.White();
        target.material.metallic = 0.5;
        target.material.indexOfRefraction = 1;
        target.material.forceCompilation(target.mesh);
      }

      target.videoTexture.video.play();
      RenderManager.instance!.playingVideoCount++;
    }
  };
  if (!target.initialized) {
    target.initialized = true;
    initializeVideoPlayer(target);

    target.videoTexture?.onLoadObservable.add(() => play());
  } else {
    play();
  }
};
export const pause = (target: VideoPlayer) => {
  if (!!target) {
    target.videoTexture?.video.pause();

    RenderManager.instance!.playingVideoCount--;
    if (RenderManager.instance!.playingVideoCount < 0) {
      RenderManager.instance!.playingVideoCount = 0;
    }
  }
};
export const setVolume = (target: VideoPlayer, volume: number) => {
  if (!!target && !!target.videoTexture) {
    target.videoTexture.video.volume = Math.max(Math.min(1, volume), 0);
  }
};
export interface VideoPlayerIO {
  /**
   *  VideoPlayer video play.
   */
  play: () => void;
  /**
   *  VideoPlayer video pause.
   */
  pause: () => void;
  /**
   *  VideoPlayer video Volume .
   */
  setVolume: (volume: number) => void;
}
