import {
  DeviceOrientationCamera,
  PointerEventTypes,
  PointerInfo,
  Vector2,
  Vector3,
} from '@babylonjs/core';

export class GyroPinchHandler {
  public static pointMap = new Map<number, Vector2>();
  public static prevDist: number = -1;

  public static processPinch = (e: PointerInfo, camera: DeviceOrientationCamera): boolean => {
    if (e.type === PointerEventTypes.POINTERDOWN) {
      this.pointMap.set(
        (e.event as PointerEvent).pointerId,
        new Vector2(e.event.clientX, e.event.clientY)
      );
      if (this.pointMap.size === 2) {
        const values = Array.from(this.pointMap.values());
        this.prevDist = Vector2.Distance(values[0], values[1]);
      }
    }

    if (e.type === PointerEventTypes.POINTERUP) {
      const id = (e.event as PointerEvent).pointerId;
      this.pointMap.clear();
      this.prevDist = -1;
      camera.enableHorizontalDragging(-2 / 300);
    }

    if (e.type === PointerEventTypes.POINTERMOVE) {
      this.pointMap.set(
        (e.event as PointerEvent).pointerId,
        new Vector2(e.event.clientX, e.event.clientY)
      );

      if (this.pointMap.size === 2) {
        camera.enableHorizontalDragging(0);
        const values = Array.from(this.pointMap.values());
        const dist = Vector2.Distance(values[0], values[1]);
        if (this.prevDist !== -1) {
          const delta = dist - this.prevDist;
          const dir = camera.target.subtract(camera.position);
          const addVec = new Vector3(dir.x * (delta * 0.01), 0, dir.z * (delta * 0.01));
          camera.position = camera.position.add(addVec);
          camera.target = camera.target.add(addVec);
          this.prevDist = dist;
          return true;
        } else {
          this.prevDist = dist;
        }
      }
    }
    return false;
  };
}
