import { ObservableManager, RenderManager } from '@/manager';
import { clearArchetype, World } from '@/World';

import type { Entity, StateByThingType } from '@/components';
import type { Scene } from '@babylonjs/core';

export class ThingStateSystem {
  //--------------------------------------------
  private static instance: ThingStateSystem | undefined;
  public static getThingStateEntities() {
    return this.instance?.getThingStateEntities() ?? [];
  }
  public static getThingStateEntitiesInFloorplan(floorplanId: string) {
    return this.instance?.getThingStateEntitiesInFloorplan(floorplanId) ?? [];
  }
  public static updateThingState(entityId: string, state: StateByThingType) {
    this.instance?.updateThingState(entityId, state);
  }

  //--------------------------------------------
  private targetArchetype = World.with('ActiveHome', 'Identifier', 'ThingState');
  private thingStateDirtyArchetype = this.targetArchetype.with('ThingStateDirty');
  private lastState = new Map<Entity, StateByThingType>();

  constructor() {
    ThingStateSystem.instance = this;
    this.targetArchetype.onEntityAdded.add((e) => this.initEntity(e));
  }
  dispose() {
    ThingStateSystem.instance = undefined;
    clearArchetype(this.targetArchetype);
    clearArchetype(this.thingStateDirtyArchetype);
    this.lastState.clear();
  }
  //--------------------------------------------
  private initEntity = (entity: (typeof this.targetArchetype.entities)[number]) => {
    this.lastState.set(entity, entity.ThingState.state);
  };
  public beforeRenderExecute = (scene: Scene) => {
    for (const entity of this.targetArchetype) {
      const lastState = this.lastState.get(entity);
      if (!lastState) continue;

      const state = entity.ThingState.state;
      if (state !== lastState) {
        World.addComponent(entity, 'ThingStateDirty', true);
      }
    }
  };
  public afterRenderExecute = (scene: Scene) => {
    for (const entity of this.thingStateDirtyArchetype) {
      World.removeComponent(entity, 'ThingStateDirty');
      const lastState = this.lastState.get(entity);
      if (!lastState) continue;

      this.lastState.set(entity, entity.ThingState.state);
    }
  };

  private getThingStateEntities() {
    return this.targetArchetype.entities ?? [];
  }
  private getThingStateEntitiesInFloorplan(floorplanId: string) {
    return this.targetArchetype.entities.filter((e) => e.Identifier.floorplanId == floorplanId);
  }

  private updateThingState(entityId: string, state: StateByThingType) {
    const entity = this.targetArchetype.entities.find((e) => e.entityId === entityId);

    if (entity) {
      const component = entity.ThingState;
      if (component) {
        // update state
        component.state = state;
        // call apply system
        ObservableManager.instance.onEntityStateChanged.notifyObservers(entityId);

        RenderManager.RenderUpdate();
      }
    }
  }
}
