import { AbstractMesh, Angle, BoundingInfo, Matrix, Scene, Vector3 } from '@babylonjs/core';

import { RenderManager } from '@/manager';
import { clearArchetype, World } from '@/World';

import type { Entity } from '@/components';
import type { MagnetismFixture } from '@/components/Magnestism';

export class TransformSystem {
  private static instance: TransformSystem | undefined;
  public static updateTransform(entityId: string, position?: Vector3, rotationY?: number) {
    this.instance?.updateTransform(entityId, position, rotationY);
  }
  public static getMovableTransformEntities() {
    return this.instance?.getMovableTransformEntities() ?? [];
  }
  public static getMovableTransformEntitiesInFloorplan(floorplanId: string) {
    return this.instance?.getMovableTransformEntitiesInFloorplan(floorplanId) ?? [];
  }

  private transforms = World.with('TransformNode');
  private transformsDirty = this.transforms.with('TransformDirty');

  private dynamicTransforms = this.transforms.with('TransformMovable');
  private staticTransforms = this.transforms.without('TransformMovable');

  private staticTransformsDirty = this.transformsDirty.without('TransformMovable');

  private activeDynamicTransforms = this.dynamicTransforms.with('ActiveHome', 'Identifier');

  private lastTransforms = new Map<Entity, Matrix>();

  constructor() {
    TransformSystem.instance = this;
    this.dynamicTransforms.onEntityAdded.add((e) => this.whenDynamicTransform(e));
    this.staticTransforms.onEntityAdded.add((e) => this.whenStaticTransform(e));
  }
  dispose() {
    TransformSystem.instance = undefined;
    clearArchetype(this.transforms);
    clearArchetype(this.transformsDirty);
    clearArchetype(this.dynamicTransforms);
    clearArchetype(this.staticTransforms);
    clearArchetype(this.staticTransformsDirty);
    clearArchetype(this.activeDynamicTransforms);
    this.lastTransforms.clear();
  }

  private whenDynamicTransform = (entity: (typeof this.dynamicTransforms.entities)[number]) => {
    const { TransformNode, TransformMovable } = entity;
    TransformNode.unfreezeWorldMatrix();
    TransformNode.getChildMeshes().forEach((mesh) => {
      mesh.unfreezeWorldMatrix();
      mesh.doNotSyncBoundingInfo = false;
    });
    this.lastTransforms.set(entity, TransformNode.getWorldMatrix().clone());

    if (TransformNode instanceof AbstractMesh) {
      const scene = TransformNode.getScene();
      scene.selectionOctree?.dynamicContent.push(TransformNode);
    }

    const magneticFace = TransformMovable.magnetismFixtureFace;
    if (magneticFace) {
      const { min: hmin, max: hmax } = TransformNode.getHierarchyBoundingVectors(true);
      const rootMatrix = TransformNode.getWorldMatrix().clone().invert();
      const corner0 = Vector3.TransformCoordinates(hmin, rootMatrix);
      const corner1 = Vector3.TransformCoordinates(hmax, rootMatrix);
      const boundingInfo = new BoundingInfo(
        // world  local  minMax  
        Vector3.Minimize(corner0, corner1),
        Vector3.Maximize(corner0, corner1)
      );

      const min = boundingInfo.boundingBox.minimum;
      const max = boundingInfo.boundingBox.maximum;

      let fixtures: MagnetismFixture[] = [];
      const pivotY = magneticFace.bottom ? min.y : magneticFace.top ? max.y : 0;

      if (magneticFace.front) {
        //  
        fixtures.push({
          position: new Vector3(0, pivotY, max.z),
          normal: Vector3.Forward(),
          constraints: [
            {
              type: 'padding',
              value: new Vector3(Number.MAX_VALUE, 0, Number.MAX_VALUE),
            },
          ],
        });
      }
      if (magneticFace.back) {
        //  
        fixtures.push({
          position: new Vector3(0, pivotY, min.z),
          normal: Vector3.Backward(),
          constraints: [
            {
              type: 'padding',
              value: new Vector3(Number.MAX_VALUE, 0, Number.MAX_VALUE),
            },
          ],
        });
      }
      if (magneticFace.left) {
        //  
        fixtures.push({
          position: new Vector3(min.x, pivotY, 0),
          normal: Vector3.Left(),
          constraints: [
            {
              type: 'padding',
              value: new Vector3(Number.MAX_VALUE, 0, Number.MAX_VALUE),
            },
          ],
        });
      }
      if (magneticFace.right) {
        //  
        fixtures.push({
          position: new Vector3(max.x, pivotY, 0),
          normal: Vector3.Right(),
          constraints: [
            {
              type: 'padding',
              value: new Vector3(Number.MAX_VALUE, 0, Number.MAX_VALUE),
            },
          ],
        });
      }
      if (magneticFace.top) {
        // 
        fixtures.push({
          position: new Vector3(0, pivotY, 0),
          normal: Vector3.Up(),
          constraints: [{ type: 'normal', value: Vector3.Down() }],
        });
      }
      if (magneticFace.bottom) {
        // 
        fixtures.push({
          position: new Vector3(0, pivotY, 0),
          normal: Vector3.Down(),
          constraints: [{ type: 'normal', value: Vector3.Up() }],
        });
      }

      if (fixtures.length > 0) {
        World.addComponent(entity, 'MagnetismFixtures', {
          fixtures: fixtures,
        });
      }
    }
  };

  private whenStaticTransform = (entity: (typeof this.staticTransforms.entities)[number]) => {
    const { TransformNode } = entity;
    TransformNode.freezeWorldMatrix();

    this.lastTransforms.delete(entity);

    if (TransformNode instanceof AbstractMesh) {
      const scene = TransformNode.getScene();
      const index = scene.selectionOctree?.dynamicContent.indexOf(TransformNode);
      if (index >= 0) {
        scene.selectionOctree?.dynamicContent.splice(index, 1);
      }
    }
  };

  public beforeRenderExecute = (scene: Scene) => {
    if (this.staticTransformsDirty.size > 0 && scene.selectionOctree) {
      scene.createOrUpdateSelectionOctree();
    }

    for (const entity of this.dynamicTransforms) {
      const lastTransform = this.lastTransforms.get(entity);
      if (!lastTransform) continue;

      const { TransformNode } = entity;

      const currentMatrix = TransformNode.getWorldMatrix();
      if (!currentMatrix.equals(lastTransform)) {
        World.addComponent(entity, 'TransformDirty', true);
      }
    }
  };

  public afterRenderExecute = (scene: Scene) => {
    for (const entity of this.transformsDirty) {
      World.removeComponent(entity, 'TransformDirty');
      const lastTransform = this.lastTransforms.get(entity);
      if (!lastTransform) continue;

      const { TransformNode } = entity;

      lastTransform.copyFrom(TransformNode.getWorldMatrix());
    }
  };

  private updateTransform(entityId: string, position?: Vector3, rotationY?: number) {
    const isDirty = position !== undefined || rotationY !== undefined;
    if (isDirty) {
      const entity = this.activeDynamicTransforms.entities.find((e) => e.entityId == entityId);
      if (entity) {
        this.updatePosition(entity, position, rotationY);
        RenderManager.RenderUpdate();
      }
    }
  }
  private updatePosition = (
    entity: (typeof this.activeDynamicTransforms.entities)[number],
    position?: Vector3,
    rotationY?: number
  ) => {
    const transformNode = entity.TransformNode;
    if (position !== undefined) {
      transformNode.setAbsolutePosition(position);
    }

    if (rotationY !== undefined) {
      const y = Angle.FromDegrees(rotationY % 360).radians();
      transformNode.rotation = new Vector3(0, y, 0);
    }
  };

  private getMovableTransformEntities() {
    return this.activeDynamicTransforms.entities ?? [];
  }
  private getMovableTransformEntitiesInFloorplan(floorplanId?: string) {
    return (
      this.activeDynamicTransforms.entities.filter(
        (e) => e.Identifier.floorplanId === floorplanId
      ) ?? []
    );
  }
}
