import { PBRMaterial } from '@babylonjs/core';

import { RenderManager } from '@/manager';
import { clearArchetype, World } from '@/World';

export class HallwaySystem {
  private static instance: HallwaySystem | undefined;
  public static setHallwayVisibility(visibility: number) {
    this.instance?.setHallwayVisibility(visibility);
  }

  private targetArchetype = World.with('ActiveHome', 'Identifier', 'Structure', 'Hallway');

  constructor() {
    HallwaySystem.instance = this;
    this.targetArchetype.onEntityAdded.add((e) => this.initTransparentable(e));
  }

  dispose() {
    HallwaySystem.instance = undefined;
    clearArchetype(this.targetArchetype);
  }

  private initTransparentable = (entity: (typeof this.targetArchetype.entities)[number]) => {
    entity.Structure.meshes.forEach((mesh) => {
      if (mesh.material && mesh.material instanceof PBRMaterial) {
        mesh.visibility = 0;
        mesh.material.environmentIntensity = 1;
      }
    });
  };
  private setHallwayVisibility(visibility: number) {
    for (const entity of this.targetArchetype.entities) {
      for (const mesh of entity.Structure.meshes) mesh.visibility = visibility;
    }
    RenderManager.RenderUpdate();
  }
}
