import { Vector3 } from '@babylonjs/core';

import { toAngle, toRadian } from '../util/camera/CameraUtils';
import { EasingFunctionType, EasingModeType } from '../util/EasingFunctionType';

export enum CameraControlType {
  PanZoom = 0, // pan only (zoom)
  PanRotate = 1, // pan only (no zoom and rotate)
  RotateZoom = 2, // rotate (zoom)
  RotatePan = 3, // rotate (no zoom)
  AllAll = 4,
  AllEach = 5,
}

export interface AlignOption {
  column: 'left' | 'center' | 'right';
  row: 'top' | 'center' | 'bottom';
}

export class AnimationOption {
  public seconds: number;
  public easingFunction: EasingFunctionType;
  public easingMode: EasingModeType;

  constructor(param?: {
    seconds?: number;
    easingFunction?: EasingFunctionType;
    easingMode?: EasingModeType;
  }) {
    if (param === undefined) {
      param = {};
    }
    if (param.seconds === undefined) {
      param.seconds = 0.6;
    }
    if (param.easingFunction === undefined) {
      param.easingFunction = EasingFunctionType.CircleEase;
    }
    if (param.easingMode === undefined) {
      param.easingMode = EasingModeType.Out;
    }
    this.seconds = param.seconds;
    this.easingFunction = param.easingFunction;
    this.easingMode = param.easingMode;
  }
}

export class CameraPosition {
  public x: number;
  public y: number;
  public h: number;
  public isRelative: boolean;

  constructor(x = 0, y = 0, h = 0, isRelative = false) {
    this.x = x;
    this.y = y;
    this.h = h;
    this.isRelative = isRelative;
  }
}
export class CameraAngle {
  public angleHor: number;
  public angleVer: number;
  private lowerVerticalAngleLimit = -88;

  constructor(param?: { angleHor?: number; angleVer?: number }) {
    if (param === undefined) {
      param = {};
    }
    if (param.angleHor === undefined) {
      param.angleHor = 0;
    }
    if (param.angleVer === undefined) {
      param.angleVer = 0;
    }
    this.angleHor = param.angleHor;
    this.angleVer = param.angleVer;
    if (this.angleVer < this.lowerVerticalAngleLimit) {
      this.angleVer = this.lowerVerticalAngleLimit;
    }
  }

  public static convertToVectorFromAlphaBetaRadian(alpha: number, beta: number): Vector3 {
    const v = Math.sin(beta - 90 * toRadian);
    const h = Math.cos(beta - 90 * toRadian);
    const x = h * Math.cos(alpha);
    const y = h * Math.sin(alpha);
    return new Vector3(-x, v, -y).normalize();
  }

  public getRotatedVectorFrom(startVec?: Vector3): Vector3 {
    const [alpha, beta] = this.getRotatedAlphaBetaAsRadianFrom(startVec);
    return CameraAngle.convertToVectorFromAlphaBetaRadian(alpha, beta);
  }

  public getRotatedAlphaBetaAsRadianFrom(startVec?: Vector3): [number, number] {
    // calculate horizontal
    let alpha = 0;
    if (startVec) {
      let norm = startVec.clone();
      norm.y = 0;
      norm = norm.normalize();
      alpha = Math.atan2(-norm.z, -norm.x) * toAngle;
    } else {
      alpha = 90;
    }
    alpha += this.angleHor;
    alpha = (alpha + 360) % 360;

    // calculate vertical
    let beta = 0;
    if (startVec) {
      let norm = startVec.clone().normalize();
      let downAngle = Math.asin(norm.y) * toAngle; // -90 ~ 90
      beta = downAngle + 90; // 0 ~ 180
    } else {
      beta = 90;
    }
    beta -= this.angleVer;
    if (beta < 0) beta = 0;
    if (beta > 180) beta = 180;

    return [alpha * toRadian, beta * toRadian];
  }

  public static readonly DOWN = new CameraAngle({ angleHor: 0, angleVer: -90 });
  public static readonly NORTH = new CameraAngle({ angleHor: 0, angleVer: 0 });
  public static readonly SOUTH = new CameraAngle({ angleHor: 180, angleVer: 0 });
  public static readonly WEST = new CameraAngle({ angleHor: -90, angleVer: 0 });
  public static readonly EAST = new CameraAngle({ angleHor: 90, angleVer: 0 });
}

export class CameraTarget {
  public name: string;
  constructor(name: string) {
    this.name = name;
  }
}

export class CameraViewport {
  public x: number;
  public y: number;
  public width: number;
  public height: number;

  constructor(x = 0, y = 0, width = 0, height = 0) {
    this.x = x;
    this.y = y;
    this.width = width;
    this.height = height;
  }
}
