import { AbstractMesh, EngineStore, Ray, Vector3 } from '@babylonjs/core';

import { type Entity, type RoomInfo, type RoomObjectInfo } from '@/components';
import { MaterialPluginManager } from '@/manager';
import { toRadian } from '@/util/camera/CameraUtils';
import { clearArchetype, World } from '@/World';

import type { With } from 'miniplex';

export class RoomSystem {
  //------------------------------------------------------------------------------------
  // #### API ####
  //------------------------------------------------------------------------------------
  /**
   *  floorplan   RoomObject Entity Room  .
   */
  public static updateRoomInFloorplan(floorplanId: string) {
    this.instance?.updateRoomInFloorplan(floorplanId);
  }
  //------------------------------------------------------------------------------------
  /**
   * Room Component   Entity RoomInfo  .
   * @param floorplanId   floorplan  RoomInfo .
   * @returns
   */
  public static getRooms(floorplanId?: string) {
    return this.instance?.RoomInfoByEntities(this.instance?.getRooms(floorplanId)) ?? [];
  }
  //------------------------------------------------------------------------------------
  /**
   * RoomObject Component   Entity RoomObjectInfo  .
   * @param roomId   room  Entity .
   * @returns
   */
  public static getRoomObjects(roomId?: string) {
    return this.instance?.RoomObjectInfoByEntities(this.instance?.getRoomObjects(roomId)) ?? [];
  }
  //------------------------------------------------------------------------------------
  /**
   * RoomObject Component   Entity  Thing(IoT)State  Entity RoomObjectInfo  .
   * @param roomId   room  Entity .
   * @returns
   */
  public static getRoomObjectsOnlyThing(roomId?: string) {
    return (
      this.instance?.RoomObjectInfoByEntities(this.instance?.getRoomObjectsOnlyThing(roomId)) ?? []
    );
  }

  //------------------------------------------------------------------------------------
  // #### System Base ####
  //------------------------------------------------------------------------------------
  private static instance: RoomSystem | undefined;
  private roomArchetype = World.with('Identifier', 'TransformNode', 'Room', 'Structure');
  private roomObjectArchetype = World.with('Identifier', 'TransformNode', 'RoomObject');
  private roomObjectDirtyArchetype = this.roomObjectArchetype.with('TransformDirty');
  //------------------------------------------------------------------------------------
  constructor() {
    RoomSystem.instance = this;
    this.roomArchetype.onEntityAdded.add((e) => this.initRoom(e));
    this.roomObjectDirtyArchetype.onEntityAdded.add((e) => this.updateRoom(e));
  }
  //------------------------------------------------------------------------------------
  dispose() {
    RoomSystem.instance = undefined;
    clearArchetype(this.roomArchetype);
    clearArchetype(this.roomObjectArchetype);
    clearArchetype(this.roomObjectDirtyArchetype);
  }

  //------------------------------------------------------------------------------------
  // #### Initialize ####
  //------------------------------------------------------------------------------------
  /**
   * Room Entity    .
   */
  private initRoom = (entity: (typeof this.roomArchetype.entities)[number]) => {
    for (let i = 0; entity.Room.viewDataList && i < entity.Room.viewDataList.length; i++) {
      const pos = entity.Room.viewDataList[i].position;
      entity.Room.viewDataList[i].position = new Vector3(pos.x, pos.y, pos.z);
      const rot = entity.Room.viewDataList[i].rotation;
      entity.Room.viewDataList[i].rotation = new Vector3(
        rot.x * toRadian,
        rot.y * toRadian,
        rot.z * toRadian
      );
    }
    entity.Structure.meshes.forEach((m) => {
      m.isPickable = true;
      MaterialPluginManager.instance.registRoomLightMaterial(entity.entityId, m.material);
    });
  };

  //------------------------------------------------------------------------------------
  // #### Update Room With Raycast ####
  //------------------------------------------------------------------------------------
  private rayDirection = Vector3.Up().scale(-1);
  private rayDistance = 1000;
  //------------------------------------------------------------------------------------
  /**
   *  floorplan   RoomObject Entity Room  .
   */
  private updateRoomInFloorplan(floorplanId: string) {
    this.roomObjectArchetype.entities
      .filter((e) => e.Identifier.floorplanId === floorplanId && !e.Room)
      .forEach((e) => this.updateRoom(e));
  }
  //------------------------------------------------------------------------------------
  /**
   * RoomObject Entity Room  .
   * @returns
   */
  private updateRoom = (entity: (typeof this.roomObjectArchetype.entities)[number]) => {
    if (entity.Room) return;

    const transformNode = entity.TransformNode;
    const structure = entity.Structure;

    const boundingInfo = transformNode.getHierarchyBoundingVectors();
    let origin = boundingInfo.max.add(boundingInfo.min).scale(0.5);
    origin.y = 2;

    let hitEntity: ReturnType<typeof this.pickWithRay> = undefined;
    let meshes: AbstractMesh[] | undefined = undefined;

    if (structure) {
      hitEntity = this.pickWithRay([origin]);
      meshes = structure.meshes;
    } else {
      const forward = transformNode.forward;
      const forwardOrigin = origin.add(forward.scale(0.5));
      const backOrigin = origin.add(forward.scale(-0.5));
      hitEntity = this.pickWithRay([origin, forwardOrigin, backOrigin]);
      meshes = transformNode.getChildMeshes();
    }

    if (hitEntity && meshes && entity.RoomObject.roomEntityId !== hitEntity.entityId) {
      const roomEntityId = hitEntity.entityId;
      entity.RoomObject.roomEntityId = roomEntityId;
      meshes.forEach((m) =>
        MaterialPluginManager.instance.registRoomLightMaterial(roomEntityId, m.material)
      );
    }
  };
  //------------------------------------------------------------------------------------
  /**
   *  origins  Raycast  Pick(Hit) Room Entity .
   * @returns
   */
  private pickWithRay = (origins: Vector3[]) => {
    const scene = EngineStore.LastCreatedScene;
    if (!scene) return;

    let ray: Ray | undefined;
    for (let i = 0; i < origins.length; i++) {
      const origin = origins[i];
      if (ray) {
        ray.origin = origin;
      } else {
        ray = new Ray(origin, this.rayDirection, this.rayDistance);
      }
      let hitEntity:
        | With<Entity, 'Identifier' | 'TransformNode' | 'Structure' | 'Room'>
        | undefined;
      const hit = scene.pickWithRay(
        ray,
        (pickedMesh) => {
          hitEntity = this.checkPickedMeshIsRoomMesh(pickedMesh);
          return !!hitEntity;
        },
        true
      );
      if (hitEntity)
        return hitEntity as With<Entity, 'Identifier' | 'TransformNode' | 'Structure' | 'Room'>;
    }
    return undefined;
  };
  //------------------------------------------------------------------------------------
  /**
   * Pick(Hit) Mesh Room Entities Mesh    Room Entity .
   * @returns
   */
  private checkPickedMeshIsRoomMesh(pickedMesh: AbstractMesh) {
    for (const entity of this.roomArchetype.entities) {
      for (const mesh of entity.Structure.meshes) {
        if (mesh === pickedMesh) {
          return entity;
        }
      }
    }
  }

  //------------------------------------------------------------------------------------
  // #### Query ####
  //------------------------------------------------------------------------------------
  /**
   * Room Component   Entity  .
   * @param floorplanId   floorplan  Entity .
   * @returns
   */
  private getRooms(floorplanId?: string) {
    return (
      this.roomArchetype.entities.flatMap((e) => {
        if (!floorplanId || e.Identifier.floorplanId === floorplanId) {
          return e;
        } else {
          return [];
        }
      }) ?? []
    );
  }
  //------------------------------------------------------------------------------------
  /**
   * RoomObject Component   Entity  .
   * @param roomId   room  Entity .
   * @returns
   */
  private getRoomObjects(roomId?: string) {
    return (
      this.roomObjectArchetype.entities.flatMap((e) => {
        let roomEntityId = e.RoomObject.roomEntityId ?? (e.Room ? e.entityId : undefined);
        if (roomEntityId) {
          if (roomId) {
            if (roomEntityId === roomId) {
              return e;
            } else {
              return [];
            }
          } else {
            return e;
          }
        } else {
          return [];
        }
      }) ?? []
    );
  }
  //------------------------------------------------------------------------------------
  /**
   * RoomObject Component   Entity  Thing(IoT)State  Entity  .
   * @param roomId   room  Entity .
   * @returns
   */
  private getRoomObjectsOnlyThing(roomId?: string) {
    return (
      this.getRoomObjects(roomId).flatMap((e) => {
        if (e.ThingState) {
          return e;
        } else {
          return [];
        }
      }) ?? []
    );
  }

  //------------------------------------------------------------------------------------
  // #### Convert Info By Entity ####
  //------------------------------------------------------------------------------------
  /**
   * Room Component   Entity RoomInfo .
   * @returns
   */
  private RoomInfoByEntities(entities: ReturnType<typeof this.getRooms>): RoomInfo[] {
    return (
      entities.flatMap((e) => {
        return {
          floorplanId: e.Identifier.floorplanId,
          roomEntityId: e.entityId,
          name: e.TransformNode.name,
          center: e.TransformNode.position,
          extends: e.Room.extends,
          viewDataList: e.Room.viewDataList,
        };
      }) ?? []
    );
  }
  //------------------------------------------------------------------------------------
  /**
   * RoomObject Component   Entity RoomObjectInfo .
   * @param entities
   * @returns
   */
  private RoomObjectInfoByEntities(
    entities: ReturnType<typeof this.getRoomObjects>
  ): RoomObjectInfo[] {
    return (
      entities.flatMap((e) => {
        return {
          roomEntityID: e.RoomObject.roomEntityId ?? e.entityId,
          objectEntityId: e.entityId,
          name: e.TransformNode.name,
          position: e.TransformNode.position,
          forward: e.TransformNode.forward,
          ThingState: e.ThingState,
        };
      }) ?? []
    );
  }
  //------------------------------------------------------------------------------------
}
