import {
  Camera,
  Color4,
  ColorCurves,
  ColorGradingTexture,
  DefaultRenderingPipeline,
  EngineStore,
  MotionBlurPostProcess,
  Scene,
  Texture,
} from '@babylonjs/core';

import { type Reflector } from '@/components';
import {
  additionalLoadAndApplyMaterial,
  defaultPostprocessSettings,
  type PostprocessSettings,
  Resolution,
} from '@/data';
import { RenderManager } from '@/manager';
import { ReflectorSystem } from '@/system';
import { MaterialChangeSystem } from '@/system/MaterialChangeSystem';

const updateMemberValue = <T, M extends keyof T>(
  data: T,
  member: M,
  value: T[M] | undefined
): void => {
  if (value !== undefined && data[member] !== undefined) {
    data[member] = value;
  }
};
export class GraphicManager {
  //-------------------------------------------------
  // * Control Postprocess
  private postProcess?: DefaultRenderingPipeline;
  private motionBlur?: MotionBlurPostProcess;

  public get currentPostprocessSettings() {
    return defaultPostprocessSettings;
  }

  public dispose() {
    this.postProcess?.dispose();
    this.motionBlur?.dispose();
  }
  public setEnablePostprocess(enablePostprocess: boolean, settings?: PostprocessSettings) {
    const scene = EngineStore.LastCreatedScene;
    if (!scene) return;

    if (enablePostprocess) {
      if (!this.postProcess) {
        this.postProcess = new DefaultRenderingPipeline('default', true, scene, scene.cameras);
        this.postProcess.automaticBuild = true;
        scene.enablePrePassRenderer()!.disableGammaTransform = true;
        scene.postProcessesEnabled = true;
      }
      this.changePostprocessSettings(scene, !settings ? defaultPostprocessSettings : settings);
    } else {
      if (!!this.postProcess) {
        this.postProcess.imageProcessing.contrast = 1;
        this.postProcess.imageProcessing.exposure = 1;

        this.postProcess.imageProcessing.colorGradingEnabled = false;
        this.postProcess.imageProcessing.toneMappingEnabled = false;
        this.postProcess.imageProcessing.colorCurvesEnabled = false;
        this.postProcess.imageProcessing.vignetteEnabled = false;

        this.postProcess.bloomEnabled = false;
        this.postProcess.depthOfFieldEnabled = false;
        this.postProcess.sharpenEnabled = false;
        this.postProcess.chromaticAberrationEnabled = false;
      }

      if (this.motionBlur) {
        this.motionBlur.dispose();
        this.motionBlur = undefined;
      }
    }

    RenderManager.RenderUpdate();
  }
  private changePostprocessSettings(scene: Scene, settings: PostprocessSettings) {
    if (!!this.postProcess) {
      const pp = this.postProcess;

      // * Enable MSAA
      const samples = settings.useMSAA != undefined && settings.useMSAA ? 4 : 1;
      updateMemberValue(pp, 'samples', samples);

      // * Enable FXAA
      updateMemberValue(pp, 'fxaaEnabled', settings.useFXAA);

      // * ImageProcessing
      this.postProcess.imageProcessingEnabled = true;

      const ppi = this.postProcess.imageProcessing;
      // * Enable Tone Mapping
      updateMemberValue(ppi, 'toneMappingEnabled', settings.useToneMapping);
      updateMemberValue(ppi, 'toneMappingType', settings.toneMappingType);

      // * Control Contrast & Exporsure
      updateMemberValue(ppi, 'contrast', settings.contrast);
      updateMemberValue(ppi, 'exposure', settings.expoure);

      // * Control ColorCurves
      updateMemberValue(ppi, 'colorCurvesEnabled', settings.useColorCurve);

      if (settings.colorCurveSettings !== undefined) {
        var curve = new ColorCurves();
        curve.globalDensity = settings.colorCurveSettings.globalDensity;
        curve.globalExposure = settings.colorCurveSettings.globalExposure;
        curve.globalHue = settings.colorCurveSettings.globalHue;
        curve.globalSaturation = settings.colorCurveSettings.globalSaturation;

        curve.highlightsDensity = settings.colorCurveSettings.highlightsDensity;
        curve.highlightsExposure = settings.colorCurveSettings.highlightsExposure;
        curve.highlightsHue = settings.colorCurveSettings.highlightsHue;
        curve.highlightsSaturation = settings.colorCurveSettings.highlightsSaturation;

        curve.midtonesDensity = settings.colorCurveSettings.midtonesDensity;
        curve.midtonesExposure = settings.colorCurveSettings.midtonesExposure;
        curve.midtonesHue = settings.colorCurveSettings.midtonesHue;
        curve.midtonesSaturation = settings.colorCurveSettings.midtonesSaturation;

        curve.shadowsDensity = settings.colorCurveSettings.shadowsDensity;
        curve.shadowsExposure = settings.colorCurveSettings.shadowsExposure;
        curve.shadowsHue = settings.colorCurveSettings.shadowsHue;
        curve.shadowsSaturation = settings.colorCurveSettings.shadowsSaturation;

        ppi.colorCurves = curve;
      }

      // * Enable Vignette
      updateMemberValue(ppi, 'vignetteEnabled', settings.useVignette);
      updateMemberValue(ppi, 'vignetteWeight', settings.vignetteWeight);

      // * Enable Bloom
      updateMemberValue(pp, 'bloomEnabled', settings.useBloom);
      updateMemberValue(pp, 'bloomKernel', settings.bloomKernel);
      updateMemberValue(pp, 'bloomScale', settings.bloomScale);
      updateMemberValue(pp, 'bloomThreshold', settings.bloomThreshold);
      updateMemberValue(pp, 'bloomWeight', settings.bloomWeight);

      // * Enable DOF
      if (pp.depthOfField.isSupported) {
        updateMemberValue(pp, 'depthOfFieldEnabled', settings.useDepthOfField);
        updateMemberValue(pp, 'depthOfFieldBlurLevel', settings.blurLevel);

        updateMemberValue(pp.depthOfField, 'fStop', settings.fStop);
        updateMemberValue(pp.depthOfField, 'focalLength', settings.focalLength);
        updateMemberValue(pp.depthOfField, 'focusDistance', settings.focusDistance);
        updateMemberValue(pp.depthOfField, 'lensSize', settings.lensSize);
      }

      // * Enable ChromaticAberration
      updateMemberValue(pp, 'chromaticAberrationEnabled', settings.useChromaticAberration);
      updateMemberValue(pp.chromaticAberration, 'aberrationAmount', settings.aberrationAmount);

      // * Enable Sharpen
      updateMemberValue(pp, 'sharpenEnabled', settings.useSharpen);
      updateMemberValue(pp.sharpen, 'edgeAmount', settings.edgeAmount);
      updateMemberValue(pp.sharpen, 'colorAmount', settings.colorAmount);

      // * Enable Color Graiding
      updateMemberValue(ppi, 'colorGradingEnabled', settings.useColorGrading);
      if (settings.useColorGrading && settings.lutTexture !== undefined) {
        if (!!settings.lutIsBlob) {
          var colorGradingTexture = new Texture(
            settings.lutTexture,
            scene,
            true,
            false,
            undefined,
            () => {
              RenderManager.RenderUpdate();
            }
          );
          colorGradingTexture.wrapU = Texture.CLAMP_ADDRESSMODE;
          colorGradingTexture.wrapV = Texture.CLAMP_ADDRESSMODE;
          ppi.colorGradingTexture = colorGradingTexture;
        } else {
          if (!ppi.colorGradingTexture || ppi.colorGradingTexture.name !== settings.lutTexture) {
            const is3DL = settings.lutTexture.toLowerCase().endsWith('.3dl');
            const isPNG = settings.lutTexture.toLowerCase().endsWith('.png');
            // using .3dl (best) :
            if (is3DL) {
              ppi.colorGradingTexture = new ColorGradingTexture(settings.lutTexture, scene, () => {
                RenderManager.RenderUpdate();
              });
            }
            // using .png :
            else if (isPNG) {
              var colorGradingTexture = new Texture(
                settings.lutTexture,
                scene,
                true,
                false,
                undefined,
                () => {
                  RenderManager.RenderUpdate();
                }
              );
              colorGradingTexture.wrapU = Texture.CLAMP_ADDRESSMODE;
              colorGradingTexture.wrapV = Texture.CLAMP_ADDRESSMODE;
              ppi.colorGradingTexture = colorGradingTexture;
            } else {
              ppi.colorGradingTexture = null;
            }
          }
        }
      }

      if (settings.useMotionBlur) {
        if (!this.motionBlur) {
          this.motionBlur = new MotionBlurPostProcess(
            'motion blur',
            scene,
            1.0,
            scene.activeCamera
          );
          // * Motion Blur   Skybox or ClearColor  
          // Motion Blur         ClearColor 
          scene.clearColor = new Color4(25 / 255, 25 / 255, 25 / 255, 0);
          this.motionBlur.clearColor = new Color4(1, 1, 1, 0);

          RenderManager.RenderUpdate(false);
          this.motionBlur.onAfterRenderObservable.addOnce(() => {
            RenderManager.RenderStop();
          });
        }
        updateMemberValue(this.motionBlur, 'motionStrength', settings.motionStrength);
        updateMemberValue(this.motionBlur, 'samples', 1);
      } else {
        if (this.motionBlur) {
          scene.clearColor = new Color4(0, 0, 0, 0);
          this.motionBlur.dispose();
          this.motionBlur = undefined;
        }
      }
    }
  }

  //-------------------------------------------------
  // * Control Refelctor Entities
  public getReflectorsInFloorplan(floorplanId: string) {
    return ReflectorSystem.getReflectorsInFloorplan(floorplanId);
  }
  public changeReflectorSettings(reflector: Reflector, level: number, blurKernel: number) {
    ReflectorSystem.changeReflectorSettings(reflector, level, blurKernel);
  }

  //-------------------------------------------------
  public replaceCamera = (oldCamera: Camera, newCamera: Camera) => {
    this.postProcess?.removeCamera(oldCamera);
    this.postProcess?.addCamera(newCamera);
  };

  //-------------------------------------------------
  public getMaterialChangeableInFloorplan(floorplanId: string) {
    return MaterialChangeSystem.getMaterialChangeableEntitiesInFloorplan(floorplanId);
  }
  public async setMaterialChange(params: Parameters<typeof additionalLoadAndApplyMaterial>) {
    await additionalLoadAndApplyMaterial(...params);
  }

  //-------------------------------------------------
  public setResolution(resolution: Resolution) {
    RenderManager.SetResolution(resolution);
  }
}
