//-----------------------------------------------------------------
let scene: Scene | undefined;
let pivotPositions: Map<string, Vector3> = new Map();

const version = '12';
export let metadata: Metadata | undefined;
export let floorplanData: { [homeId: string]: FloorPlanData[] | undefined } = {};

//-----------------------------------------------------------------
export interface QualitySettings {
  simpleMode?: boolean;
  modelLOD: 'LOD0' | 'LOD1';
  textureMaxSize: '2048' | '1024' | '512' | '256';
  resolution?: Resolution;
  reflection?: boolean;
  useNormalTexture?: boolean;
}
let qualitySettings: QualitySettings;

// *    
export enum Resolution {
  MAX = -1,
  p1440 = 1440,
  p1080 = 1080,
  p900 = 900,
  p720 = 720,
  NONE = 0,
}

//-----------------------------------------------------------------
let loadedEnvironment = new Map<string, CubeTexture>();
let lightForStandardMaterial: DirectionalLight | undefined;
let loadedMaterialList = new Map<string, PBRMaterial | StandardMaterial>();
interface RoomLightRaycastData {
  floorplanId: string;
  origin: Vector3;
  items: {
    direction: Vector3;
    meshes: AbstractMesh[];
  }[];
}

const ExtensionName = 'ZED_EXTENSION';
//-----------------------------------------------------------------
/**
 *  HomeID Data Model Load Component 
 * @param scene: Scene
 * @param homeId Data Load  HomeID
 */
export const loadHome = async (
  _scene: Scene,
  homeId: string,
  graphicManager: GraphicManager,
  _qualitySettings: QualitySettings
) => {
  scene = _scene;
  qualitySettings = _qualitySettings;

  totalCount = 0;
  loadedCount = 0;

  const performance = PerformanceProfiler.getInstance(scene);
  //-----------------------------------------
  /**
   * simpleMode True , GLTF    Data    . (for dev)
   */
  if (qualitySettings.simpleMode) {
    const index = 'materials';
    SceneLoader.OnPluginActivatedObservable.add((loader) => {
      const gltfLoader = loader as unknown as GLTFFileLoader;

      gltfLoader.onParsed = (data) => {
        // @ts-ignore
        const materials: IMaterial[] = data.json[index];
        for (const m of materials) {
          if (m.pbrMetallicRoughness) {
            m.pbrMetallicRoughness.baseColorTexture = undefined;
            m.pbrMetallicRoughness.metallicRoughnessTexture = undefined;
          }

          m.extras = undefined;
          m.normalTexture = undefined;
          m.occlusionTexture = undefined;
          m.emissiveTexture = undefined;
          m.emissiveFactor = undefined;
        }

        // @ts-ignore
        const nodes: IImage[] = data.json['nodes'];
        totalCount += nodes.length;
      };
    });
  } else {
    SceneLoader.OnPluginActivatedObservable.add((loader) => {
      const gltfLoader = loader as unknown as GLTFFileLoader;
      gltfLoader.onParsed = (data) => {
        if (qualitySettings.useNormalTexture !== undefined && !qualitySettings.useNormalTexture) {
          // @ts-ignore
          const materials: IMaterial[] = data.json['materials'];
          for (const m of materials) {
            m.normalTexture = undefined;
          }
        }

        // @ts-ignore
        const images: IImage[] = data.json['images'];
        for (const m of images) {
          if (m.uri) {
            m.uri = `${qualitySettings.textureMaxSize}/${m.uri}`;
          }
        }
        // @ts-ignore
        const nodes: IImage[] = data.json['nodes'];
        totalCount += nodes.length;
      };
    });
  }
  //-----------------------------------------
  /**
   *  
   */

  // * metadata     (     )
  if (!metadata) {
    Profile.start('Data Load - Common');

    // * Cache 
    metadata = await getJsonDataFromCache<Metadata>('Zed', `Zed_Metadata.json`);

    // * Cache    version ,  json   Cache 
    if (!metadata || metadata.version !== version) {
      const [homeData, homeFloorplanData, postProcessSettings] = await Promise.all([
        loadJson<HomeData[]>('/assets/data/Home.json'),
        loadJson<HomeFloorPlanData[]>('/assets/data/HomeFloorPlan.json'),
        loadJson<PostprocessSettings>('/assets/data/PostProessSettings.json'),
      ]);

      metadata = {
        version: version,
        homeData: homeData,
        homeFloorPlanData: homeFloorplanData,
        postProcessSettings: postProcessSettings,
      };

      await cacheJsonData('Zed', `Zed_Metadata.json`, metadata);
    }

    Profile.end('Data Load - Common');
    performance.addCustom('loadHome', 'Common', Profile.get('Data Load - Common'));
  }

  // * Metadata Postprocess 
  setDefalutPostprocessSettings(metadata.postProcessSettings);
  graphicManager.setEnablePostprocess(true, metadata.postProcessSettings);

  //-----------------------------------------
  /**
   *   homeID  home()  
   */
  const home = metadata.homeData.find((data) => data.homeId === homeId);
  if (!home) {
    console.log(`The home does not exist. [homeID : ${homeId}]`);
    return;
  }
  // * Home  ambientColor  
  if (home.ambientColor) {
    scene.ambientColor = new Color3(home.ambientColor.r, home.ambientColor.g, home.ambientColor.b);
  }

  // * Standard Material  Light 
  lightForStandardMaterial = new DirectionalLight('', new Vector3(0, -1, 0), scene);
  lightForStandardMaterial.intensity = home.lightIntensity ?? 0.85;
  lightForStandardMaterial.name = 'Standard Only Light';
  lightForStandardMaterial.includedOnlyMeshes = [];

  // * Home  EnvironmentTexture    default Texture 
  scene.environmentTexture = loadEnvironment(scene, `${home.environmentTexture ?? 'default.env'}`);
  scene.environmentIntensity = 1;

  //-----------------------------------------
  /**
   *  home()  FloorPlan()  
   */
  const homeFloorplans = metadata.homeFloorPlanData.filter(
    (homeFloorPlan) => homeFloorPlan.homeId === homeId
  );
  if (homeFloorplans.length === 0) {
    console.log(`The floor plan does not exist for that home. [homeID : ${homeId}]`);
    return;
  }
  //-----------------------------------------
  /**
   *  FloorPlan()  FloorPlanBatch(  )  
   */

  // * homeId  floorplanData     (     )
  if (!floorplanData[homeId]) {
    Profile.start('Data Load - FloorPlan');

    // * User Cache 
    floorplanData[homeId] = await getJsonDataFromCache<FloorPlanData[]>(
      'User',
      `Zed_${homeId}.json`
    );

    // * User Cache , ZED Cache  (, metadata   floorplan data   )
    if (!floorplanData[homeId] && metadata.version === version) {
      floorplanData[homeId] = await getJsonDataFromCache<FloorPlanData[]>(
        'Zed',
        `Zed_${homeId}.json`
      );
    }

    // * Cache     ,  json   homeId Cache 
    if (!floorplanData[homeId]) {
      floorplanData[homeId] = await Promise.all(
        homeFloorplans.map((homeFloorPlan) =>
          loadJson<FloorPlanData>(`/assets/floorplan/${homeFloorPlan.floorplanId}.json`)
        )
      );
      if (!floorplanData[homeId]) return;

      await cacheJsonData('Zed', `Zed_${homeId}.json`, floorplanData[homeId]!);
    }
    Profile.end('Data Load - FloorPlan');
    performance.addCustom('loadHome', 'FloorPlan', Profile.get('Data Load - FloorPlan'));
  }

  homeFloorplans.forEach((floorplan) => {
    const homeFloorplan = homeFloorplans.find((f) => f.floorplanId === floorplan.floorplanId);
    const pivotPosition = homeFloorplan
      ? convertUnityCoordsToBabylon(homeFloorplan.position)
      : Vector3.Zero();
    pivotPositions.set(floorplan.floorplanId, pivotPosition);
  });

  //-----------------------------------------
  /**
   *  //    
   */
  Profile.start('Model Load');
  scene.blockfreeActiveMeshesAndRenderingGroups = true;

  for (const floorplan of floorplanData[homeId]!) {
    const floorplanId = floorplan.floorplanId;

    const parentTransform = new TransformNode(floorplanId, scene);
    parentTransform.rotation = new Vector3(0, Angle.FromDegrees(180).radians(), 0);
    parentTransform.scaling = new Vector3(1, 1, -1);

    const floorplanSrc = `/assets/model/${floorplan.floorplanId}_${qualitySettings.modelLOD}.glb`;
    const floorplanPosition = Vector3.Zero();
    const floorplanRotation = 0;
    const floorplanScale = Vector3.One();

    const additionalMaterials = [
      ...new Set(floorplan.materialOverrideData?.map((a) => a.materialName)),
    ];

    GLTFLoader.RegisterExtension(
      ExtensionName,
      (loader) => new ZEDExtension(loader, homeId, floorplanId)
    );
    await Promise.all([
      /**
       * Floorplan Model Load
       */
      loadModel(
        parentTransform,
        floorplanId,
        floorplanId,
        floorplanSrc,
        floorplanPosition,
        floorplanRotation,
        floorplanScale
      ),
      /**
       * Furniture Model Load
       */
      ...floorplan.furniturePlaceData.map((furnitureBatch) => {
        const furnitureName = furnitureBatch.name;
        const modelSrc = `/assets/model/${furnitureName}_${qualitySettings.modelLOD}.glb`;

        let position = convertUnityScaleToBabylon(furnitureBatch.position);
        let rotationY = convertUnityRotationYToBabylon(furnitureBatch.rotationY);
        rotationY = Angle.FromDegrees(rotationY % 360).radians();
        let scale = convertUnityScaleToBabylon(furnitureBatch.scale);

        return loadModel(
          parentTransform,
          floorplanId,
          furnitureName,
          modelSrc,
          position,
          rotationY,
          scale
        );
      }),
      /**
       * Material Load
       */
      ...additionalMaterials.map((materialName) => {
        const src = `/assets/model/${materialName}_${qualitySettings.modelLOD}.glb`;
        return loadMaterial(materialName, src);
      }),
    ]);
    GLTFLoader.UnregisterExtension(ExtensionName);

    applyOverrideMaterials(floorplanId, floorplan.materialOverrideData);
    /**
     *   
     */
    RoomSystem.updateRoomInFloorplan(floorplanId);
  }
  scene.blockfreeActiveMeshesAndRenderingGroups = false;
  Profile.end('Model Load');
  performance.addCustom('loadHome', 'Model Load', Profile.get('Model Load'));

  //-----------------------------------------
  /**
   * Shader 
   */
  Profile.start('Shader Compile');
  await Promise.all(scene.meshes.map((mesh) => mesh.material?.forceCompilationAsync(mesh)));
  Profile.end('Shader Compile');
  performance.addCustom('loadHome', 'Shader Compile', Profile.get('Shader Compile'));
  //-----------------------------------------
  // *   
  if (ObservableManager.instance.onAllModelLoaded.hasObservers()) {
    ObservableManager.instance.onAllModelLoaded.notifyObservers(undefined);
  }
  // *   1 frame  
  RenderManager.RenderUpdate();
};

//-----------------------------------------------------------------
let loadedCount = 0;
let totalCount = 0;
const IgnoreMeshName = '__root__';
const splitDot = '.';
const validateModelFormats = ['gltf', 'glb'];
/**
 * @param prop ZEDProp
 * @param modelName Model .
 * @param src Load Model 
 * @param pivotPosition pivot 
 * @param position Model  (final position = pivotPosition + position)
 * @param rotationY Model 
 * @param scale Model 
 * @param onLoaded Model   
 * *  
 * * model: Load Model   
 * * rootTransform:  modelName TransformNode
 */
const loadModel = async (
  parentTransform: TransformNode,
  floorplanId: string,
  modelName: string,
  src: string,
  position: Vector3,
  rotationY: number,
  scale: Vector3
) => {
  try {
    let model: ISceneLoaderAsyncResult;
    src = src.trim();

    // *   glb or gltf   
    const extension = src.split(splitDot).pop();
    if (extension && !validateModelFormats.includes(extension.toLowerCase())) {
      console.log(`.${extension} file format is not supported`);
      return;
    }

    // * Model Load
    model = await SceneLoader.ImportMeshAsync(
      '',
      src,
      undefined,
      scene,
      undefined,
      `${splitDot}${extension}`
    );

    // * babylonMeshes __root__  mesh  Extend   
    const meshes = model.meshes.filter((mesh) => mesh.name !== IgnoreMeshName);

    // *  modelName rootTransform 
    let root = model.transformNodes.find((node) => node.name.includes(modelName));
    let isMeshNode = !root;

    // * rootTransform  ,  modelName meshNode 
    if (isMeshNode) {
      root = meshes.find((node) => node.name.includes(modelName));
    }

    if (root) {
      const pivotPosition = pivotPositions.get(floorplanId) ?? Vector3.Zero();

      // * Data  // 
      root.setAbsolutePosition(pivotPosition.add(position));
      root.rotation = new Vector3(0, rotationY, 0);
      root.scaling = scale;

      const parent = root.parent;
      let transform: TransformNode | undefined = undefined;
      if (isMeshNode) {
        // * MeshNode   TransformNode  parent 
        transform = new TransformNode(root.name, scene);
        root.parent = transform;
      } else {
        transform = root;
      }

      transform.parent = parentTransform;
      if (parent) {
        parent.dispose(false, false);
      }
    } else {
      console.log(
        ` modelName TransformNode or Mesh   . root   . modelName: [${modelName}]`
      );
    }

    return model;
  } catch (error) {
    // console.log(error);
  }
};
const loadEnvironment = (scene: Scene, name: string, intensity?: number) => {
  let envTex = loadedEnvironment.get(name);
  if (!envTex) {
    const src = `/assets/environment/${name}`;
    envTex = CubeTexture.CreateFromPrefilteredData(src, scene);
    envTex.gammaSpace = false;
    // envTex.rotationY = Math.PI;
    loadedEnvironment.set(name, envTex);
  }

  if (intensity) {
    if (envTex.level != intensity) {
      envTex.level = intensity;
    }
  }
  return envTex;
};
//-----------------------------------------------------------------
export const addModel = async (
  homeId: string,
  floorplanId: string,
  modelName: string,
  position?: Vector3,
  rotationY?: number,
  scale?: Vector3
) => {
  if (!scene) return;

  const parentTransform = scene.getTransformNodeByName(floorplanId);
  if (!parentTransform) return;

  const modelSrc = `/assets/model/${modelName}_${qualitySettings.modelLOD}.glb`;

  GLTFLoader.RegisterExtension(
    ExtensionName,
    (loader) => new ZEDExtension(loader, homeId, floorplanId)
  );
  const model = await loadModel(
    parentTransform,
    floorplanId,
    modelName,
    modelSrc,
    position ?? parentTransform.position,
    rotationY ?? 0,
    scale ?? Vector3.One()
  );
  GLTFLoader.UnregisterExtension(ExtensionName);

  if (!model) return;

  await Promise.all(model.meshes.map((mesh) => mesh.material?.forceCompilationAsync(mesh)));

  RenderManager.RenderUpdate();

  return model;
};
//-----------------------------------------------------------------
const loadMaterial = async (modelName: string, src: string) => {
  try {
    if (loadedMaterialList.has(modelName)) return;

    src = src.trim();

    // *   glb or gltf   
    const extension = src.split(splitDot).pop();
    if (extension && !validateModelFormats.includes(extension.toLowerCase())) {
      console.log(`.${extension} file format is not supported`);
      return;
    }

    // * Model Load
    const model = await SceneLoader.ImportMeshAsync(
      '',
      src,
      undefined,
      scene,
      undefined,
      `${splitDot}${extension}`
    );

    model.meshes.forEach((m) => m.dispose(false, false));
  } catch (error) {
    // console.log(error);
  }
};
export const additionalLoadAndApplyMaterial = async (
  entity: ZEDSchema['MaterialChangeable'],
  overrideData: MaterialData
) => {
  const homeId = entity.Identifier.homeId;
  const floorplanId = entity.Identifier.floorplanId;
  const materialName = overrideData.materialName;

  if (!loadedMaterialList.has(materialName)) {
    GLTFLoader.RegisterExtension(
      ExtensionName,
      (loader) => new ZEDExtension(loader, homeId, floorplanId)
    );
    const src = `/assets/model/${materialName}_${qualitySettings.modelLOD}.glb`.trim();
    await loadMaterial(materialName, src);
    GLTFLoader.UnregisterExtension(ExtensionName);
  }
  await applyMaterial(entity, overrideData);
};
const applyOverrideMaterials = (floorplanId: string, overrideData?: MaterialData[]) => {
  if (!overrideData) return;

  const materialChangeableEntities =
    MaterialChangeSystem.getMaterialChangeableEntitiesInFloorplan(floorplanId);

  for (const entity of materialChangeableEntities) {
    const name = entity.TransformNode.name;
    const dataList = overrideData.filter((d) => d.name === name);

    for (let i = 0; i < dataList.length; i++) {
      const data = dataList[i];
      applyMaterial(entity, data);
    }
  }
};
const applyMaterial = async (
  entity: ZEDSchema['MaterialChangeable'],
  overrideData: MaterialData
) => {
  const type = entity.Structure.structureType;
  const reflection = type === StructureType.Floor;
  const meshes = entity.Structure.meshes;
  if (overrideData.index >= meshes.length) return;

  const mesh = meshes[overrideData.index];
  const materialName = overrideData.materialName;
  const material = mesh.material;
  if (
    !material ||
    material.name === materialName ||
    !(material instanceof StandardMaterial || material instanceof PBRMaterial)
  )
    return;

  const loadedMaterial = loadedMaterialList.get(materialName);
  if (
    !loadedMaterial ||
    !(loadedMaterial instanceof StandardMaterial || loadedMaterial instanceof PBRMaterial)
  )
    return;

  const newMaterial = loadedMaterial.clone(materialName);
  if (newMaterial instanceof PBRMaterial) {
    newMaterial.reflectionTexture = material.reflectionTexture;
  } else if (newMaterial instanceof StandardMaterial) {
    if (!reflection) {
      newMaterial.reflectionTexture = null;
    }
    if (lightForStandardMaterial) {
      lightForStandardMaterial.includedOnlyMeshes.push(mesh);
    }
  }
  newMaterial.lightmapTexture = material.lightmapTexture;
  newMaterial.useLightmapAsShadowmap = true;

  mesh.material = newMaterial;
  material.dispose(false, false);

  await mesh.material.forceCompilationAsync(mesh);
  RenderManager.RenderUpdate();

  const baseMaterialData = entity.MaterialChangeable.materialData;
  if (
    baseMaterialData.find(
      (d) =>
        d.name === overrideData.name &&
        d.index === overrideData.index &&
        d.materialName === overrideData.materialName
    )
  ) {
    const index = entity.MaterialChangeable.materialOverrideData.findIndex(
      (a) => a.name === overrideData.name && a.index === overrideData.index
    );
    if (index !== -1) {
      entity.MaterialChangeable.materialOverrideData.splice(index);
    }
  } else {
    const index = entity.MaterialChangeable.materialOverrideData.findIndex(
      (a) => a.name === overrideData.name && a.index === overrideData.index
    );
    if (index !== -1) {
      entity.MaterialChangeable.materialOverrideData[index] = overrideData;
    } else {
      entity.MaterialChangeable.materialOverrideData.push(overrideData);
    }
  }
};
//-----------------------------------------------------------------
export const disposeLoader = () => {
  SceneLoader.OnPluginActivatedObservable.clear();

  scene = undefined;
  // metadata = undefined;
  pivotPositions.clear();

  if (lightForStandardMaterial) lightForStandardMaterial.dispose(false, true);
  lightForStandardMaterial = undefined;

  loadedEnvironment.forEach((t) => {
    if (t) t.dispose();
  });
  loadedEnvironment.clear();

  loadedMaterialList.clear();
};
//-----------------------------------------------------------------
export class ZEDExtension implements IGLTFLoaderExtension {
  //-------------------------------------------------------
  public readonly name = ExtensionName;
  public enabled: boolean;
  private loader: GLTFLoader;
  private homeId: string;
  private floorplanId: string;

  constructor(loader: GLTFLoader, homeId: string, floorplanId: string) {
    this.loader = loader;
    this.enabled = loader.isExtensionUsed(ExtensionName);
    this.homeId = homeId;
    this.floorplanId = floorplanId;
  }
  //-------------------------------------------------------
  private lightmap = new Map<number, BaseTexture>();
  private entities: Entity[] = [];

  public dispose() {
    this.lightmap.clear();
    this.entities = [];

    (this.loader as any) = null;
  }
  //-------------------------------------------------------
  //@ts-ignore
  public async loadSceneAsync(context: string, scene: IScene): Nullable<Promise<void>> {
    if (scene.extras && scene.extras.floorplan) {
      const entity = createEntity({
        Identifier: {
          homeId: this.homeId,
          floorplanId: this.floorplanId,
        },
      });

      World.addComponent(entity, 'Floorplan', {
        extends: scene.extras.floorplan.extends,
        pivotPosition: pivotPositions.get(this.floorplanId) ?? Vector3.Zero(),
      });
      this.entities.push(entity);
    }

    await this.loader.loadSceneAsync(context, scene);
  }
  //-------------------------------------------------------
  public async loadNodeAsync(
    context: string,
    node: INode,
    assign: (babylonMesh: TransformNode) => void
    //@ts-ignore
  ): Nullable<Promise<TransformNode>> {
    await this.loader.loadNodeAsync(context, node, assign);

    // *  Model Load   
    if (loadedCount === 0) {
      if (ObservableManager.instance.onFirstModelLoaded.hasObservers()) {
        ObservableManager.instance.onFirstModelLoaded.notifyObservers(undefined);
      }
    }
    // * Model Load   Progress 
    if (ObservableManager.instance.onModelLoadProgress.hasObservers()) {
      ObservableManager.instance.onModelLoadProgress.notifyObservers(++loadedCount / totalCount);
    }

    const transform = node._babylonTransformNode;
    const meshes = node._primitiveBabylonMeshes;

    if (transform) {
      if (meshes) {
        this.optimizeMeshes(meshes);
      }

      const extras = node.extras;
      if (extras) {
        const entity = createEntity({
          Identifier: {
            homeId: this.homeId,
            floorplanId: this.floorplanId,
          },
          TransformNode: transform,
        });

        //-------------------------------------------------------
        const materialData = node.extras.material;
        const structure = node.extras.structure;
        if ((materialData || structure) && meshes) {
          if ((materialData && materialData.reflection) || (structure && structure.reflection)) {
            for (const mesh of meshes) {
              const material = mesh.material as PBRMaterial;

              // * Mirror    PBR => Standard 
              const newMaterial = new StandardMaterial(material.name, scene);
              newMaterial.diffuseColor = material.albedoColor;
              if (material.albedoTexture) newMaterial.diffuseTexture = material.albedoTexture;
              if (material.bumpTexture) newMaterial.bumpTexture = material.bumpTexture;
              if (material.ambientTexture) newMaterial.ambientTexture = material.ambientTexture;
              if (material.lightmapTexture) {
                newMaterial.lightmapTexture = material.lightmapTexture;
                newMaterial.useLightmapAsShadowmap = true;
              }
              newMaterial.specularColor = Color3.Black();
              if (qualitySettings.reflection)
                newMaterial.reflectionTexture = ReflectorSystem.getReflectionTexture(
                  this.floorplanId
                );
              newMaterial.ambientColor = Color3.White();

              mesh.material = newMaterial;
              if (!loadedMaterialList.has(material.name)) {
                loadedMaterialList.set(newMaterial.name, newMaterial);
              }
              material.dispose(false, false);

              if (structure) {
                if (lightForStandardMaterial) {
                  lightForStandardMaterial.includedOnlyMeshes.push(mesh);
                }
              }
            }
          } else if (materialData) {
            for (const mesh of meshes) {
              const material = mesh.material as PBRMaterial;
              if (!loadedMaterialList.has(material.name)) {
                loadedMaterialList.set(material.name, material);
              }
            }
          } else if (structure) {
            const structureType = structure.structureType as StructureType;
            if (structureType === StructureType.Floor || structureType === StructureType.Wall) {
              for (const mesh of meshes) {
                const material = mesh.material as PBRMaterial;
                if (!loadedMaterialList.has(material.name)) {
                  loadedMaterialList.set(material.name, material);
                }
              }
            }
          }
        }

        if (structure && meshes) {
          const structureType = structure.structureType as StructureType;

          if (structureType !== StructureType.Builtin) {
            World.addComponent(entity, 'Structure', {
              structureType: structureType,
              meshes: meshes,
            });
          }

          if (structureType === StructureType.Floor || structureType === StructureType.Wall) {
            const materialData: MaterialData[] = [];
            for (let i = 0; i < meshes.length; i++) {
              const material = meshes[i].material;
              if (material) {
                materialData.push({
                  name: transform.name,
                  materialName: material.name,
                  index: i,
                });
              }
            }
            World.addComponent(entity, 'MaterialChangeable', {
              materialData: materialData,
              materialOverrideData: [],
            });
          }

          if (structureType === StructureType.Floor) {
            World.addComponent(entity, 'Room', {
              extends: transform.getHierarchyBoundingVectors(),
              viewDataList: structure.viewDataList,
            });
            World.addComponent(entity, 'ThingState', {
              type: ThingType.Light,
              state: getDefaultThingState(ThingType.Light),
            });
            World.addComponent(entity, 'GUIDisplayable', true);
            World.addComponent(entity, 'CameraTarget', true);
            World.addComponent(entity, 'LightController', true);
            World.addComponent(entity, 'MagnetismTarget', true);
          } else if (structureType === StructureType.Wall) {
            World.addComponent(entity, 'MagnetismTarget', true);
          } else if (structureType === StructureType.OutterWall) {
            World.addComponent(entity, 'Transparentable', true);
          } else if (structureType === StructureType.Ceiling) {
            World.addComponent(entity, 'Ceiling', true);
          } else if (structureType === StructureType.Hallway) {
            World.addComponent(entity, 'Hallway', true);
          }

          if (
            structureType !== StructureType.OutterWall &&
            structureType !== StructureType.Hallway
          ) {
            World.addComponent(entity, 'RoomObject', {});
          }
        }

        //-------------------------------------------------------
        const furniture = node.extras.furniture;
        if (furniture && transform) {
          //-------------------------------------------------------
          if (furniture.moveable) {
            const magneticFace = furniture.magneticFace;
            World.addComponent(entity, 'TransformMovable', {
              magnetismFixtureFace: magneticFace,
            });
          }
          //-------------------------------------------------------
          if (furniture.video) {
            let target = transform
              .getChildTransformNodes()
              .find((t) => t.name == furniture.video.target);

            if (!target) target = transform;

            const childMeshes = target.getChildMeshes();
            if (childMeshes.length > furniture.video.index) {
              const targetMesh = childMeshes[furniture.video.index];
              World.addComponent(entity, 'VideoPlayer', {
                initialized: false,
                src: '/assets/video/zigbang.mp4',
                mesh: targetMesh,
                material: targetMesh.material as PBRMaterial,
                defaultPlay: false,
              });
            }
          }
          //-------------------------------------------------------
          if (furniture.thingType && furniture.thingType >= 0) {
            const thingType = furniture.thingType as ThingType;

            // * Thing  Furniture GUIDisplayable  (GUI       Component)
            World.addComponent(entity, 'ThingState', {
              type: thingType,
              state: getDefaultThingState(thingType),
            });
            World.addComponent(entity, 'GUIDisplayable', {
              displayName: transform.name,
            });

            // * Thing  Furniture CameraTarget  (Camera MoveToTarget   Component)
            World.addComponent(entity, 'CameraTarget', {});

            // * Door Window Thing  Openable  (    Component)
            if (thingType === ThingType.Door || thingType === ThingType.Window) {
              if (node.extras.furniture.animation) {
                const target = transform
                  .getChildTransformNodes()
                  .find((t) => t.name == node.extras.furniture.animation.target);
                if (target) {
                  const animation = node.extras.furniture.animation;

                  World.addComponent(entity, 'Openable', {
                    target: target,
                    openPosition: new Vector3(
                      -animation.openPosition.x,
                      animation.openPosition.y,
                      -animation.openPosition.z
                    ),
                    closePosition: new Vector3(
                      -animation.closePosition.x,
                      animation.closePosition.y,
                      -animation.closePosition.z
                    ),
                    openRotation: new Vector3(
                      Angle.FromDegrees(-animation.openRotation.x).radians(),
                      Angle.FromDegrees(-animation.openRotation.y).radians(),
                      Angle.FromDegrees(-animation.openRotation.z).radians()
                    ),
                    closeRotation: new Vector3(
                      Angle.FromDegrees(-animation.closeRotation.x).radians(),
                      Angle.FromDegrees(-animation.closeRotation.y).radians(),
                      Angle.FromDegrees(-animation.closeRotation.z).radians()
                    ),
                  });
                }
              }
            }
          }

          World.addComponent(entity, 'ReflectionTarget', true);
          World.addComponent(entity, 'RoomObject', {});

          // TODO: /      
        }
        //-------------------------------------------------------
        this.entities.push(entity);
      }
    }
  }
  private optimizeMeshes(meshes: AbstractMesh[]) {
    for (const mesh of meshes) {
      // // 
      mesh.freezeWorldMatrix();
      // BoundingInfo  
      mesh.doNotSyncBoundingInfo = true;
      // Pick   (raycast)
      mesh.isPickable = false;
      //       
      mesh.alwaysSelectAsActiveMesh = false;
    }
  }
  //-------------------------------------------------------
  public async loadMaterialPropertiesAsync(
    context: string,
    material: IMaterial,
    babylonMaterial: Material
  ) {
    await this.loader.loadMaterialPropertiesAsync(context, material, babylonMaterial);

    const constantContext = `${context}/commonConstant`;

    if (babylonMaterial instanceof PBRMaterial) {
      //-------------------------------------------
      babylonMaterial.albedoColor.r = Math.max(0, Math.min(babylonMaterial.albedoColor.r, 1));
      babylonMaterial.albedoColor.g = Math.max(0, Math.min(babylonMaterial.albedoColor.g, 1));
      babylonMaterial.albedoColor.b = Math.max(0, Math.min(babylonMaterial.albedoColor.b, 1));
      babylonMaterial.alphaCutOff = 0.5;
      babylonMaterial.maxSimultaneousLights = 1;

      if (material.extras) {
        //-------------------------------------------
        if (material.extras.lightmapTexture) {
          const lightmapTexture = material.extras.lightmapTexture;
          const i = lightmapTexture.index ?? -1;
          if (i >= 0) {
            babylonMaterial.lightmapTexture =
              this.lightmap.get(i) ??
              (await this.loader.loadTextureInfoAsync(
                `${constantContext}/lightmapTexture`,
                lightmapTexture,
                (bt) => {
                  bt.level = i.lightmapLevel ?? 1;
                  this.lightmap.set(i, bt);
                }
              ));
            babylonMaterial.useLightmapAsShadowmap = true;
          }
          babylonMaterial.ambientColor = Color3.White();
        }

        //-------------------------------------------
        if (material.extras.reflectionProbe) {
          const scene = EngineStore.LastCreatedScene;
          if (scene) {
            const textureName = `${material.extras.reflectionProbe.name}.env`;
            let envTex = loadedEnvironment.get(textureName);
            if (!envTex) {
              const src = `/assets/environment/${textureName}`;
              envTex = CubeTexture.CreateFromPrefilteredData(src, scene);
              envTex.gammaSpace = false;
              loadedEnvironment.set(textureName, envTex);

              const intensity = material.extras.reflectionProbe.intensity;
              if (intensity) {
                if (envTex.level != intensity) {
                  envTex.level = intensity;
                }
              }
            }
            babylonMaterial.reflectionTexture = envTex;
          }
        }
      }
    }

    if (material.extras) {
    }
  }
  //-------------------------------------------------------
  public onReady(): void {}
}
