'use client';

import { forwardRef, memo, useEffect, useImperativeHandle, useRef, useState } from 'react';

import styles from '@/styles/zed.module.css';

import ZED from '../ZED';

import { ZedContext } from './hooks/useZed';

const ZEDComponent = forwardRef<ZED | undefined>(function Zed(
  {
    children,
    className,
    style,
    ...rest
  }: Omit<React.CanvasHTMLAttributes<HTMLCanvasElement>, 'width' | 'height'>,
  ref
) {
  const [zed, setZed] = useState<ZED>();
  const [canvasRef, setCanvasRef] = useState<HTMLCanvasElement | null>(null);

  useImperativeHandle(ref, () => zed, [zed]);

  useEffect(() => {
    setZed(canvasRef ? new ZED(canvasRef) : undefined);
  }, [canvasRef]);

  useEffect(() => {
    if (!zed) return;

    return () => {
      zed.dispose();
    };
  }, [zed]);

  return (
    <div className={[className, styles.wrapper].join(' ')} style={style}>
      <canvas ref={setCanvasRef} className={[styles.full, styles.canvas].join(' ')} {...rest} />
      <ZedContext.Provider value={zed}>{children}</ZedContext.Provider>
    </div>
  );
});

/**
 * Zigbang 3D for IoT
 */
export const Zed = memo(ZEDComponent);
