import { clearArchetype, World } from '@/World';

export class CameraTargetSystem {
  private static instance: CameraTargetSystem | undefined;
  public static getCameraTargetEntities() {
    return this.instance?.getCameraTargetEntities() ?? [];
  }
  public static getCameraTargetEntitiesInFloorplan(floorplanId: string) {
    return this.instance?.getCameraTargetEntitiesInFloorplan(floorplanId) ?? [];
  }
  public static getCameraTargetEntityWithSameName(targetName: string) {
    const entities = this.instance?.targetArchetype.entities.find(
      (e) => e.TransformNode.name == targetName
    );
    return entities;
  }

  private targetArchetype = World.with('ActiveHome', 'Identifier', 'TransformNode', 'CameraTarget');

  constructor() {
    CameraTargetSystem.instance = this;
  }

  dispose() {
    CameraTargetSystem.instance = undefined;
    clearArchetype(this.targetArchetype);
  }

  public getCameraTargetEntities() {
    return this.targetArchetype.entities ?? [];
  }
  public getCameraTargetEntitiesInFloorplan(floorplanId: string) {
    return (
      this.targetArchetype.entities.filter((e) => e.Identifier.floorplanId == floorplanId) ?? []
    );
  }
}
